import UI from  '../../../core/helpers/UI';
import ManageService from '../services/ManageService';
import Enviroment from '../../../core/helpers/Enviroment';

class ManageController {
  context;
  manage_service;

  constructor(context) {
    this.context = context;
    this.manage_service = new ManageService();

    if(this.context.$route.params.id) {
      this.context.audio_id = JSON.parse(this.context.$route.params.id);
      this.context.is_edit = true;
      this._infoAudio();
    }
  }

  manageAudio() {
    //DNS64F
    if(this.context.is_edit) {
      this._editAudio();
    } else {
      this._createAudio();
    }
  }

  _createAudio() {
    if(this.context.$refs.form_audio.validate() && this.context.audio) {
      UI.loading('show');
      this.manage_service.createAudio(this.context.title, this.context.audio_link)
      .then((r) => {
        if(r.success == true) {
          this.context.$refs.form_audio.reset();
          this.deleteAudio();
          this.context.send_url = false;
          window.location = '#/audios/list';
          UI.mensajeOk('', r.msg);
          UI.loading('hide');
        }
      }).catch((e) => {
        UI.loading('hide');
        UI.mensajeBad('', e.msg);
      });
    } else {
      UI.mensajeBad('', 'Por favor, carga un audio, playlist o podcast.');
    }
  }

  _editAudio() {
    if(this.context.$refs.form_audio.validate() && this.context.audio) {
      UI.loading('show');
      this.manage_service.editAudio(this.context.audio_id, this.context.title, this.context.audio_link)
      .then((r) => {
        if(r.success == true) {
          this.deleteAudio();
          UI.mensajeOk('', r.msg);
          //this.context.$router.go(-1);
          window.location = '#/audios/list';
          UI.loading('hide');
        }
      }).catch((e) => {
        UI.loading('hide');
        UI.mensajeBad('', e.msg);
      });
    } else {
      UI.mensajeBad('', 'Por favor, carga un audio, playlist o podcast.');
    }
  }

  _infoAudio() {
    UI.loading('show');
    this.manage_service.infoAudio(this.context.audio_id)
    .then((r) => {
      if(r.success == true) {
        this.context.info_audio = r.data;

        this.context.audio_id = r.data.mod_audio_id;
        this.context.title = r.data.name;
        this.context.audio_link = r.data.audio;
        this.context.audio = Enviroment.getUrlZfiles() + this.context.audio_link;

        if(this.context.audio) {
          this.context.send_url = false;
        } else {
          this.context.send_url = true;
        }

        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  uploadedAudio() {
    //rutacuenta_encode_token

    //this.context.audio_link = this.context.upload_audio.name;
    
    this.context.audio_link = 'c260082ec9e742-tk-_sl_Faudios_sl_F2022_sl_F04_sl_F22_sl_F1650645682_om_horse.mp3';
    this.context.audio = Enviroment.getUrlZfiles() + 'c260082ec9e742-tk-_sl_Faudios_sl_F2022_sl_F04_sl_F22_sl_F1650645682_om_horse.mp3';
  }

  deleteAudio() {
    this.context.audio = '';
    this.context.upload_audio = null;
  }
}

export default ManageController;