import Rest from '../../../core/libs/Rest';

class ManageRepository {
  rest;

  constructor() {
    this.rest = new Rest();
  }

  createImage(title, image) {
    this.rest.setParams({name:title, image:image});
    this.rest.setEndPoint('api/mod-image/store-image');
    return this.rest.post();
  }

  editImage(mod_image_id, title, image) {
    this.rest.setParams({name:title, image:image});
    this.rest.setEndPoint('api/mod-image/update-image');
    return this.rest.put(mod_image_id);
  }

  uploadImage(image, destiny) {
    this.rest.setParams({image:image, destiny:destiny});
    this.rest.setEndPoint('api/core/archivos-cargar');
    return this.rest.post();
  }

  infoImage(image_id) {
    this.rest.setParams('/'+image_id);
    this.rest.setEndPoint('api/mod-image/show-image');
    return this.rest.get();
  }
}

export default ManageRepository;