<template>
  <div>
    <section class="main">
      <div class="container-fluid main-box-content contentModulo py-2">
        <div class="breadcrumb px-3">
          <ul>
            <li>Inicio</li>
            <li>Módulos</li>
            <li>{{is_edit ? 'Actualizar' : 'Crear'}} video</li>
          </ul>
        </div>

        <div class="title">
          <h1 class="my-0">{{is_edit ? 'Actualizar' : 'Crear'}} video</h1>
        </div>

        <v-card class="mt-3 mt-md-4">
          <v-card-text class="px-lg-4">
            <v-form class="w-100" ref="form_video" method="post" v-model="form_valid" lazy-validation @submit.prevent="manageVideo()">
              <v-text-field label="Titulo" v-model="title" :rules="[rules.requerido]" filled></v-text-field>

              <v-text-field label="URL Video" v-model="video_url" :rules="[rules.requerido, rules.url]" type="url" filled></v-text-field>

              <v-btn class="d-table mx-auto mt-2" rounded width="120" color="success" type="submit">{{is_edit ? 'Actualizar' : 'Guardar'}}</v-btn>
            </v-form>
          </v-card-text>
        </v-card>
      </div>
    </section>
  </div>
</template>

<script>
import ManageController from '../controllers/ManageController';

export default {
  data() {
    return {
      title: '',
      video_url: '',
      form_valid: true,

      rules: {
        requerido: value => !!value || 'Campo obligatorio',
        url: value => this.validatedUrl(value) || 'URL no válida',
        email: value => !value || /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(value) || 'Email incorrecto',
      },

      is_edit: false,

      manage_controller: null
    };
  },

  mounted() {
    this.manage_controller = new ManageController(this);
  },

  methods: {
    manageVideo() {
      this.manage_controller.manageVideo();
    },

    validatedUrl(str) {
      let url;

      try {
        url = new URL(str);
      } catch (_) {
        return false;
      }

      return url.protocol === "http:" || url.protocol === "https:";
    },
  }
}
</script>