import LoginService from "../services/LoginService";
import ErrorManager from "../../../libs/ServiceResponse/ErrorManager";
import Core from "../../../helpers/Core";
import Enviroment from "../../../helpers/Enviroment";
import UI from  '../../../helpers/UI';
import SessionUser from "../../../libs/SessionUser";
import { EventSystem } from "../../../libs/Events";

class LoginController {
  context;
  create_service;
  login_service = null;

  constructor(context) {
    this.context = context;
    let session_user = new SessionUser();
    session_user.destroyAll();
    this.login_service = new LoginService();   
    if(Enviroment.getAppEnv() != 'local' && Enviroment.isProyectOM()){
      location.href = Enviroment.getBaseUrlApic();
    }
  }

  loginUser(){    
    if(Enviroment.getModeDevelopment()=='true'){
      this.context.$router.push(Enviroment.getUrlDashboard());
    }else{
      this._normalLogin();
    }
  }
    _normalLogin(){
      this.login_service.loginUser(this.context.email, this.context.password)
      .then((userlogin) => {
        Core.setToken(userlogin.data.token);
        Core.setCuentaString(userlogin.data.cuentastr);
        let session_user = new SessionUser();
        session_user.setUserId(userlogin.data.usuario_id);
        this.context.$router.push(Enviroment.getUrlDashboard());
        UI.mensajeOk('INGRESO AL SISTEMA',"Bienvenido al sistema.");
        EventSystem.emit('loginUserSusses',{data:userlogin.data});
      })
      .catch((e) => new ErrorManager(e, () => {
      }));
    }
}

export default LoginController;