import UI from  '../../../core/helpers/UI';
import File from "../../../core/helpers/File";
import ListTeachersService from '../services/ListTeachersService';

class ListTeachersController {
  context;
  list_teachers_service;

  constructor(context) {
    this.context = context;
    this.list_teachers_service = new ListTeachersService();
    
    this.listCourses();
  }

  listCourses() {
    UI.loading('show');
    this.list_teachers_service.listCourses()
    .then((r) => {
      if(r.success == true) {
        this.context.list_courses = [];
        this.context.list_courses = r.data;
        
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }
}

export default ListTeachersController;