import UI from  '../../../core/helpers/UI';
import File from "../../../core/helpers/File";
import MainService from '../services/MainService';

class MainController {
  context;
  main_service;

  constructor(context) {
    this.context = context;
    this.main_service = new MainService();
  }

  createSetting() {
    if(this.context.$refs.form_settings.validate()) {
      UI.loading('show');
      this.main_service.createSetting(this.context.setting, this.context.description)
      .then((r) => {
        if(r.success == true) {
          this.context.$refs.form_settings.reset();
          UI.mensajeOk('', r.msg);
          UI.loading('hide');
        }
      }).catch((e) => {
        UI.loading('hide');
        UI.mensajeBad('', e.msg);
      });
    }
  }
}

export default MainController;