import Rest from '../../../core/libs/Rest';

class ListRepository {
  rest;

  constructor() {
    this.rest = new Rest();
  }

  listVideos() {
    this.rest.setParams('');
    this.rest.setEndPoint('api/mod-video/list-all');
    return this.rest.get();
  }

  deleteVideo(video_id) {
    this.rest.setParams('');
    this.rest.setEndPoint('api/mod-video/delete-video');
    return this.rest.delete(video_id);
  }
}

export default ListRepository;