import ManageRepository from "../repositories/ManageRepository";

class ManageService {
  manage_repository;

  constructor() {
    this.manage_repository = new ManageRepository();
  }

  async listQuestions() {
    let r = await this.manage_repository.listQuestions();
    return r;
  }

  async infoQuiz(quiz_id) {
    let r = await this.manage_repository.infoQuiz(quiz_id);
    return r;
  }

  async createQuiz(title, description, answer_quiz, start_date, final_date, questions) {
    let r = await this.manage_repository.createQuiz(title, description, answer_quiz, start_date, final_date, questions);
    return r;
  }

  async editQuiz(quiz_id, title, description, questions) {
    let r = await this.manage_repository.editQuiz(quiz_id, title, description, questions);
    return r;
  }
}

export default ManageService;