import Rest from '../../../core/libs/Rest';

class ManageRepository {
  rest;

  constructor() {
    this.rest = new Rest();
  }

  createTitle(title) {
    this.rest.setParams({name:title, content:'borrar'});
    this.rest.setEndPoint('api/mod-title/store-title');
    return this.rest.post();
  }

  editTitle(mod_title_id, title) {
    this.rest.setParams({name:title, content:'borrar'});
    this.rest.setEndPoint('api/mod-title/update-title');
    return this.rest.put(mod_title_id);
  }

  infoTitle(title_id) {
    this.rest.setParams('/'+title_id);
    this.rest.setEndPoint('api/mod-title/show-title');
    return this.rest.get(title_id);
  }
}

export default ManageRepository;