import Rest from '../../../core/libs/Rest';

class ListRepository {
  rest;

  constructor() {
    this.rest = new Rest();
  }

  listAudios() {
    this.rest.setParams('');
    this.rest.setEndPoint('api/mod-audio/list-all');
    return this.rest.get();
  }

  deleteAudio(audio_id) {
    this.rest.setParams('');
    this.rest.setEndPoint('api/mod-audio/delete-audio');
    return this.rest.delete(audio_id);
  }
}

export default ListRepository;