import ListRepository from "../repositories/ListRepository";

class ListService {
  list_repository;

  constructor() {
    this.list_repository = new ListRepository();
  }

  async listAudios() {
    let r = await this.list_repository.listAudios();
    return r;
  }

  async deleteAudio(audio_id) {
    let r = await this.list_repository.deleteAudio(audio_id);
    return r;
  }
}

export default ListService;