import UI from  '../../../core/helpers/UI';
import Helpers from '../../../core/helpers/Helpers';
import ListService from '../services/ListService';

class ListController {
  context;
  list_service;

  constructor(context) {
    this.context = context;
    this.list_service = new ListService();

    this.context.header_titles = [
      { text: 'Título', value: 'name' },
      { text: 'Estado', value: 'status' },
      { text: '', value: 'actions', sortable: false },
    ];
    
    this._listTitles();
  }

  _listTitles() {
    this.context.list_titles = [];
    UI.loading('show');
    this.list_service.listTitles()
    .then((r) => {
      if(r.success == true) {
        this.context.list_titles = r.data;
        
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  deleteTitle(title_id) {
    UI.loading('show');
    this.list_service.deleteTitle(title_id)
    .then((r) => {
      if(r.success == true) {
        this._listTitles();
        UI.mensajeOk('', r.msg);
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }
}

export default ListController;