import Helpers from "./Helpers";

export default class Enviroment{

    static getUrlEndpoint(){
      return process.env.VUE_APP_URL_ENDPOINT;
    }

    static getUrlZfiles(){
        if(!Helpers.isEmpty(window.ENVIROMENT.URL_ZFILES)){
            return window.ENVIROMENT.URL_ZFILES;
        }else if(!Helpers.isEmpty(process.env.VUE_APP_URL_ZFILES)){
            return process.env.VUE_APP_URL_ZFILES;
        }else{
            return Enviroment.getFullUrlApi() +'zfiles/';
        }
    }

    static getUrlDashboard(){
        if(!Helpers.isEmpty(window.ENVIROMENT.URL_DASHBOARD)){
            return window.ENVIROMENT.URL_DASHBOARD;
        }else{
            return '/start/welcome';
        }
    }

    static getUrlMockcup(){
        return process.env.VUE_APP_URL_MOCKUP;
    }

    static getModeDevelopment(){
        try{
            return process.env.VUE_APP_MODE_DEVELOPMENT;
        }catch(e){}
    }

    static getTypeToken(){
      try{
        return process.env.VUE_APP_TYPE_TOKEN;
      }catch(e){}
    }

    static getTokenAccount(){
      try{
        return process.env.VUE_APP_TOKEN_ACCOUNT;
      }catch(e){}
    }

    static getSegmentApi(){
      try{
        return process.env.VUE_APP_SEGMENT_API;
      }catch(e){}
    }

    static getFullUrlApi(){
      try{
        return process.env.VUE_APP_URL_ENDPOINT + process.env.VUE_APP_SEGMENT_API;
      }catch(e){}
    }

    static getTokenMinutesExpires(){
      try{
        return process.env.VUE_APP_TOKEN_MINUTES_EXPIRES;
      }catch(e){}
    }

    static getBackArquitecture(){
      try{
          return process.env.VUE_APP_ARC;
      }catch(e){}
    }

    static getTipoMenu(){
      try{
          return process.env.VUE_APP_TYPE_MENU;
      }catch(e){}
    }

    static getBaseUrlEndpoint(){
      try{
          return process.env.VUE_APP_URL_BASE_ENDPOINT;
      }catch(e){}
    }

    static getAppEnv(){
      try{
          return process.env.VUE_APP_ENV;
      }catch(e){}
    }

    static getExternalLoadFile(){
      try{
          return process.env.VUE_APP_EXTERNAL_LOAD_FILE;
      }catch(e){}
    }

    static getExternalBaseUrl(){
      try{
          return process.env.VUE_APP_EXTERNAL_BASE_URL;
      }catch(e){}
    }

    static getExternalUrlLoadFile(){
      try{
          return this.getExternalBaseUrl() +'api/core/archivos-cargar';
      }catch(e){}
    }

    static isProyectOM(){
      try{
          return process.env.VUE_APP_IS_PROJECT_OM;
      }catch(e){
          return false;
      }
    }

    static getBaseUrlApic(){
      try{
          return process.env.VUE_APP_URL_BASE_APIC;
      }catch(e){}
    }
}