import axios from "axios";
import Core from "../helpers/Core";
import Enviroment from "../helpers/Enviroment";
import Helpers from '../helpers/Helpers';
import User from "../helpers/User";
import SessionUser from "./SessionUser";

export default class Rest{

    enable_mockup = false;
    response_mockup = null;
    api = '';
    segment_api = '';
    end_point_geterate_token = '';

    constructor() {
      this.endpoint = '';
      this.params = null;
      this.base_url = Enviroment.getFullUrlApi();

      this.end_point_geterate_token = this.base_url+'api/core/token-generate-public';
      // this.segment_api = Enviroment.getSegmentApi();
      // this.base_url = Helpers.getApiUrl() + this.segment_api;
      // this.end_point_geterate_token = this.segment_api+'api/core/token-generate-public';
    }

    enableTokenSys(){
      Core.setTokenSys();
    }

    setBaseUrl(base_url){
      this.base_url = base_url;
    }

    setEndPoint(endpoint){
      this.endpoint = endpoint;
    }

    loadEndPointStandarLoadFile(){
        let end_point = (Enviroment.getBackArquitecture() == 'V2') 
                    ? 'api/crcore/cr-archivos-cargar' : 'api/core/archivos-cargar';
      this.endpoint = this.base_url + end_point;
    }

    loadEndPointStandarImageSignature(){
        let end_point = (Enviroment.getBackArquitecture() == 'V2') 
                    ? 'api/crcore/resources-save-image-signature' : '/api/core/resources-save-image-signature';
        this.endpoint = this.base_url + end_point;
    }

    loadEndPointStandarThumbnail(){
        let end_point = (Enviroment.getBackArquitecture() == 'V2') 
                    ? 'api/crcore/resources-save-image' : 'api/core/resources-save-image';
        this.endpoint = this.base_url + end_point;
    }

    setParams(params){
        this.params = params;
    }

    post(){
        let urlFinal = this.base_url + this.endpoint;
        let dataSend =  this.params;
        return this.peticion('POST',urlFinal,dataSend);
    }

    get(){
        let urlFinal = this.base_url + this.endpoint + this.params;
        return this.peticion('GET',urlFinal);
    }

    put(ente_id){
        let urlFinal = this.base_url + this.endpoint + '/' + ente_id;
        let dataSend =  this.params;
        return this.peticion('PUT',urlFinal,dataSend);
    }

    delete(ente_id){
        let urlFinal = this.base_url + this.endpoint + '/' + ente_id;
        return this.peticion('DELETE',urlFinal);
    }

    enableMockup(response){
        this.enable_mockup = true;
        this.response_mockup = response;
    }

    peticion(method,urlFinal,dataSend){
      if(this.enable_mockup){ return this.peticionMockup();}
      return new Promise((resolve, reject) => {
        if(Enviroment.getTypeToken() == 'PUBLIC'){
          this.getToken((token)=>{
            this._peticionSend(resolve,reject,token,method,urlFinal,dataSend);
          })
        }else{
          var token = Core.getToken();
          this._peticionSend(resolve,reject,token,method,urlFinal,dataSend);
        }
      })
    }

    _peticionSend(resolve,reject,token,method,urlFinal,dataSend){
        if(token =='' || token ==null){
            token = 'ERROR';
        }

        var params = {};
        var sData = {};
        var headers = {
            'Accept': 'application/json, text/javascript',
            'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
            'Authorization' : ''+ token +'',
            'Auth-visit-id' : ''+ Core.getVisitId() +''
        };

        if(method=='PUT'){
            sData = this.serialize(dataSend);
        }else if(method=='GET'){
            params = {};
        }else if(method=='DELETE'){
            params = {};
        }else{
            var infoForm = new FormData();
            for(var key in dataSend){
                infoForm.set(key,''+dataSend[key]);
            }
            sData = infoForm;
        }
        
        const config = {
            method: method,
            url: urlFinal,
            data:sData,
            params:params,
            headers: headers
        };

        axios(config)
        .then(response => {
            resolve(response.data);
        })
        .catch(e => {
            reject(e.response.data);
        })
    }

    getToken(resolve_calback){
      let current_time = new Date().getTime();
      let renew_token = Number(localStorage.getItem('token_renew'));

      if(renew_token == 0 || Number(current_time) > Number(renew_token) ){
          let session_user_renew = new SessionUser();
          session_user_renew.destroyToken();
      }

      if(User.getToken()){
          resolve_calback(User.getToken());
      }else{
          var params = {};
          var sData = {};
          var headers = {
              'Accept': 'application/json, text/javascript',
              'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
              'Authorization' : ''+ Enviroment.getTokenAccount() +'',
              'Auth-visit-id' : ''+ Core.getVisitId() +''
          };
  
          const config = {
              method: 'GET',
              url: this.end_point_geterate_token,
              data:{},
              params:params,
              headers: headers
          };
          
          axios(config)
          .then(response => {
              localStorage.setItem('token_renew',''+ (current_time + (Enviroment.getTokenMinutesExpires() * 60 * 1000)));
              localStorage.setItem('token', response.data.data);
              resolve_calback(response.data.data);
          })
          .catch(e => { console.log(e); })
      }
    }

    peticionMockup(){
        return new Promise((resolve, reject) => {
            // var token = localStorage.getItem("TOKEN");
            var token = Core.getToken();
            if(token==''){
                token = 'XXX-1-1-1-XXX';
            }
            var params = {}; // Parametros adicionales en GET
            var sData = {}; // Envio de parametros
            var headers = {
                'Accept': 'application/json, text/javascript',
                'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
                'Authorization' : ''+token+''
            };

            var method = 'POST';

            var infoForm = new FormData();
            var urlFinal = this.base_url + 'api/crcore/cr-staging-mockup';
            sData = infoForm;

            infoForm = new FormData();
            infoForm.set('jsonstring',JSON.stringify(this.response_mockup));
            sData = infoForm;

            const config = {
                method: method,
                url: urlFinal,
                data:sData,
                params:params,
                headers: headers
            };

            axios(config)
            .then(response => {
                resolve(response.data);
            })
            .catch(e => {
                reject(e.response.data);
            })
        })
    }

    file(fileInput){
        let method = 'POST';
        let urlFinal = this.endpoint;
        let dataSend = this.params;
        let clave='archivo';
        console.log('urlFinal', urlFinal);
        var external_load_file = Enviroment.getExternalLoadFile();
        if(external_load_file == 'true'){
            urlFinal = Enviroment.getExternalUrlLoadFile();
            console.log('urlFinal-modificada', urlFinal);
        }

        return new Promise((resolve, reject) => {
            if(!fileInput){
                reject('False');
            }

            var token = localStorage.getItem("token");
            var infoForm = new FormData();
            var sData = null;

            if(method=='PUT'){
                sData = this.serialize(dataSend);
            }else{

                for(var key in dataSend){
                    infoForm.set(key,''+dataSend[key]);
                }
                infoForm.set(clave,fileInput);
                sData = infoForm;
            }

            const config = {
                method: method,
                url: urlFinal,
                data:sData,
                headers: {
                    'Accept': 'application/json, text/javascript',
                    'Content-Type': 'multipart/form-data',
                    'Authorization' : ''+token+''
                }
            };

            axios(config)
            .then(response => {
                console.log(' E ');
                resolve(response.data);
            })
            .catch(e => {
                console.log(' F ');
                reject(e);
            })
        })
    }

    serialize(obj) {
        var str = [];
        for (var p in obj)
          if (obj.hasOwnProperty(p)) {
            str.push(encodeURIComponent(p) + "=" + encodeURIComponent(obj[p]));
          }
        return str.join("&");
    }

    peticionMockupV2(type, data, string_data_mockup){
        return new Promise((resolve, reject) => {
            var token = Core.getToken();
            if(token==''){
                token = 'XXX-1-1-1-XXX';
            }
            var params = {};
            var sData = {};
            var headers = {
                'Accept': 'application/json, text/javascript',
                'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
                'Authorization' : ''+token+''
            };
            
            var method = 'POST';

            var infoForm = new FormData();
            var urlFinal = Enviroment.getUrlMockcup() + this.endpoint;
            infoForm = new FormData();
            
            try {
              for(let info in this.params){
                infoForm.set(info,this.params[info]); // 
              }
            } catch (error) {}

            infoForm.set('METHOD', type);
            infoForm.set('jsonstring', JSON.stringify(string_data_mockup));

            sData = infoForm;

            const config = {
                method: method,
                url: urlFinal,
                data:sData,
                params:params,
                headers: headers
            };           

            axios(config)
            .then(response => {
                resolve(response.data);
            })
            .catch(e => {
                reject(e.response.data);
            })
        })        
    }
}
