import DashboardStudentRepository from "../repositories/DashboardStudentRepository";

class DashboardStudentService {
  dashboard_student_repository;

  constructor() {
    this.dashboard_student_repository = new DashboardStudentRepository();
  }

  async counters() {
    let r = await this.dashboard_student_repository.counters();
    return r;
  }

  async myCourses() {
    let r = await this.dashboard_student_repository.myCourses();
    return r;
  }

  async lastLessons() {
    let r = await this.dashboard_student_repository.lastLessons();
    return r;
  }
}

export default DashboardStudentService;