import ListRepository from "../repositories/ListRepository";

class ListService {
  list_repository;

  constructor() {
    this.list_repository = new ListRepository();
  }

  async listDocuments() {
    let r = await this.list_repository.listDocuments();
    return r;
  }

  async deleteDocument(document_id) {
    let r = await this.list_repository.deleteDocument(document_id);
    return r;
  }
}

export default ListService;