import UI from  '../../../core/helpers/UI';
import File from "../../../core/helpers/File";
import DetailTeachersService from '../services/DetailTeachersService';

class DetailTeachersController {
  context;
  detail_teachers_service;

  constructor(context) {
    this.context = context;
    this.detail_teachers_service = new DetailTeachersService();

    if(this.context.$route.params.id) {
      this.context.lesson_id = JSON.parse(this.context.$route.params.id);
    
      this.infoLesson();
    }
  }

  infoLesson() {
    UI.loading('show');
    this.detail_teachers_service.infoLesson(this.context.lesson_id)
    .then((r) => {
      if(r.success == true) {
        this.context.info_lesson = {};
        this.context.info_lesson = r.data;

        if(r.data.modules) {
          this.context.components = r.data.modules;
        }
        
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }
}

export default DetailTeachersController;