import Rest from '../../../core/libs/Rest';
import { LISTADO_STUDENTS_MOCKUP } from '../mockups/ListStudents';

class ListStudentsRepository {
  rest;

  constructor() {
    this.rest = new Rest();
  }

  listCoursesForStudent() {
    this.rest.setParams('');
    this.rest.setEndPoint('api/courses/list-all-students');
    return this.rest.peticionMockupV2('GET', null, LISTADO_STUDENTS_MOCKUP.LIST_COURSES_STUDENT);
    //return this.rest.get();
  }

  subscribeCourse(course_id) {
    this.rest.setParams({course_id:course_id});
    this.rest.setEndPoint('api/courses/subscribe-course');
    return this.rest.peticionMockupV2('POST', course_id, LISTADO_STUDENTS_MOCKUP.SUBSCRIBE_COURSE);
    //return this.rest.get();
  }

  cancelSubscribeCourse(course_id) {
    this.rest.setParams({course_id:course_id});
    this.rest.setEndPoint('api/courses/unsubscribed-course');
    return this.rest.peticionMockupV2('POST', course_id, LISTADO_STUDENTS_MOCKUP.UNSUBSCRIBE_COURSE);
    //return this.rest.get();
  }
}

export default ListStudentsRepository;