import Rest from '../../../core/libs/Rest';
import { LISTADO_MOCKUP } from '../mockups/ListCourses';

class ListCoursesRepository {
  rest;

  constructor() {
    this.rest = new Rest();
  }

  listCourses() {
    this.rest.setParams('');
    this.rest.setEndPoint('api/reports/list-all');
    return this.rest.peticionMockupV2('GET', null, LISTADO_MOCKUP.LIST_COURSES);
    //return this.rest.get();
  }
}

export default ListCoursesRepository;