<template>
  <div>
    <section class="main">
      <div class="container-fluid main-box-content contentModulo py-2">
        <div class="breadcrumb px-3">
          <ul>
            <li>Inicio</li>
            <li>Cursos</li>
            <li>Lecciones</li>
            <li>Detalle de la lección</li>
          </ul>
        </div>

        <div class="title d-flex align-items-center flex-wrap mb-4">
          <h1 class="my-0">{{info_lesson.title}}</h1>
        </div>

        <p>{{info_lesson.description}}</p>

        <div class="row">
          <div class="col-lg-8 col-xl-9 mt-3 mt-md-4 mb-4">
            <v-card elevation="2">
              <template v-for="(item, i) in components">
                <!-- Título -->
                <div class="box-component" v-if="item.type == 1" :key="i">
                  <v-card-title>
                    {{item.title}}
                  </v-card-title>
                  <v-card-text v-if="item.description">
                    {{item.description}}
                  </v-card-text>

                  <v-divider></v-divider>
                </div>
                
                <!-- Artículo -->
                <div class="box-component" v-if="item.type == 2" :key="i">
                  <v-card-title>
                    {{item.title}}
                  </v-card-title>
                  <v-card-text v-if="item.article">
                    {{item.article}}
                  </v-card-text>

                  <v-divider></v-divider>
                </div>
                
                <!-- Documentos -->
                <div class="box-component" v-if="item.type == 3" :key="i">
                  <v-card-title>
                    {{item.title}}
                  </v-card-title>
                  <v-card-text v-if="item.attachments && item.attachments.length > 0">
                    <ul class="m-0 p-0 list-unstyled">
                      <template v-for="(doc, ii) in item.attachments">
                        <li :key="ii">
                          <v-btn text color="info" class="d-flex align-items-center justify-content-start px-0" :href="doc.url" target="_blank">
                            {{doc.title}}
                            <v-icon class="ml-3">mdi-download</v-icon>
                          </v-btn>
                        </li>
                      </template>
                    </ul>
                  </v-card-text>

                  <v-divider></v-divider>
                </div>
                
                <!-- Cuestionario -->
                <div class="box-component" v-if="item.type == 4" :key="i">
                  <v-card-title>
                    {{item.title}}
                  </v-card-title>
                  <v-card-text>
                    <template v-for="(ques, iii) in item.questions">
                      <div class="block-question pt-4" :key="iii">
                        <div class="d-flex align-items-center flex-wrap mb-1">
                          <h4 class="fw-500 fz-16 text-dark-success my-0">Pregunta:</h4> 
                          <p class="fz-16 my-0 ml-2">{{ques.title}}</p>
                        </div>
                        <div class="d-flex align-items-center flex-wrap mb-1">
                          <h4 class="fw-500 fz-16 text-dark-success my-0">Descripción:</h4> 
                          <p class="fz-16 my-0 ml-2">{{ques.description}}</p>
                        </div>
                        
                        <!-- Cerrada única -->
                        <div class="answer-box flex-wrap mb-1" v-if="ques.type == 1">
                          <v-radio-group disabled :v-model="iii" class="mt-1">
                            <template v-for="(op, iiii) in ques.options">
                              <v-radio :label="op.text" :value="op.value" :key="iiii"></v-radio>
                            </template>
                          </v-radio-group>
                        </div>
                        
                        <!-- Cerrada múltiple -->
                        <div class="answer-box flex-wrap mb-1" v-if="ques.type == 2">
                          <template v-for="(op, iiii) in ques.options">
                            <v-checkbox disabled :v-model="iiii" :key="iiii" class="mt-0" :label="op.text" color="primary" :value="op.value" hide-details></v-checkbox>
                          </template>
                        </div>
                        
                        <!-- Abierta -->
                        <div class="answer-box flex-wrap mb-1" v-if="ques.type == 3">
                          <v-textarea disabled filled :v-model="iii" label="Tu respuesta"></v-textarea>
                        </div>
                      </div>
                    </template>

                    <v-btn class="mt-2 d-table mx-auto" rounded color="success" min-width="120" disabled>Guardar y enviar</v-btn>
                  </v-card-text>

                  <v-divider></v-divider>
                </div>
                
                <!-- Video -->
                <div class="box-component" v-if="item.type == 5" :key="i">
                  <v-card-title>
                    {{item.title}}
                  </v-card-title>
                  <v-card-text>
                    <iframe width="100%" height="450" :src="item.url_video" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
                  </v-card-text>

                  <v-divider></v-divider>
                </div>
                
                <!-- Audio -->
                <div class="box-component" v-if="item.type == 6" :key="i">
                  <v-card-title>
                    {{item.title}}
                  </v-card-title>
                  <v-card-text>
                    <audio controls>
                      <source :src="item.url_audio" type="audio/mpeg">
                    </audio>
                  </v-card-text>
                </div>
              </template>
            </v-card>

            <div class="d-flex flex-wrap align-items-center justify-content-between mt-4 mb-3">
              <v-btn color="info" rounded min-width="120" :href="'#/lessons/detail-admin/'+info_lesson.prev_lesson_id">
                <v-icon class="mr-3">mdi-chevron-left</v-icon>
                Anterior
              </v-btn>
              <v-btn color="info" rounded min-width="120" :href="'#/lessons/detail-admin/'+info_lesson.next_lesson_id">
                Siguiente
                <v-icon class="ml-3">mdi-chevron-right</v-icon>
              </v-btn>
            </div>
          </div>
          <div class="col-lg-4 col-xl-3 mt-3 mt-md-4 mb-4">
            <v-card>
              <v-card-title>
                Lecciones del curso
              </v-card-title>
              <v-card-text>
                <ul class="list-unstyled m-0 p-0">
                  <li>
                    <v-btn text class="px-0" color="info" href="#/lessons/detail-admin/2">Lección 2</v-btn>
                  </li>
                  <li>
                    <v-btn text class="px-0" color="info" href="#/lessons/detail-admin/3">Lección 3</v-btn>
                  </li>
                  <li>
                    <v-btn text class="px-0" color="info" href="#/lessons/detail-admin/4">Lección 4</v-btn>
                  </li>
                  <li>
                    <v-btn text class="px-0" color="info" href="#/lessons/detail-admin/5">Lección 5</v-btn>
                  </li>
                  <li>
                    <v-btn text class="px-0" color="info" href="#/lessons/detail-admin/6">Lección 6</v-btn>
                  </li>
                </ul>
              </v-card-text>
            </v-card>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import Helpers from '../../../core/helpers/Helpers';
import DetailTeachersController from '../controllers/DetailTeachersController';

export default {
  data() {
    return {
      Helpers,
      lesson_id: null,
      info_lesson: {},
      components: [],

      question_1: null,
      question_2: null,
      op_1: null,
      op_2: null,
      op_3: null,
      op_4: null,
      question_4: '',

      detail_teachers_controller: null
    };
  },

  mounted() {
    this.detail_teachers_controller = new DetailTeachersController(this);
  },

  methods: {
    
  }
}
</script>