import UI from  '../../../core/helpers/UI';
import File from "../../../core/helpers/File";
import ListService from '../services/ListService';

class ListController {
  context;
  list_service;

  constructor(context) {
    this.context = context;
    this.list_service = new ListService();

    this.context.header_teachers = [
      { text: 'ID', value: 'teacher_id' },
      { text: 'Nombres', value: 'names' },
      { text: 'Apellidos', value: 'last_names' },
      { text: 'Estado', value: 'state' },
      { text: 'Última conexión', value: 'last_conection' },
      { text: '', value: 'actions' },
    ];
    
    this.listCourses();
    this.listTeachers();
  }

  listTeachers() {
    UI.loading('show');
    this.list_service.listTeachers()
    .then((r) => {
      if(r.success == true) {
        this.context.is_search = false;
        this.context.course_id = null;
        this.context.list_teachers = [];
        this.context.list_teachers = r.data;

        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  listCourses() {
    UI.loading('show');
    this.list_service.listCourses()
    .then((r) => {
      if(r.success == true) {
        this.context.list_courses = [];
        this.context.list_courses = r.data;
        
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  filterListTeachers() {
    if(this.context.course_id) {
      UI.loading('show');
      this.list_service.filterListTeachers(this.context.course_id)
      .then((r) => {
        if(r.success == true) {
          this.context.is_search = true;
          this.context.list_teachers = [];
          this.context.list_teachers = r.data;

          UI.loading('hide');
        }
      }).catch((e) => {
        UI.loading('hide');
        UI.mensajeBad('', e.msg);
      });
    } else {
      UI.mensajeBad('', 'Selecciona un curso para realizar la búsqueda.');
    }
  }

  deleteTeacher(position, teacher_id) {
    UI.loading('show');
    this.list_service.deleteTeacher(teacher_id)
    .then((r) => {
      if(r.success == true) {
        this.context.list_teachers.splice(position, 1);
        UI.mensajeOk('', r.msg);
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  infoTeacher(teacher_id) {
    UI.loading('show');
    this.list_service.infoTeacher(teacher_id)
    .then((r) => {
      if(r.success == true) {
        this.context.info_teacher = r.data;
        console.log(this.context.info_teacher);
        this.context.$set(this.context.dialog_detail, teacher_id, true);
        
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  closeDetail(teacher_id) {
    this.context.$set(this.context.dialog_detail, teacher_id, false);
    this.context.info_teacher = {};
  }
}

export default ListController;