import Site from '../../core/layouts/Principal.vue';
import List from './views/List.vue';
import Manage from './views/Manage.vue';

window.routes.push({
  path: '/videos',
  name: 'videos',
  component: Site,
  children: [
    {
      path: 'list',
      name: 'list',
      component: List,
    },
    {
      path: 'manage/:id?',
      name: 'manage',
      component: Manage,
    },
  ],
});