import Rest from '../../../core/libs/Rest';
import { LIST_MOCKUP } from '../mockups/List';

class ListRepository {
  rest;

  constructor() {
    this.rest = new Rest();
  }

  listCategories() {
    this.rest.setParams('');
    this.rest.setEndPoint('api/category/list-all');
    return this.rest.peticionMockupV2('GET', null, LIST_MOCKUP.LIST_CATEGORIES);
    //return this.rest.get();
  }

  listInscriptions() {
    this.rest.setParams('');
    this.rest.setEndPoint('api/inscriptions/my-inscriptions');
    return this.rest.peticionMockupV2('GET', null, LIST_MOCKUP.LIST_INSCRIPTIONS);
    //return this.rest.get();
  }

  deleteInscription(course_id) {
    this.rest.setParams('');
    this.rest.setEndPoint('api/inscriptions/delete-inscription');
    return this.rest.peticionMockupV2('DELETE', course_id, LIST_MOCKUP.DELETE_INSCRIPTION);
    //return this.rest.get();
  }

  filterInscriptions(date_init, date_finish) {
    this.rest.setParams({date_init:date_init, date_finish:date_finish});
    this.rest.setEndPoint('api/inscriptions/filter-inscriptions');
    return this.rest.peticionMockupV2('POST', null, LIST_MOCKUP.FILTER_LIST_INSCRIPTIONS);
    //return this.rest.get();
  }
}

export default ListRepository;