import Rest from '../../../core/libs/Rest';
import { LISTADO_MOCKUP } from '../mockups/List';

class ListRepository {
  rest;

  constructor() {
    this.rest = new Rest();
  }

  listStudents() {
    this.rest.setParams('');
    this.rest.setEndPoint('api/students/list-all');
    return this.rest.peticionMockupV2('GET', null, LISTADO_MOCKUP.LIST_STUDENTS);
    //return this.rest.get();
  }

  listCourses() {
    this.rest.setParams('');
    this.rest.setEndPoint('api/courses/list-all');
    return this.rest.peticionMockupV2('GET', null, LISTADO_MOCKUP.LIST_COURSES);
    //return this.rest.get();
  }

  filterListStudents(course_id) {
    this.rest.setParams('');
    this.rest.setEndPoint('api/students/filter-by-course');
    return this.rest.peticionMockupV2('POST', course_id, LISTADO_MOCKUP.LIST_STUDENTS_FILTER);
    //return this.rest.get();
  }

  deleteStudent(student_id) {
    this.rest.setParams('');
    this.rest.setEndPoint('api/students/delete');
    return this.rest.peticionMockupV2('DELETE', student_id, LISTADO_MOCKUP.DELETE_STUDENT);
    //return this.rest.delete(course_id);
  }

  infoStudent(student_id) {
    this.rest.setParams('/'+student_id);
    this.rest.setEndPoint('api/students/detail');
    return this.rest.peticionMockupV2('GET', null, LISTADO_MOCKUP.INFO_STUDENT);
    //return this.rest.delete(course_id);
  }
}

export default ListRepository;