import Rest from '../../../core/libs/Rest';
import { LISTADO_TEACHERS_MOCKUP } from '../mockups/ListTeachers';

class ListTeachersRepository {
  rest;

  constructor() {
    this.rest = new Rest();
  }

  listCourses() {
    this.rest.setParams('');
    this.rest.setEndPoint('api/course/list-all');
    return this.rest.peticionMockupV2('GET', null, LISTADO_TEACHERS_MOCKUP.LIST_COURSES_TEACHERS);
    //return this.rest.get();
  }
}

export default ListTeachersRepository;