import UI from  '../../../core/helpers/UI';
import Helpers from '../../../core/helpers/Helpers';
import ManageService from '../services/ManageService';

class ManageController {
  context;
  manage_service;

  constructor(context) {
    this.context = context;
    this.manage_service = new ManageService();

    if(this.context.$route.params.id) {
      this.context.title_id = JSON.parse(this.context.$route.params.id);
      this.context.is_edit = true;
      this._infoTitle();
    }
  }

  manageTitle() {
    //DNS64F
    if(this.context.is_edit) {
      this._editTitle();
    } else {
      this._createTitle();
    }
  }

  _createTitle() {
    if(this.context.$refs.form_title.validate()) {
      UI.loading('show');
      this.manage_service.createTitle(this.context.title)
      .then((r) => {
        if(r.success == true) {
          this.context.$refs.form_title.reset();
          window.location = '#/titles/list';
          UI.mensajeOk('', r.msg);
          UI.loading('hide');
        }
      }).catch((e) => {
        UI.loading('hide');
        UI.mensajeBad('', e.msg);
      });
    }
  }

  _editTitle() {
    if(this.context.$refs.form_title.validate()) {
      UI.loading('show');
      this.manage_service.editTitle(this.context.title_id, this.context.title)
      .then((r) => {
        if(r.success == true) {
          UI.mensajeOk('', r.msg);
          this.context.$refs.form_title.reset();
          window.location = '#/titles/list';
          UI.loading('hide');
        }
      }).catch((e) => {
        UI.loading('hide');
        UI.mensajeBad('', e.msg);
      });
    }
  }

  _infoTitle() {
    UI.loading('show');
    this.manage_service.infoTitle(this.context.title_id)
    .then((r) => {
      if(r.success == true) {
        this.context.info_title = r.data;

        this.context.title_id = r.data.mod_title_id;
        this.context.title = r.data.name;

        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }
}

export default ManageController;