<template>
  <div>
    <section class="main">
      <div class="container-fluid main-box-content contentModulo py-2">
        <div class="breadcrumb px-3">
          <ul>
            <li>Inicio</li>
            <li>Módulos</li>
            <li>{{is_edit ? 'Actualizar' : 'Crear'}} documento</li>
          </ul>
        </div>

        <div class="title">
          <h1 class="my-0">{{is_edit ? 'Actualizar' : 'Crear'}} documento</h1>
        </div>

        <v-card class="mt-3 mt-md-4">
          <v-card-text class="px-lg-4">
            <v-form class="w-100" ref="form_document" method="post" v-model="form_valid" lazy-validation @submit.prevent="manageDocument()">
              <v-text-field label="Titulo" :rules="[rules.requerido]" v-model="title" filled></v-text-field>

              <v-file-input :prepend-icon="null" v-model="upload_document" filled label="Documento" hide-details @change="uploadFile" v-if="!document_link"></v-file-input>

              <div class="row justify-content-center pt-4" v-if="document_link">
                <div class="col-md-6">
                  <v-chip color="primary" class="d-block mx-auto chip-document">{{name_document}}</v-chip>
                </div>
                <div class="col-md-2">
                  <v-btn icon color="danger" @click="deleteDocument()">
                    <v-icon>mdi-close</v-icon>
                  </v-btn>
                </div>
              </div>

              <v-card-title class="fw-300 fz-24 px-0 mt-3" v-if="list_documents.length > 0">Documentos asociados</v-card-title>

              <v-simple-table v-if="list_documents.length > 0">
                <template v-slot:default>
                  <thead>
                    <tr>
                      <th class="text-left">
                        Nombre
                      </th>
                      <th class="text-left">
                        Tipo de archivo
                      </th>
                      <th class="text-left">
                        &nbsp;
                      </th>
                    </tr>
                  </thead>
                  <tbody>
                    <template v-for="(doc, i) in list_documents">
                      <tr :key="i">
                        <td>{{doc.name}}</td>
                        <td>{{doc.type}}</td>
                        <td>
                          <div class="d-flex align-items-center justify-content-center">
                            <v-tooltip bottom>
                              <template v-slot:activator="{ on, attrs }">
                                <v-btn color="danger" icon v-bind="attrs" v-on="on" @click="deleteUploaded(i)">
                                  <v-icon>mdi-trash-can-outline</v-icon>
                                </v-btn>
                              </template>
                              <span>Eliminar</span>
                            </v-tooltip>
                          </div>
                        </td>
                      </tr>
                    </template>
                  </tbody>
                </template>
              </v-simple-table>

              <v-btn class="d-table mx-auto mt-4" rounded width="120" color="success" type="submit">{{is_edit ? 'Actualizar' : 'Guardar'}}</v-btn>
            </v-form>
          </v-card-text>
        </v-card>
      </div>
    </section>
  </div>
</template>

<script>
import Helpers from '../../../core/helpers/Helpers';
import ManageController from '../controllers/ManageController';

export default {
  data() {
    return {
      Helpers,
      title: '',
      upload_document: null,

      form_valid: true,
      rules: {
        requerido: value => !!value || 'Campo obligatorio',
        url: value => this.validatedUrl(value) || 'URL no válida',
        email: value => !value || /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(value) || 'Email incorrecto',
      },
      document_link: '',
      document: '',
      name_document: '',
      list_documents: [],
      is_edit: false,
      info_document: {},

      manage_controller: null
    };
  },

  mounted() {
    this.manage_controller = new ManageController(this);
  },

  methods: {
    uploadFile() {
      this.manage_controller.uploadFile();
    },

    deleteUploaded(position) {
      this.manage_controller.deleteUploaded(position);
    },

    manageDocument() {
      this.manage_controller.manageDocument();
    },

    deleteDocument() {
      this.manage_controller.deleteDocument();
    }
  }
}
</script>