import UI from  '../../../core/helpers/UI';
import File from "../../../core/helpers/File";
import ListService from '../services/ListService';

class ListController {
  context;
  list_service;

  constructor(context) {
    this.context = context;
    this.list_service = new ListService();

    this.context.headers = [
      { text: 'Título', value: 'name' },
      { text: 'Descripción', value: 'description' },
      { text: 'Curso asignado', value: 'module.course.title' },
      { text: '', value: 'actions' },
    ];
    
    this.listLessons();
    this.listModules();
  }

  listModules() {
    UI.loading('show');
    this.list_service.listModules()
    .then((r) => {
      if(r.success == true) {
        this.context.list_modules = [];
        this.context.list_modules = r.data;
        
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  listLessons() {
    UI.loading('show');
    this.list_service.listLessons()
    .then((r) => {
      if(r.success == true) {
        this.context.module_id = null;
        this.context.is_search = false;
        this.context.list_lessons = [];
        this.context.list_lessons = r.data;
        
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  filterListLessons() {
    if(this.context.module_id) {
      UI.loading('show');
      this.list_service.filterListLessons(this.context.module_id)
      .then((r) => {
        if(r.success == true) {
          this.context.is_search = true;
          this.context.list_lessons = [];
          this.context.list_lessons = r.data;
          
          UI.loading('hide');
        }
      }).catch((e) => {
        UI.loading('hide');
        UI.mensajeBad('', e.msg);
      });
    } else {
      UI.mensajeBad('', 'Debes seleccionar un módulo para filtrar.');
    }
  }

  deleteLesson(position, lesson_id) {
    UI.loading('show');
    this.list_service.deleteLesson(lesson_id)
    .then((r) => {
      if(r.success == true) {
        this.context.list_lessons.splice(position, 1);
        UI.mensajeOk('', r.msg);
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }
}

export default ListController;