import Rest from '../../../core/libs/Rest';
import { MANAGE_MOCKUP } from '../mockups/Manage';

class ManageRepository {
  rest;

  constructor() {
    this.rest = new Rest();
  }

  listComponents() {
    this.rest.setParams('');
    this.rest.setEndPoint('api/courses/list-components');
    return this.rest.peticionMockupV2('GET', null, MANAGE_MOCKUP.LIST_COMPONENTS);
    //return this.rest.get();
  }

  infoLesson(lesson_id) {
    this.rest.setParams('/'+lesson_id);
    this.rest.setEndPoint('api/lessons/info');
    return this.rest.peticionMockupV2('GET', null, MANAGE_MOCKUP.INFO_COURSE);
    //return this.rest.get();
  }

  createLesson(title, description, components) {
    this.rest.setParams({title:title,description:description, components:components});
    this.rest.setEndPoint('api/lesson/store');
    return this.rest.peticionMockupV2('POST', null, MANAGE_MOCKUP.CREATE_LESSON);
    //return this.rest.get();
  }

  editLesson(lesson_id, title, description, components) {
    this.rest.setParams({title:title, description:description, components:components});
    this.rest.setEndPoint('api/lesson/store');
    return this.rest.peticionMockupV2('PUT', lesson_id, MANAGE_MOCKUP.EDIT_LESSON);
    //return this.rest.get();
  }
}

export default ManageRepository;