import Rest from '../../../core/libs/Rest';
import { DASHBOARD_ADMIN_MOCKUP } from '../mockups/DashboardAdmin';

class DashboardRepository {
  rest;

  constructor() {
    this.rest = new Rest();
  }

  counters() {
    this.rest.setParams('');
    this.rest.setEndPoint('api/dashboard/general-counter');
    return this.rest.peticionMockupV2('GET', null, DASHBOARD_ADMIN_MOCKUP.COUNTERS);
  }

  myCourses() {
    this.rest.setParams('');
    this.rest.setEndPoint('api/dashboard/my-courses');
    return this.rest.peticionMockupV2('GET', null, DASHBOARD_ADMIN_MOCKUP.MY_COURSES);
  }

  lastLessons() {
    this.rest.setParams('');
    this.rest.setEndPoint('api/dashboard/general-counter');
    return this.rest.peticionMockupV2('GET', null, DASHBOARD_ADMIN_MOCKUP.LAST_LESSONS);
  }
}

export default DashboardRepository;