<template>
  <div>
    <section class="main">
      <div class="container-fluid main-box-content contentModulo py-2">
        <div class="breadcrumb px-3">
          <ul>
            <li>Inicio</li>
            <li>Lecciones</li>
            <li>{{is_edit ? 'Crear' : 'Editar'}} lección</li>
          </ul>
        </div>

        <div class="title">
          <h1 class="my-0">{{is_edit ? 'Crear' : 'Editar'}}</h1>
        </div>

        <v-card class="my-3 my-md-4">
          <v-card-text class="px-lg-4">
            <v-form class="w-100" ref="form_manage_lesson" method="post" v-model="form_valid" lazy-validation @submit.prevent="manageLesson()">
              <div class="row align-items-end pt-3">
                <div class="col-lg-12 px-2">
                  <v-text-field label="Titulo" :rules="[rules.requerido]" v-model="title" filled></v-text-field>
                </div>
                <div class="col-lg-12 px-2">
                  <v-textarea label="Descripción" :rules="[rules.requerido]" v-model="description" filled></v-textarea>
                </div>
                <div class="col-sm-6 col-md-8">
                  <v-select item-text="title" v-model="component_id" item-value="component_id" hide-details :disabled="selected_components.length == list_components.length" :items="list_components" label="Componentes" filled clearable></v-select>
                </div>
                <div class="col-sm-3 col-md-2">
                  <v-btn block color="primary" min-width="120" min-height="56" rounded :disabled="!component_id" @click="addComponent(component_id)">Agregar componente</v-btn>
                </div>
                <div class="col-sm-3 col-md-2">
                  <v-menu offset-y>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn block color="primary" v-bind="attrs" v-on="on" min-width="120" min-height="56" rounded outlined>Crear componente</v-btn>
                    </template>
                    <v-list>
                      <v-list-item href="#/titles/manage">
                        <v-list-item-title>Título</v-list-item-title>
                      </v-list-item>
                      <v-list-item href="#/articles/manage">
                        <v-list-item-title>Artículo</v-list-item-title>
                      </v-list-item>
                      <v-list-item href="#/documents/manage">
                        <v-list-item-title>Documentos</v-list-item-title>
                      </v-list-item>
                      <v-list-item href="#/quizes/manage">
                        <v-list-item-title>Cuestionario</v-list-item-title>
                      </v-list-item>
                      <v-list-item href="#/videos/manage">
                        <v-list-item-title>Video</v-list-item-title>
                      </v-list-item>
                    </v-list>
                  </v-menu>
                </div>
                <div class="col-12 mt-3">
                  <template v-for="(item, index) in selected_components">
                    <div :class="index > 0 ? 'block-lesson py-2 border-top' : 'block-lesson py-2'" :key="index">
                      <div class="row align-items-center">
                        <div class="col-sm-8 col-md-9">
                          <h4 class="my-0 fz-16 fw-500 text-standar">{{item.title}}</h4>
                        </div>
                        <div class="col-sm-4 col-md-3 d-flex align-items-center justify-content-end flex-wrap">
                          <v-tooltip bottom v-if="selected_components.length > 0 && index+1 != selected_components.length">
                            <template v-slot:activator="{ on, attrs }">
                              <v-btn color="warning" v-bind="attrs" class="ml-3" v-on="on" icon @click="downComponent(index)">
                                <v-icon>mdi-arrow-down</v-icon>
                              </v-btn>
                            </template>
                            <span>Bajar {{item.title}}</span>
                          </v-tooltip>

                          <v-tooltip bottom v-if="selected_components.length > 0 && index > 0">
                            <template v-slot:activator="{ on, attrs }">
                              <v-btn color="warning" v-bind="attrs" class="ml-3" v-on="on" icon @click="upComponent(index)">
                                <v-icon>mdi-arrow-up</v-icon>
                              </v-btn>
                            </template>
                            <span>Subir {{item.title}}</span>
                          </v-tooltip>

                          <v-tooltip bottom>
                            <template v-slot:activator="{ on, attrs }">
                              <v-btn color="primary" v-bind="attrs" class="ml-3" v-on="on" icon>
                                <v-icon>mdi-eye-outline</v-icon>
                              </v-btn>
                            </template>
                            <span>Habilitar {{item.title}}</span>
                          </v-tooltip>

                          <v-tooltip bottom>
                            <template v-slot:activator="{ on, attrs }">
                              <v-btn color="danger" v-bind="attrs" v-on="on" @click="removeComponent(index)" class="ml-3" icon>
                                <v-icon>mdi-close</v-icon>
                              </v-btn>
                            </template>
                            <span>Eliminar {{item.title}}</span>
                          </v-tooltip>
                        </div>
                      </div>
                    </div>
                  </template>
                </div>
              </div>

              <v-btn color="success" class="d-table mt-4 mx-auto" rounded min-width="120"  :disabled="selected_components.length == 0" type="submit">{{is_edit ? 'Actualizar' : 'Guardar'}}</v-btn>
            </v-form>
          </v-card-text>
        </v-card>
      </div>
    </section>
  </div>
</template>

<script>
import Helpers from '../../../core/helpers/Helpers';
import ManageController from '../controllers/ManageController';

export default {
  data() {
    return {
      Helpers,
      title: '',
      description: '',
      selected_components: [],
      list_components: [],
      component_id: null,
      lesson_id: null,

      form_valid: true,
      rules: {
        requerido: value => !!value || 'Campo obligatorio',
        url: value => this.validatedUrl(value) || 'URL no válida',
        email: value => !value || /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(value) || 'Email incorrecto',
      },

      is_edit: false,

      manage_controller: null
    };
  },

  mounted() {
    this.manage_controller = new ManageController(this);
  },

  methods: {
    addComponent(component_id) {
      this.manage_controller.addComponent(component_id);
    },

    removeComponent(index) {
      this.manage_controller.removeComponent(index);
    },

    upComponent(index) {
      this.manage_controller.upComponent(index);
    },

    downComponent(index) {
      this.manage_controller.downComponent(index);
    },

    manageLesson() {
      this.manage_controller.manageLesson();
    }
  }
}
</script>