import Rest from '../../../core/libs/Rest';
import { LISTADO_MOCKUP } from '../mockups/List';

class ListRepository {
  rest;

  constructor() {
    this.rest = new Rest();
  }

  listQuizes() {
    this.rest.setParams('');
    this.rest.setEndPoint('api/quiz/list-all');
    return this.rest.peticionMockupV2('GET', null, LISTADO_MOCKUP.LIST_QUIZES);
    //return this.rest.get();
  }

  deleteQuiz(quiz_id) {
    this.rest.setParams('');
    this.rest.setEndPoint('api/quizs/delete');
    return this.rest.peticionMockupV2('DELETE', quiz_id, LISTADO_MOCKUP.DELETE_QUIZ);
    //return this.rest.get();
  }
}

export default ListRepository;