import UI from  '../../../core/helpers/UI';
import File from "../../../core/helpers/File";
import DetailStudentsService from '../services/DetailStudentsService';

class DetailStudentsController {
  context;
  detail_students_service;

  constructor(context) {
    this.context = context;
    this.detail_students_service = new DetailStudentsService();

    this.context.course_id = JSON.parse(this.context.$route.params.id);

    if(this.context.course_id) {
      this.infoCourse();
    }
  }

  infoCourse() {
    UI.loading('show');
    this.detail_students_service.infoCourse(this.context.course_id)
    .then((r) => {
      if(r.success == true) {
        this.context.info_course = r.data;
        this.context.list_lessons = [];
        this.context.list_lessons = r.data.lessons;
        
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }
}

export default DetailStudentsController;