import UI from  '../../../core/helpers/UI';
import File from "../../../core/helpers/File";
import ManageService from '../services/ManageService';

class ManageController {
  context;
  manage_service;
  current;
  after;
  before;

  constructor(context) {
    this.context = context;
    this.manage_service = new ManageService();

    if(this.context.$route.params.id) {
      this.context.lesson_id = JSON.parse(this.context.$route.params.id);

      if(this.context.lesson_id) {
        this.infoLesson();
        this.context.is_edit = true;
      } else {
        this.context.is_edit = false;
      }
    }

    this.listComponents();
  }

  listComponents() {
    UI.loading('show');
    this.manage_service.listComponents()
    .then((r) => {
      if(r.success == true) {
        this.context.list_components = [];
        this.context.list_components = r.data;
        
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  infoLesson() {
    UI.loading('show');
    this.manage_service.infoLesson(this.context.lesson_id)
    .then((r) => {
      if(r.success == true) {
        let info_lesson = r.data;

        this.context.lesson_id = info_lesson.lesson_id;
        this.context.title = info_lesson.title;
        this.context.description = info_lesson.description;
        
        if(info_lesson.components && info_lesson.components.length > 0) {
          info_lesson.components.forEach((v) => {
            this.context.selected_components.push(v);
          });
        }

        UI.loading('hide');
      }
    }).catch((e) => {
      console.log(e);
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  manageLesson() {
    if(this.context.is_edit) {
      this._editLesson();
    } else {
      this._createLesson();
    }
  }

  _createLesson() {
    if(this.context.$refs.form_manage_lesson.validate()) {
      UI.loading('show');

      let components_arr = [];
      this.context.selected_components.forEach((v) => {
        components_arr.push(v.component_id);
      });

      this.manage_service.createLesson(
        this.context.title,
        this.context.description,
        JSON.stringify(components_arr)
      ).then((r) => {
        if(r.success == true) {
          this.context.$refs.form_manage_lesson.reset();
          this.context.selected_components = [];
          components_arr = [];
          this.context.component_id = null;
          UI.loading('hide');
          UI.mensajeOk('', r.msg);
        }
      }).catch((e) => {
        UI.loading('hide');
        UI.mensajeBad('', e.msg);
      });
    }
  }

  _editLesson() {
    if(this.context.$refs.form_manage_lesson.validate()) {
      UI.loading('show');

      let components_arr = [];
      this.context.selected_components.forEach((v) => {
        components_arr.push(v.component_id);
      });

      this.manage_service.editLesson(
        this.context.lesson_id,
        this.context.title,
        this.context.description,
        JSON.stringify(components_arr)
      ).then((r) => {
        if(r.success == true) {
          UI.loading('hide');
          UI.mensajeOk('', r.msg);
        }
      }).catch((e) => {
        UI.loading('hide');
        UI.mensajeBad('', e.msg);
      });
    }
  }

  backHistory() {
    this.context.$router.go(-1);
  }

  addComponent(component_id) {
    this.context.list_components.forEach((v) => {
      if(v.component_id == component_id) {
        this.context.selected_components.push(v);
      }
    });
    const dataArr = new Set(this.context.selected_components);
    let result = [...dataArr];
    this.context.selected_components = result;
    this.context.component_id = null;
  }

  removeComponent(position) {
    this.context.selected_components.splice(position, 1);
  }

  upComponent(index) {
    let arr = this.context.selected_components;
    let empty = [];

    arr.forEach((v, i) => {
      if(index == i) {
        this.current = v;
      }
      if(index-1 == i) {
        this.before = v;
      }
    });

    arr[index] = this.before;
    arr[index-1] = this.current;

    this.context.selected_components = empty;
    this.context.selected_components = arr;
    this.before = null;
    this.current = null;
  }

  downComponent(index) {
    let arr = this.context.selected_components;
    let empty = [];

    arr.forEach((v, i) => {
      if(index == i) {
        this.current = v;
      }
      if(index+1 == i) {
        this.after = v;
      }
    });

    arr[index] = this.after;
    arr[index+1] = this.current;

    this.context.selected_components = empty;
    this.context.selected_components = arr;
    this.after = null;
    this.current = null;
  }
}

export default ManageController;