import UI from  '../../../core/helpers/UI';
import ListService from '../services/ListService';

class ListController {
  context;
  list_service;

  constructor(context) {
    this.context = context;
    this.list_service = new ListService();

    this.context.header_audios = [
      { text: 'Título', value: 'name' },
      { text: 'Estado', value: 'state' },
      { text: 'Audio', value: 'audio' },
      { text: '', value: 'actions', sortable: false },
    ];
    
    this._listAudios();
  }

  _listAudios() {
    this.context.list_audios = [];
    UI.loading('show');
    this.list_service.listAudios()
    .then((r) => {
      if(r.success == true) {
        this.context.list_audios = r.data;
        
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  deleteAudio(audio_id) {
    UI.loading('show');
    this.list_service.deleteAudio(audio_id)
    .then((r) => {
      if(r.success == true) {
        this._listAudios();
        UI.mensajeOk('', r.msg);
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }
}

export default ListController;