import ListRepository from "../repositories/ListRepository";

class ListService {
  list_repository;

  constructor() {
    this.list_repository = new ListRepository();
  }

  async listVideos() {
    let r = await this.list_repository.listVideos();
    return r;
  }

  async deleteVideo(video_id) {
    let r = await this.list_repository.deleteVideo(video_id);
    return r;
  }
}

export default ListService;