import Rest from '../../../core/libs/Rest';
import { MANAGE_MOCKUP } from '../mockups/Manage';

class ManageRepository {
  rest;

  constructor() {
    this.rest = new Rest();
  }

  infoCourse(course_id) {
    this.rest.setParams('/'+course_id);
    this.rest.setEndPoint('api/course/show-course');
    return this.rest.peticionMockupV2('GET', null, MANAGE_MOCKUP.INFO_COURSE);
    //return this.rest.get();
  }

  listCategories() {
    this.rest.setParams('');
    this.rest.setEndPoint('api/category/list-all');
    return this.rest.peticionMockupV2('GET', null, MANAGE_MOCKUP.LIST_CATEGORIES);
    //return this.rest.get();
  }

  listLessons() {
    this.rest.setParams('');
    this.rest.setEndPoint('api/lessons/list-all');
    return this.rest.peticionMockupV2('GET', null, MANAGE_MOCKUP.LIST_LESSONS);
    //return this.rest.get();
  }

  createCourse(title, category_id, image, description, lessons) {
    this.rest.setParams({title:title, course_cat_id:category_id, image:image, description:description, lessons:lessons});
    this.rest.setEndPoint('api/course/store-course');
    return this.rest.peticionMockupV2('POST', null, MANAGE_MOCKUP.CREATE_COURSE);
    //return this.rest.post();
  }

  editCourse(course_id, title, category_id, image, description, lessons) {
    this.rest.setParams({title:title, course_cat_id:category_id, image:image, description:description, lessons:lessons});
    this.rest.setEndPoint('api/course/update-course');
    return this.rest.peticionMockupV2('PUT', course_id, MANAGE_MOCKUP.EDIT_COURSE);
    //return this.rest.put(course_id);
  }
}

export default ManageRepository;