import ListRepository from "../repositories/ListRepository";

class ListService {
  list_repository;

  constructor() {
    this.list_repository = new ListRepository();
  }

  async listQuizes() {
    let r = await this.list_repository.listQuizes();
    return r;
  }

  async deleteQuiz(quiz_id) {
    let r = await this.list_repository.deleteQuiz(quiz_id);
    return r;
  }
}

export default ListService;