import ManageRepository from "../repositories/ManageRepository";

class ManageService {
  manage_repository;

  constructor() {
    this.manage_repository = new ManageRepository();
  }

  async createImage(title, image) {
    let r = await this.manage_repository.createImage(title, image);
    return r;
  }

  async editImage(image_id, title, image) {
    let r = await this.manage_repository.editImage(image_id, title, image);
    return r;
  }

  async uploadImage(image, destiny) {
    let r = await this.manage_repository.uploadImage(image, destiny);
    return r;
  }

  async infoImage(image_id) {
    let r = await this.manage_repository.infoImage(image_id);
    return r;
  }
}

export default ManageService;