<template>
  <div>
    <section class="main">
      <div class="container-fluid main-box-content contentModulo py-2">
        <div class="breadcrumb px-3">
          <ul>
            <li>Inicio</li>
            <li>Inscripciones</li>
            <li>Detalle de inscripción</li>
          </ul>
        </div>

        <div class="title">
          <h1 class="my-0">Detalle de inscripción</h1>
        </div>

        <v-card class="mt-3 mt-md-4">
          <v-card-text class="pb-0">
            <div class="row">
              <div class="col-md-6 col-lg-4 mb-4" v-if="detail_inscription.names">
                <h4 class="mt-0 mb-1 text-left text-standar fw-600 fz-18">Nombre estudiante:</h4>
                <p class="my-0 fw-400 fz-16 text-black">{{detail_inscription.names+' '+detail_inscription.last_name}}</p>
              </div>

              <div class="col-md-6 col-lg-4 mb-4" v-if="detail_inscription.course">
                <h4 class="mt-0 mb-1 text-left text-standar fw-600 fz-18">Curso inscrito:</h4>
                <p class="my-0 fw-400 fz-16 text-black">{{detail_inscription.course.name}}</p>
              </div>

              <div class="col-md-6 col-lg-4 mb-4" v-if="detail_inscription.dater">
                <h4 class="mt-0 mb-1 text-left text-standar fw-600 fz-18">Fecha de inscripción:</h4>
                <p class="my-0 fw-400 fz-16 text-black">{{detail_inscription.dater}}</p>
              </div>

              <div class="col-md-6 col-lg-4 mb-4" v-if="detail_inscription.state">
                <h4 class="mt-0 mb-1 text-left text-standar fw-600 fz-18">Estado actual:</h4>
                <p class="my-0 fw-400 fz-16 text-black" v-if="detail_inscription.state == 1">En progreso</p>
                <p class="my-0 fw-400 fz-16 text-black" v-if="detail_inscription.state == 2">Finalizado</p>
              </div>

              <div class="col-md-6 col-lg-4 mb-4" v-if="detail_inscription.progress">
                <h4 class="mt-0 mb-1 text-left text-standar fw-600 fz-18">Progreso:</h4>
                <p class="my-0 fw-400 fz-16 text-black">{{detail_inscription.progress}}%</p>
              </div>
              <div class="col-md-6 col-lg-4 mb-4" v-if="detail_inscription.teacher">
                <h4 class="mt-0 mb-1 text-left text-standar fw-600 fz-18">Profesor:</h4>
                <p class="my-0 fw-400 fz-16 text-black">{{detail_inscription.teacher}}</p>
              </div>
            </div>

            <v-btn class="mb-4 d-table mx-auto" @click="backHistory()" min-width="120" color="primary" outlined>Volver</v-btn>
          </v-card-text>
        </v-card>
      </div>
    </section>
  </div>
</template>

<script>
import Helpers from '../../../core/helpers/Helpers';
import DetailController from '../controllers/DetailController';

export default {
  data() {
    return {
      Helpers,
      detail_inscription_id: null,
      detail_inscription: {},

      detail_controller: null
    };
  },

  mounted() {
    this.detail_controller = new DetailController(this);
  },

  methods: {
    backHistory() {
      this.detail_controller.backHistory();
    }
  }
}
</script>