import UI from  '../../../core/helpers/UI';
import File from "../../../core/helpers/File";
import CategoriesService from '../services/CategoriesService';

class CategoriesController {
  context;
  categories_service;

  constructor(context) {
    this.context = context;
    this.categories_service = new CategoriesService();

    this.context.headers = [
      { text: 'ID', value: 'course_cat_id' },
      { text: 'Nombre', value: 'name' },
      { text: 'Fecha', value: 'date' },
      { text: '', value: 'actions' },
    ];
    
    this.listCategories();
  }

  listCategories() {
    UI.loading('show');
    this.categories_service.listCategories()
    .then((r) => {
      if(r.success == true) {
        this.context.list_categories = [];
        this.context.list_categories = r.data;
        
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  deleteCategory(position, course_cat_id) {
    UI.loading('show');
    this.categories_service.deleteCategory(course_cat_id)
    .then((r) => {
      if(r.success == true) {
        this.context.list_categories.splice(position, 1);
        UI.mensajeOk('', r.msg);
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  editCategory(item) {
    this.context.show_form = true;
    this.context.is_edit = true;
    this.context.category_id = item.course_cat_id;
    this.context.name_category = item.name;
  }

  cancelCategory() {
    this.context.show_form = false;
    this.context.is_edit = false;
    this.context.category_id = null;
    this.context.name_category = '';
    this.context.$refs.form_category.reset();
  }

  createCategory() {
    this.context.show_form = true;
    this.context.is_edit = false;
    this.context.category_id = null;
    this.context.name_category = '';
    this.context.$refs.form_category.reset();
  }

  manageCategory() {
    if(this.context.is_edit) {
      this._editCategory();
    } else {
      this._createCategory();
    }
  }

  _createCategory() {
    if(this.context.$refs.form_category.validate()) {
      UI.loading('show');
      this.categories_service.createCategory(this.context.name_category)
      .then((r) => {
        if(r.success == true) {
          UI.mensajeOk('', r.msg);
          this.cancelCategory();
          this.listCategories();
          UI.loading('hide');
        }
      }).catch((e) => {
        UI.loading('hide');
        UI.mensajeBad('', e.msg);
      });
    }
  }

  _editCategory() {
    if(this.context.$refs.form_category.validate()) {
      UI.loading('show');
      this.categories_service.editCategory(this.context.category_id, this.context.name_category)
      .then((r) => {
        if(r.success == true) {
          UI.mensajeOk('', r.msg);
          this.cancelCategory();
          this.listCategories();
          UI.loading('hide');
        }
      }).catch((e) => {
        console.log(e);
        UI.loading('hide');
        UI.mensajeBad('', e.msg);
      });
    }
  }
}

export default CategoriesController;