import Rest from '../../../core/libs/Rest';
import { LISTADO_MOCKUP } from '../mockups/List';

class ListRepository {
  rest;

  constructor() {
    this.rest = new Rest();
  }

  listTuitions() {
    this.rest.setParams('');
    this.rest.setEndPoint('api/tuition/list-all');
    return this.rest.peticionMockupV2('GET', null, LISTADO_MOCKUP.LIST_TUITIONS);
    //return this.rest.get();
  }

  listCategories() {
    this.rest.setParams('');
    this.rest.setEndPoint('api/categories/list-all');
    return this.rest.peticionMockupV2('GET', null, LISTADO_MOCKUP.LIST_CATEGORIES);
    //return this.rest.get();
  }

  deleteTuition(tuition_id) {
    this.rest.setParams('');
    this.rest.setEndPoint('api/tuitions/delete');
    return this.rest.peticionMockupV2('DELETE', tuition_id, LISTADO_MOCKUP.DELETE_TUITION);
    //return this.rest.get();
  }

  filterListTuitions(category_id, date_init, date_finish) {
    this.rest.setParams({category_id:category_id, date_init:date_init, date_finish:date_finish});
    this.rest.setEndPoint('api/tuition/list-filtered');
    return this.rest.peticionMockupV2('POST', null, LISTADO_MOCKUP.LIST_TUITIONS_BY_FILTER);
    //return this.rest.post();
  }

  infoTuition(tuition_id) {
    this.rest.setParams('/'+tuition_id);
    this.rest.setEndPoint('api/tuitions/detail');
    return this.rest.peticionMockupV2('GET', null, LISTADO_MOCKUP.INFO_TUITION);
    //return this.rest.get();
  }
}

export default ListRepository;