import ListRepository from "../repositories/ListRepository";

class ListService {
  list_repository;

  constructor() {
    this.list_repository = new ListRepository();
  }

  async listImages() {
    let r = await this.list_repository.listImages();
    return r;
  }

  async deleteImage(image_id) {
    let r = await this.list_repository.deleteImage(image_id);
    return r;
  }
}

export default ListService;