import ListRepository from "../repositories/ListRepository";

class ListService {
  list_repository;

  constructor() {
    this.list_repository = new ListRepository();
  }

  async listTuitions() {
    let r = await this.list_repository.listTuitions();
    return r;
  }

  async listCategories() {
    let r = await this.list_repository.listCategories();
    return r;
  }

  async deleteTuition(tuition_id) {
    let r = await this.list_repository.deleteTuition(tuition_id);
    return r;
  }

  async filterListTuitions(category_id, date_init, date_finish) {
    let r = await this.list_repository.filterListTuitions(category_id, date_init, date_finish);
    return r;
  }

  async infoTuition(tuition_id) {
    let r = await this.list_repository.infoTuition(tuition_id);
    return r;
  }
}

export default ListService;