import UI from  '../../../core/helpers/UI';
import File from "../../../core/helpers/File";
import DashboardTeacherService from '../services/DashboardTeacherService';

class DashboardTeacherController {
  context;
  dashboard_teacher_service;

  constructor(context) {
    this.context = context;
    this.dashboard_teacher_service = new DashboardTeacherService();
    
    this.context.headers = [
      { text: 'Titulo', value: 'name' },
      { text: 'Curso', value: 'course', sortable: false },
      { text: 'Progreso del curso', value: 'progress', sortable: false },
    ];

    this.counters();
    this.myCourses();
    this.lastLessons();
  }

  counters() {
    UI.loading('show');
    this.dashboard_teacher_service.counters()
    .then((r) => {
      if(r.success == true) {
        this.context.list_stadistics = {};
        this.context.list_stadistics = r.data;
        
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });  
  }

  myCourses() {
    UI.loading('show');
    this.dashboard_teacher_service.myCourses()
    .then((r) => {
      if(r.success == true) {
        this.context.list_my_courses = [];
        this.context.list_my_courses = r.data;
        
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  lastLessons() {
    UI.loading('show');
    this.dashboard_teacher_service.lastLessons()
    .then((r) => {
      if(r.success == true) {
        this.context.list_lessons = [];
        this.context.list_lessons = r.data;
        
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }
}

export default DashboardTeacherController;