import Rest from '../../../core/libs/Rest';
import { LISTADO_MOCKUP } from '../mockups/List';

class ListRepository {
  rest;

  constructor() {
    this.rest = new Rest();
  }

  listTeachers() {
    this.rest.setParams('');
    this.rest.setEndPoint('api/teachers/list-all');
    return this.rest.peticionMockupV2('GET', null, LISTADO_MOCKUP.LIST_TEACHERS);
    //return this.rest.get();
  }

  listCourses() {
    this.rest.setParams('');
    this.rest.setEndPoint('api/courses/list-all');
    return this.rest.peticionMockupV2('GET', null, LISTADO_MOCKUP.LIST_COURSES);
    //return this.rest.get();
  }

  filterListTeachers(course_id) {
    this.rest.setParams('');
    this.rest.setEndPoint('api/teachers/filter-by-course');
    return this.rest.peticionMockupV2('POST', course_id, LISTADO_MOCKUP.LIST_TEACHERS_FILTER);
    //return this.rest.get();
  }

  deleteTeacher(teacher_id) {
    this.rest.setParams('');
    this.rest.setEndPoint('api/teachers/delete');
    return this.rest.peticionMockupV2('DELETE', teacher_id, LISTADO_MOCKUP.DELETE_TEACHER);
    //return this.rest.delete(course_id);
  }

  infoTeacher(teacher_id) {
    this.rest.setParams('/'+teacher_id);
    this.rest.setEndPoint('api/teachers/detail');
    return this.rest.peticionMockupV2('GET', null, LISTADO_MOCKUP.INFO_TEACHER);
    //return this.rest.delete(course_id);
  }
}

export default ListRepository;