import { _ } from "core-js";

export default class Session{
 
  static set(key, value){
    localStorage.setItem(key, ''+ value);
  }

  static get(key){
    return localStorage.getItem(key);
  }

  static toggleValue(key){
    let current_key = Session.get(key);

    if(current_key == undefined){
      return false;
    }else if(current_key == 'TRUE'){
      Session.set(key, 'FALSE');
      return true;
    }else{
      Session.set(key, 'TRUE');
      return false;
    }
  }

  static paramDeclared(key){
    let current_key = Session.get(key);

    if(current_key == undefined){
      Session.set(key, 'TRUE');
      return false;
    }else if(current_key == 'TRUE'){
      return true;
    }
  }

  static destoyAll(){
    let menu_status = Session.get('menu_opened');
    localStorage.clear();
    Session.set('menu_opened', menu_status);
  }  
}