import LoginRepository from "../repositories/LoginRepository";
import ServiceResponse from "../../../libs/ServiceResponse/ServiceResponse";

class LoginService {
  login_repository = null;
  
  constructor() {
    this.login_repository = new LoginRepository();
  }
 
  async loginUser(email, password) {
    let response_service = await this.login_repository.loginUser(email, password);

    if(!response_service.success) {
      return new ServiceResponse(false, [], 'Error en el servicio').toPromise();
    }

    return new ServiceResponse(true, response_service.data, 'Usuarios con exito').toPromise();
  }
}

export default LoginService;