import Rest from '../../../core/libs/Rest';

class ListRepository {
  rest;

  constructor() {
    this.rest = new Rest();
  }

  listArticles() {
    this.rest.setParams('');
    this.rest.setEndPoint('api/mod-article/list-all');
    return this.rest.get();
  }

  deleteArticle(article_id) {
    this.rest.setParams('');
    this.rest.setEndPoint('api/mod-article/delete-article');
    return this.rest.delete(article_id);
  }
}

export default ListRepository;