import Rest from '../../../core/libs/Rest';
import { LISTADO_MOCKUP } from '../mockups/List';

class CategoriesRepository {
  rest;

  constructor() {
    this.rest = new Rest();
  }

  listCategories() {
    this.rest.setParams('');
    this.rest.setEndPoint('api/category/list-all');
    return this.rest.peticionMockupV2('GET', null, LISTADO_MOCKUP.LIST_CATEGORIES);
    //return this.rest.get();
  }

  deleteCategory(course_cat_id) {
    this.rest.setParams('');
    this.rest.setEndPoint('api/category/delete-category');
    return this.rest.peticionMockupV2('DELETE', course_cat_id, LISTADO_MOCKUP.DELETE_CATEGORY);
    //return this.rest.delete(course_cat_id);
  }

  createCategory(name) {
    this.rest.setParams({name:name});
    this.rest.setEndPoint('api/category/store-category');
    return this.rest.peticionMockupV2('POST', null, LISTADO_MOCKUP.CREATE_CATEGORY);
    //return this.rest.post();
  }

  editCategory(course_cat_id, name) {
    this.rest.setParams({name:name});
    this.rest.setEndPoint('api/category/update-category');
    return this.rest.peticionMockupV2('PUT', course_cat_id, LISTADO_MOCKUP.EDIT_CATEGORY);
    //return this.rest.put(course_cat_id);
  }
}

export default CategoriesRepository;