import CategoriesRepository from "../repositories/CategoriesRepository";

class CategoriesService {
  categories_repository;

  constructor() {
    this.categories_repository = new CategoriesRepository();
  }

  async listCategories() {
    let r = await this.categories_repository.listCategories();
    return r;
  }

  async deleteCategory(course_cat_id) {
    let r = await this.categories_repository.deleteCategory(course_cat_id);
    return r;
  }

  async createCategory(name) {
    let r = await this.categories_repository.createCategory(name);
    return r;
  }

  async editCategory(course_cat_id, name) {
    let r = await this.categories_repository.editCategory(course_cat_id, name);
    return r;
  }
}

export default CategoriesService;