import ListRepository from "../repositories/ListRepository";

class ListService {
  list_repository;

  constructor() {
    this.list_repository = new ListRepository();
  }

  async listTitles() {
    let r = await this.list_repository.listTitles();
    return r;
  }

  async deleteTitle(title_id) {
    let r = await this.list_repository.deleteTitle(title_id);
    return r;
  }
}

export default ListService;