import Rest from '../../../core/libs/Rest';

class ManageRepository {
  rest;

  constructor() {
    this.rest = new Rest();
  }

  createVideo(title, article) {
    this.rest.setParams({name:title, video:article});
    this.rest.setEndPoint('api/mod-video/store-video');
    return this.rest.post();
  }

  editVideo(mod_video_id, title, video_url) {
    this.rest.setParams({name:title, video:video_url});
    this.rest.setEndPoint('api/mod-video/update-video');
    return this.rest.put(mod_video_id);
  }

  infoVideo(video_id) {
    this.rest.setParams('/'+video_id);
    this.rest.setEndPoint('api/mod-video/show-video');
    return this.rest.get(video_id);
  }
}

export default ManageRepository;