<template>
    <div>
        <v-data-table
            :headers="headers"
            :items="passengers"
            :search="search"
            :loading="loading"
            class="elevation-1"
            :items-per-page="10"
        >
            <template v-slot:top>
                <v-text-field
                    v-model="search"
                    label="Buscar"
                    filled
                ></v-text-field>
            </template>
            <template v-slot:item.actions="{ item }">
                <!-- Botones -->
                <v-tooltip bottom v-for="(item_button, index) in config_buttons" :key="index">
                    <template v-slot:activator="{ on, attrs }">
                        <!-- Enlace -->
                        <v-btn icon :color="item_button.color" v-bind="attrs" v-on="on" v-if="item_button.webservice.endpoint == null" :href="configureButtonUrl(item, item_button)">
                            <v-icon>{{ item_button.icon }}</v-icon>
                        </v-btn>
                        <!-- Servicio de eliminar -->
                        <v-btn icon :color="item_button.color" v-bind="attrs" v-on="on" v-if="item_button.webservice.endpoint" @click="deleteFromAPI(item, item_button)">
                            <v-icon>{{ item_button.icon }}</v-icon>
                        </v-btn>
                    </template>
                    <span>{{ item_button.label }}</span>
                </v-tooltip>
            </template>
        </v-data-table>
    </div>
</template>

<script>
import Helpers from '../../../../../helpers/Helpers';
import Rest from '../../../../../libs/Rest';
import PaginatorSimpleController from '../controllers/PaginatorSimpleController';
export default {
    props: ['headers','endpoint', 'config_buttons'],
    data: () => {
        return {
            Helpers,
            passengers: [],
            loading: true,
            search: '',

            paginator_controller: null
        };
    },
    mounted(){
        this.paginator_controller = new PaginatorSimpleController(this);
    },
    methods: {
        configureButtonUrl(item_entity, item_button){
            return this.paginator_controller.configureButtonUrl(item_entity, item_button);
        },
        deleteFromAPI(item_entity, item_button){
            return this.paginator_controller.deleteFromAPI(item_button['webservice']['endpoint'], item_entity[item_button.webservice.param]);
        }
    },
}
</script>