import ListStudentsEnrolledRepository from "../repositories/ListStudentsEnrolledRepository";

class ListStudentsEnrolledService {
  list_students_enrolled_repository;

  constructor() {
    this.list_students_enrolled_repository = new ListStudentsEnrolledRepository();
  }

  async listStudents() {
    let r = await this.list_students_enrolled_repository.listStudents();
    return r;
  }
}

export default ListStudentsEnrolledService;