import DetailStudentsRepository from "../repositories/DetailStudentsRepository";

class DetailStudentsService {
  detail_students_repository;

  constructor() {
    this.detail_students_repository = new DetailStudentsRepository();
  }

  async infoCourse(course_id) {
    let r = await this.detail_students_repository.infoCourse(course_id);
    return r;
  }
}

export default DetailStudentsService;