import UI from  '../../../core/helpers/UI';
import File from "../../../core/helpers/File";
import ListAdminService from '../services/ListAdminService';

class ListAdminController {
  context;
  list_admin_service;

  constructor(context) {
    this.context = context;
    this.list_admin_service = new ListAdminService();

    this.context.headers = [
      { text: 'Título', value: 'title', sortable: false },
      { text: 'Descripción', value: 'description', sortable: false },
      { text: 'Fecha', value: 'date', sortable: false },
      { text: 'Cant. Estudiantes', value: 'count_students', sortable: false },
      { text: 'Estado', value: 'state', sortable: false },
      { text: '', value: 'actions', sortable: false },
    ];
    
    this.listCourses();
    this.listCategories();
  }

  listCourses() {
    UI.loading('show');
    this.list_admin_service.listCourses()
    .then((r) => {
      if(r.success == true) {
        this.context.date_init = null;
        this.context.date_finish = null;
        this.context.category_id = null;
        this.context.is_search = false;
        this.context.list_courses = [];
        this.context.list_courses = r.data;
        
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  listCategories() {
    UI.loading('show');
    this.list_admin_service.listCategories()
    .then((r) => {
      if(r.success == true) {
        this.context.list_categories = [];
        this.context.list_categories = r.data;
        
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  filterListCourses() {
    if(this.context.category_id) {
      UI.loading('show');
      this.list_admin_service.filterListCourses(this.context.date_init, this.context.date_finish, this.context.category_id)
      .then((r) => {
        if(r.success == true) {
          this.context.is_search = true;
          this.context.list_courses = [];
          this.context.list_courses = r.data;
          
          UI.loading('hide');
        }
      }).catch((e) => {
        UI.loading('hide');
        UI.mensajeBad('', e.msg);
      });
    } else {
      UI.mensajeBad('', 'Debes seleccionar una categoría para filtrar.');
    }
  }

  deleteCourse(position, course_id) {
    UI.loading('show');
    this.list_admin_service.deleteCourse(course_id)
    .then((r) => {
      if(r.success == true) {
        this.context.list_courses.splice(position, 1);
        UI.mensajeOk('', r.msg);
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  goResults(poll_id) {
    window.location.href = '#/surveys/results?survey='+poll_id;
  }
}

export default ListAdminController;