<template>
  <div>
    <section class="main">
      <div class="container-fluid main-box-content contentModulo py-2">
        <div class="breadcrumb px-3">
          <ul>
            <li>Inicio</li>
            <li>Cursos</li>
            <li>Listado de cursos</li>
          </ul>
        </div>

        <div class="title">
          <h1 class="my-0">Listado de cursos</h1>
        </div>

        <div class="row mt-3 mt-md-4 pb-4">
          <template v-for="(course, i) in list_courses">
            <div class="col-sm-6 col-md-4 mt-4 mt-lg-5" :key="i">
              <v-card elevation="2">
                <v-img v-if="course.image" :src="course.image" height="200px"></v-img>

                <div class="row mx-0 py-3 align-items-start">
                  <div class="col-3" v-if="course.progress > 0 && course.is_subscribed || course.is_subscribed">
                    <v-progress-circular size="60" rotate="-90" width="5" :value="course.progress" color="success" rounded>
                      {{course.progress}}%
                    </v-progress-circular>
                  </div>
                  <div :class="course.progress > 0 && course.is_subscribed || course.is_subscribed ? 'col-9 pl-0' : 'col-12'">
                    <v-card-title v-if="course.title" class="p-0">
                      {{course.title}}
                    </v-card-title>

                    <v-card-subtitle v-if="course.category" class="p-0 mt-1">
                      {{course.category.category_name}}
                    </v-card-subtitle>
                  </div>
                </div>

                <v-card-actions>
                  <v-btn text color="primary" :href="'#/courses/detail-students/'+course.course_id">Ver curso</v-btn>
                  <v-spacer></v-spacer>
                  <v-btn color="info" class="fw-500" text v-if="!course.is_subscribed" @click="subscribeCourse(course.course_id, i)">
                    Inscribirme
                  </v-btn>
                  <v-btn color="danger" class="fw-500" text v-if="course.is_subscribed" @click="cancelSubscribeCourse(course.course_id, i)">
                    Cancelar inscripción
                  </v-btn>
                </v-card-actions>
              </v-card>
            </div>
          </template>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import Helpers from '../../../core/helpers/Helpers';
import ListStudentsController from '../controllers/ListStudentsController';

export default {
  data() {
    return {
      Helpers,
      list_courses: [],

      list_student_controller: null
    };
  },

  mounted() {
    this.list_student_controller = new ListStudentsController(this);
  },

  methods: {
    subscribeCourse(course_id, position) {
      this.list_student_controller.subscribeCourse(course_id, position);
    },

    cancelSubscribeCourse(course_id, position) {
      this.list_student_controller.cancelSubscribeCourse(course_id, position);
    },
  }
}
</script>