<template>
  <div>
    <section class="main">
      <div class="container-fluid main-box-content contentModulo py-2">
        <div class="breadcrumb px-3">
          <ul>
            <li>Inicio</li>
            <li>Módulos</li>
            <li>{{is_edit ? 'Editar' : 'Crear'}} imagen</li>
          </ul>
        </div>

        <div class="title">
          <h1 class="my-0">{{is_edit ? 'Editar' : 'Crear'}} imagen</h1>
        </div>

        <v-card class="mt-3 mt-md-4">
          <v-card-text class="px-lg-4">
            <v-form class="w-100" ref="form_image" method="post" v-model="form_valid" lazy-validation @submit.prevent="manageImage()">
              <v-text-field label="Titulo" v-model="title" :rules="[rules.requerido]" filled></v-text-field>

              <div class="row">
                <div class="col-sm-12 mb-3 mb-md-0">
                  <div class="row align-items-start justify-content-center" v-if="image && !send_url">
                    <div class="col-10 col-sm-11 col-md-6">
                      <img :src="image" width="100%" class="d-block" alt="">
                    </div>
                    <div class="col-2 col-sm-1">
                      <v-btn icon class="d-table ml-auto" color="danger" @click="deleteImage()">
                        <v-icon>mdi-close</v-icon>
                      </v-btn>
                    </div>
                  </div>

                  <v-file-input filled label="Archivo de imagen" v-if="!send_url && !image" :prepend-icon="null" v-model="upload_image" @change="uploadedImage"></v-file-input>
                </div>
              </div>

              <v-btn class="d-table mx-auto mt-2" rounded width="120" color="success" type="submit">{{is_edit ? 'Actualizar' : 'Guardar'}}</v-btn>
            </v-form>
          </v-card-text>
        </v-card>
      </div>
    </section>
  </div>
</template>

<script>
import Helpers from '../../../core/helpers/Helpers';
import ManageController from '../controllers/ManageController';

export default {
  data() {
    return {
      Helpers,
      image_id: null,
      is_edit: false,
      title: '',
      image: '',
      send_url: false,

      form_valid: true,

      rules: {
        requerido: value => !!value || 'Campo obligatorio',
        url: value => this.validatedUrl(value) || 'URL no válida',
        email: value => !value || /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(value) || 'Email incorrecto',
      },

      upload_image: null,

      manage_controller: null
    };
  },

  mounted() {
    this.manage_controller = new ManageController(this);
  },

  methods: {
    manageImage() {
      this.manage_controller.manageImage();
    },

    uploadedImage() {
      this.manage_controller.uploadedImage();
    },

    deleteImage() {
      this.manage_controller.deleteImage();
    },

    changeSend() {
      this.image = '';
      this.upload_image = null;
      this.send_url = !this.send_url;
    },

    validatedUrl(str) {
      let url;

      try {
        url = new URL(str);
      } catch (_) {
        return false;
      }

      return url.protocol === "http:" || url.protocol === "https:";
    },
  }
}
</script>