import UI from  '../../../core/helpers/UI';
import File from "../../../core/helpers/File";
import ListService from '../services/ListService';

class ListController {
  context;
  list_service;

  constructor(context) {
    this.context = context;
    this.list_service = new ListService();

    this.context.headers = [
      { text: 'Código estudiante', value: 'student_id' },
      { text: 'Estudiante', value: 'student' },
      { text: 'Curso', value: 'enrollment.course.title' },
      { text: 'Estado', value: 'state' },
      { text: '', value: 'actions' },
    ],
    
    this.listInscriptions();
  }

  listInscriptions() {
    UI.loading('show');
    this.list_service.listInscriptions()
    .then((r) => {
      if(r.success == true) {
        this.context.is_search = false;
        this.context.date_init = null;
        this.context.date_finish = null;
        this.context.list_inscriptions = [];
        this.context.list_inscriptions = r.data;
        
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  deleteInscription(position, inscription_id) {
    UI.loading('show');
    this.list_service.deleteInscription(inscription_id)
    .then((r) => {
      if(r.success == true) {
        this.context.list_inscriptions.splice(position, 1);
        UI.mensajeOk('', r.msg);

        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  filterInscriptions() {
    if(this.context.date_init && this.context.date_finish) {
      UI.loading('show');
      this.list_service.filterInscriptions(this.context.date_init, this.context.date_finish)
      .then((r) => {
        if(r.success == true) {
          this.context.is_search = true;
          this.context.list_inscriptions = [];
          this.context.list_inscriptions = r.data;
          
          UI.loading('hide');
        }
      }).catch((e) => {
        UI.loading('hide');
        UI.mensajeBad('', e.msg);
      });
    } else {
      UI.mensajeBad('', 'Selecciona dos fechas para realizar la búsqueda');
    }
  }
}

export default ListController;