import MyInscriptionsRepository from "../repositories/MyInscriptionsRepository";

class MyInscriptionsService {
  my_inscriptions_repository;

  constructor() {
    this.my_inscriptions_repository = new MyInscriptionsRepository();
  }

  async listCategories() {
    let r = await this.my_inscriptions_repository.listCategories();
    return r;
  }

  async listMyInscriptions() {
    let r = await this.my_inscriptions_repository.listMyInscriptions();
    return r;
  }

  async deleteSubscription(course_id) {
    let r = await this.my_inscriptions_repository.deleteSubscription(course_id);
    return r;
  }

  async filterMyInscriptions(search, category_id) {
    let r = await this.my_inscriptions_repository.filterMyInscriptions(search, category_id);
    return r;
  }
}

export default MyInscriptionsService;