import ListStudentsRepository from "../repositories/ListStudentsRepository";

class ListStudentsService {
  list_students_repository;

  constructor() {
    this.list_students_repository = new ListStudentsRepository();
  }

  async listCoursesForStudent() {
    let r = await this.list_students_repository.listCoursesForStudent();
    return r;
  }

  async subscribeCourse(course_id) {
    let r = await this.list_students_repository.subscribeCourse(course_id);
    return r;
  }

  async cancelSubscribeCourse(course_id) {
    let r = await this.list_students_repository.cancelSubscribeCourse(course_id);
    return r;
  }
}

export default ListStudentsService;