import Site from '../../core/layouts/Principal.vue';
import ListStudentsEnrolled from './views/ListStudentsEnrolled.vue';
import ListStudentsNews from './views/ListStudentsNews.vue';
import ListCourses from './views/ListCourses.vue';

window.routes.push({
  path: '/reports',
  name: 'reports',
  component: Site,
  children: [
    {
      path: 'list-enrolled',
      name: 'list-enrolled',
      component: ListStudentsEnrolled,
    },
    {
      path: 'list-news',
      name: 'list-news',
      component: ListStudentsNews,
    },
    {
      path: 'list-courses',
      name: 'list-courses',
      component: ListCourses,
    },
  ],
});