<template>
  <div>
    <section class="main">
      <div class="container-fluid main-box-content contentModulo py-2">
        <div class="breadcrumb px-3">
          <ul>
            <li>Inicio</li>
            <li>Lecciones</li>
            <li>Listado de lecciones</li>
          </ul>
        </div>

        <div class="title">
          <h1 class="my-0">Listado de lecciones</h1>
        </div>

        <v-card class="mt-3 mt-md-4">
          <v-card-text>
            <div class="row align-items-end pt-3">
              <div class="col-md-8 col-lg-9 col-xl-10 box-set-date">
                <div class="row align-items-end mx-n2">
                  <div class="col-lg-6 px-2">
                    <p class="mt-0 mb-1 text-left text-standar fw-600 fz-16">Palabra clave</p>

                    <v-text-field v-model="search_list_lessons" append-icon="mdi-magnify" outlined dense hide-details clearable></v-text-field>
                  </div>
                  <div class="col-lg-3 px-2">
                    <p class="mt-0 mb-1 text-left text-standar fw-600 fz-16">Módulos</p>

                    <v-select outlined dense hide-details :items="list_modules" v-model="module_id" item-text="name" item-value="module_id"></v-select>
                  </div>
                  <div class="col-lg-2 px-2">
                    <v-btn rounded @click="filterListLessons()" color="success" class="d-table ml-auto px-md-4">Buscar</v-btn>
                  </div>
                </div>
              </div>
              <div class="col-md-4 col-lg-3 col-xl-2 mt-3 mt-md-0">
                <div class="d-flex align-items-center justify-content-between">
                  <v-btn color="info" @click="getAllLessons()" v-if="is_search" rounded class="d-table ml-auto">
                    Ver todos
                  </v-btn>
                </div>
              </div>
            </div>

            <div class="table-responsive mt-3">
              <v-data-table
                class="mt-3"
                :headers="headers"
                :items="list_lessons"
                :items-per-page="10"
                :search="search_list_lessons"
              >
                <template v-slot:item.actions="{ item }">
                  <!-- Editar -->
                  <v-tooltip bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn icon color="primary" :href="'#/lessons/manage/'+item.lesson_id" v-bind="attrs" v-on="on">
                        <v-icon>mdi-square-edit-outline</v-icon>
                      </v-btn>
                    </template>
                    <span>Editar</span>
                  </v-tooltip>
                  
                  <!-- Eliminar -->
                  <v-tooltip bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn icon color="danger" v-bind="attrs" v-on="on" @click="deleteLesson(list_lessons.map(function(x) {return x.lesson_id; }).indexOf(item.lesson_id), item.lesson_id)">
                        <v-icon>mdi-trash-can-outline</v-icon>
                      </v-btn>
                    </template>
                    <span>Eliminar</span>
                  </v-tooltip>
                </template>
              </v-data-table>
            </div>
          </v-card-text>
        </v-card>
      </div>
    </section>
  </div>
</template>

<script>
import Helpers from '../../../core/helpers/Helpers';
import ListController from '../controllers/ListController';

export default {
  data() {
    return {
      Helpers,
      search_list_lessons: '',
      headers: [],
      list_lessons: [],
      list_modules: [],
      dialog_detail: {},
      is_search: false,
      module_id: null,

      list_controller: null
    };
  },

  mounted() {
    this.list_controller = new ListController(this);
  },

  methods: {
    deleteLesson(position, lesson_id) {
      this.list_controller.deleteLesson(position, lesson_id);
    },

    getAllLessons() {
      this.list_controller.listLessons();
    },

    filterListLessons() {
      this.list_controller.filterListLessons();
    }
  }
}
</script>