import Rest from '../../../core/libs/Rest';
import { LISTADO_MOCKUP } from '../mockups/List';

class ListAdminRepository {
  rest;

  constructor() {
    this.rest = new Rest();
  }

  listCourses() {
    this.rest.setParams('');
    this.rest.setEndPoint('api/course/list-all');
    return this.rest.peticionMockupV2('GET', null, LISTADO_MOCKUP.LIST_COURSES);
    //return this.rest.get();
  }

  listCategories() {
    this.rest.setParams('');
    this.rest.setEndPoint('api/category/list-all');
    return this.rest.peticionMockupV2('GET', null, LISTADO_MOCKUP.LIST_CATEGORIES);
    //return this.rest.get();
  }

  deleteCourse(course_id) {
    this.rest.setParams('');
    this.rest.setEndPoint('api/course/delete-course');
    return this.rest.peticionMockupV2('DELETE', course_id, LISTADO_MOCKUP.DELETE_COURSE);
    //return this.rest.delete(course_id);
  }

  filterListCourses(date_init, date_finish, category_id) {
    this.rest.setParams({date_init:date_init, date_finish:date_finish, category_id:category_id});
    this.rest.setEndPoint('api/course/filter-by-category');
    return this.rest.peticionMockupV2('POST', null, LISTADO_MOCKUP.LIST_FILTER_COURSES);
    //return this.rest.post();
  }
}

export default ListAdminRepository;