import ManageRepository from "../repositories/ManageRepository";

class ManageService {
  manage_repository;

  constructor() {
    this.manage_repository = new ManageRepository();
  }

  async createDocument(title, document) {
    let r = await this.manage_repository.createDocument(title, document);
    return r;
  }

  async editDocument(title_id, title, documents) {
    let r = await this.manage_repository.editDocument(title_id, title, documents);
    return r;
  }

  async infoDocument(title_id) {
    let r = await this.manage_repository.infoDocument(title_id);
    return r;
  }
}

export default ManageService;