import UI from  '../../../core/helpers/UI';
import Helpers from '../../../core/helpers/Helpers';
import ManageService from '../services/ManageService';

class ManageController {
  context;
  manage_service;

  constructor(context) {
    this.context = context;
    this.manage_service = new ManageService();

    if(this.context.$route.params.id) {
      this.context.article_id = JSON.parse(this.context.$route.params.id);
      this.context.is_edit = true;
      this._infoArticle();
    }
  }

  manageArticle() {
    //DNS64F
    if(this.context.is_edit) {
      this._editArticle();
    } else {
      this._createArticle();
    }
  }

  _createArticle() {
    if(this.context.$refs.form_title.validate()) {
      UI.loading('show');
      this.manage_service.createArticle(this.context.title, this.context.article)
      .then((r) => {
        if(r.success == true) {
          this.context.$refs.form_title.reset();
          UI.mensajeOk('', r.msg);
          window.location = '#/articles/list';
          UI.loading('hide');
        }
      }).catch((e) => {
        UI.loading('hide');
        UI.mensajeBad('', e.msg);
      });
    }
  }

  _editArticle() {
    if(this.context.$refs.form_title.validate()) {
      UI.loading('show');
      this.manage_service.editArticle(this.context.article_id, this.context.title, this.context.article)
      .then((r) => {
        if(r.success == true) {
          UI.mensajeOk('', r.msg);
          UI.loading('hide');
          window.location = '#/articles/list';
        }
      }).catch((e) => {
        UI.loading('hide');
        UI.mensajeBad('', e.msg);
      });
    }
  }

  _infoArticle() {
    UI.loading('show');
    this.manage_service.infoArticle(this.context.article_id)
    .then((r) => {
      if(r.success == true) {
        this.context.info_title = r.data;

        this.context.article_id = r.data.mod_article_id;
        this.context.title = r.data.name;
        this.context.article = r.data.content;

        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }
}

export default ManageController;