<template>
  <div>
    <section class="main">
      <div class="container-fluid main-box-content contentModulo py-2">
        <div class="breadcrumb px-3">
          <ul>
            <li>Inicio</li>
            <li>Reportes</li>
            <li>Listado de cursos</li>
          </ul>
        </div>

        <div class="title">
          <h1 class="my-0">Listado de cursos</h1>
        </div>

        <v-card class="mt-3 mt-md-4">
          <v-card-text>
            <div class="table-responsive">
              <v-data-table
                class="mt-3"
                :headers="headers"
                :items="list_courses"
                :items-per-page="10"
                :search="search_list_courses"
              >
                <template v-slot:top>
                  <v-text-field
                    filled
                    v-model="search_list_courses"
                    append-icon="mdi-magnify"
                    label="Buscar"
                    hide-details
                    clearable
                    class="mb-4"
                  ></v-text-field>
                </template>

                <template v-slot:item.state="{ item }">
                  <!-- Estado -->
                  <v-chip color="success" v-if="item.state == 1">Habilitada</v-chip>
                </template>

                <template v-slot:item.public_url="{ item }">
                  <!-- Link -->
                  <v-tooltip bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn icon color="warning" :href="item.public_url" target="_blank" v-bind="attrs" v-on="on">
                        <v-icon>mdi-link</v-icon>
                      </v-btn>
                    </template>
                    <span>Ver enlace</span>
                  </v-tooltip>
                </template>

                <template v-slot:item.actions="{ item }">
                  <!-- Editar -->
                  <v-tooltip bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn icon color="primary" @click="goEdit(item.course_id)" v-bind="attrs" v-on="on">
                        <v-icon>mdi-square-edit-outline</v-icon>
                      </v-btn>
                    </template>
                    <span>Editar</span>
                  </v-tooltip>

                  <!-- Detalle -->
                  <v-tooltip bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn icon color="success" @click="goDetail(item.course_id)" v-bind="attrs" v-on="on">
                        <v-icon>mdi-eye-outline</v-icon>
                      </v-btn>
                    </template>
                    <span>Ver detalle</span>
                  </v-tooltip>
                  
                  <!-- Eliminar -->
                  <v-tooltip bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn icon color="danger" v-bind="attrs" v-on="on" @click="deleteSurvey(list_courses.map(function(x) {return x.course_id; }).indexOf(item.course_id), item.course_id)">
                        <v-icon>mdi-trash-can-outline</v-icon>
                      </v-btn>
                    </template>
                    <span>Eliminar</span>
                  </v-tooltip>
                </template>
              </v-data-table>
            </div>
          </v-card-text>
        </v-card>
      </div>
    </section>
  </div>
</template>

<script>
import Helpers from '../../../core/helpers/Helpers';
import ListCoursesController from '../controllers/ListCoursesController';

export default {
  data() {
    return {
      Helpers,
      search_list_courses: '',
      headers: [],
      list_courses: [],

      list_courses_controller: null
    };
  },

  mounted() {
    this.list_courses_controller = new ListCoursesController(this);
  },

  methods: {
    
  }
}
</script>