import ListRepository from "../repositories/ListRepository";

class ListService {
  list_repository;

  constructor() {
    this.list_repository = new ListRepository();
  }

  async listCategories() {
    let r = await this.list_repository.listCategories();
    return r;
  }

  async listInscriptions() {
    let r = await this.list_repository.listInscriptions();
    return r;
  }

  async deleteInscription(course_id) {
    let r = await this.list_repository.deleteInscription(course_id);
    return r;
  }

  async filterInscriptions(date_init, date_finish) {
    let r = await this.list_repository.filterInscriptions(date_init, date_finish);
    return r;
  }
}

export default ListService;