<template>
  <div>
    <section class="main">
      <div class="container-fluid main-box-content contentModulo py-2">
        <div class="breadcrumb px-3">
          <ul>
            <li>Inicio</li>
            <li>Profesores</li>
            <li>Listado de estudiantes</li>
          </ul>
        </div>

        <div class="title">
          <h1 class="my-0">Listado de estudiantes</h1>
        </div>

        <v-card class="mt-3 mt-md-4">
          <v-card-text>
            <div class="row align-items-end pt-3">
              <div class="col-md-8 col-lg-9 col-xl-10 box-set-date">
                <div class="row align-items-end mx-n2">
                  <div class="col-lg-7 px-2">
                    <p class="mt-0 mb-1 text-left text-standar fw-600 fz-16">Palabras clave</p>

                    <v-text-field outlined dense v-model="search_list_students" append-icon="mdi-magnify" hide-details clearable class="my-0"></v-text-field>
                  </div>
                  <div class="col-lg-3 px-2">
                    <p class="mt-0 mb-1 text-left text-standar fw-600 fz-16">Cursos</p>

                    <v-select outlined v-model="course_id" :items="list_courses" item-value="course_id" item-text="title" dense hide-details></v-select>
                  </div>
                  <div class="col-lg-2 px-2">
                    <v-btn rounded @click="filterListStudents()" color="success" class="d-table ml-auto px-md-4">Buscar</v-btn>
                  </div>
                </div>
              </div>
              <div class="col-md-4 col-lg-3 col-xl-2 mt-3 mt-md-0">
                <div class="d-flex align-items-center justify-content-between">
                  <v-btn color="info" @click="listStudents()" v-if="is_search" rounded class="d-table ml-auto">
                    Ver todos
                  </v-btn>
                </div>
              </div>
            </div>

            <div class="table-responsive mt-3">
              <v-data-table
                class="mt-3"
                :headers="header_students"
                :items="list_students"
                :items-per-page="10"
                :search="search_list_students"
              >
                <template v-slot:item.state="{ item }">
                  <!-- Estado -->
                  <v-chip color="success" v-if="item.state == 1">Activo</v-chip>
                  <v-chip color="danger" v-if="item.state == 2">Inactivo</v-chip>
                </template>

                <template v-slot:item.actions="{ item }">
                  <!-- Detalle -->
                  <v-dialog v-model="dialog_detail[item.student_id]" width="500">
                    <template v-slot:activator="{ on, attrs }">
                      <v-tooltip bottom v-bind="attrs" v-on="on">
                        <template v-slot:activator="{ on, attrs }">
                          <v-btn icon color="success" @click="infoStudent(item.student_id)" v-bind="attrs" v-on="on">
                            <v-icon>mdi-eye-outline</v-icon>
                          </v-btn>
                        </template>
                        <span>Ver detalle</span>
                      </v-tooltip>
                    </template>

                    <v-card>
                      <div class="title d-flex align-items-center justify-content-between flex-wrap p-4">
                        <h1 class="my-0">Detalle del estudiante</h1>
                        <v-btn icon color="danger" @click="closeDetail(item.student_id)">
                          <v-icon>mdi-close</v-icon>
                        </v-btn>
                      </div>

                      <v-card-text>
                        <div class="row">
                          <div class="col-sm-6 mb-4" v-if="info_student.names || info_student.last_names">
                            <h4 class="mt-0 mb-1 text-left text-standar fw-600 fz-16">Nombre estudiante:</h4>
                            <p class="my-0 fw-400 fz-16 text-black">{{info_student.names+' '+info_student.last_names}}</p>
                          </div>
                          <div class="col-sm-6 mb-4" v-if="info_student.program">
                            <h4 class="mt-0 mb-1 text-left text-standar fw-600 fz-16">Programa:</h4>
                            <p class="my-0 fw-400 fz-16 text-black">{{info_student.program}}</p>
                          </div>
                          <div class="col-sm-6 mb-4" v-if="info_student.dater">
                            <h4 class="mt-0 mb-1 text-left text-standar fw-600 fz-16">Fecha de inscripción:</h4>
                            <p class="my-0 fw-400 fz-16 text-black">{{info_student.dater}}</p>
                          </div>
                          <div class="col-sm-6 mb-4" v-if="info_student.last_conection">
                            <h4 class="mt-0 mb-1 text-left text-standar fw-600 fz-16">Última conexión:</h4>
                            <p class="my-0 fw-400 fz-16 text-black">{{info_student.last_conection}}</p>
                          </div>
                          <div class="col-12 mb-4" v-if="info_student.courses && info_student.courses.length > 0">
                            <h4 class="mt-0 mb-1 text-left text-standar fw-600 fz-16">Cursos:</h4>
                            <p class="my-0 fw-400 fz-16 text-black">
                              <template v-for="(name, i) in info_student.courses">
                                <span :key="i">{{i > 0 ? ', '+name.title : name.title}}</span>
                              </template>
                            </p>
                          </div>
                        </div>
                      </v-card-text>
                    </v-card>
                  </v-dialog>
                  
                  <!-- Eliminar -->
                  <v-tooltip bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn icon color="danger" v-bind="attrs" v-on="on" @click="deleteStudent(list_students.map(function(x) {return x.student_id; }).indexOf(item.student_id), item.student_id)">
                        <v-icon>mdi-trash-can-outline</v-icon>
                      </v-btn>
                    </template>
                    <span>Eliminar</span>
                  </v-tooltip>
                </template>
              </v-data-table>
            </div>
          </v-card-text>
        </v-card>
      </div>
    </section>
  </div>
</template>

<script>
import Helpers from '../../../core/helpers/Helpers';
import ListController from '../controllers/ListController';

export default {
  data() {
    return {
      Helpers,
      search_list_students: '',
      header_students: [],
      list_students: [],
      list_courses: [],
      dialog_detail: {},
      info_student: {},
      is_search: false,
      course_id: null,

      date_init: (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10),
      date_finish: (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10),
      menu_init: false,
      menu_finish: false,

      list_controller: null
    };
  },

  mounted() {
    this.list_controller = new ListController(this);
  },

  methods: {
    listStudents() {
      this.list_controller.listStudents();
    },

    deleteStudent(position, student_id) {
      this.list_controller.deleteStudent(position, student_id);
    },

    infoStudent(student_id) {
      this.list_controller.infoStudent(student_id);
    },

    closeDetail(student_id) {
      this.list_controller.closeDetail(student_id);
    },

    filterListStudents() {
      this.list_controller.filterListStudents();
    }
  }
}
</script>