import UI from  '../../../core/helpers/UI';
import Helpers from '../../../core/helpers/Helpers';
import ManageService from '../services/ManageService';

class ManageController {
  context;
  manage_service;

  constructor(context) {
    this.context = context;
    this.manage_service = new ManageService();

    if(this.context.$route.params.id) {
      this.context.video_id = JSON.parse(this.context.$route.params.id);
      this.context.is_edit = true;
      this._infoVideo();
    }
  }

  manageVideo() {
    if(this.context.is_edit) {
      this._editVideo();
    } else {
      this._createVideo();
    }
  }

  _createVideo() {
    if(this.context.$refs.form_video.validate()) {
      UI.loading('show');
      this.manage_service.createVideo(this.context.title, this.context.video_url)
      .then((r) => {
        if(r.success == true) {
          this.context.list_videos = [];
          this.context.$refs.upload_video = null;
          this.context.$refs.form_video.reset();
          UI.mensajeOk('', r.msg);
          window.location = '#/videos/list';
          UI.loading('hide');
        }
      }).catch((e) => {
        UI.loading('hide');
        UI.mensajeBad('', e.msg);
      });
    }
  }

  _editVideo() {
    if(this.context.$refs.form_video.validate()) {
      UI.loading('show');
      this.manage_service.editVideo(
        this.context.video_id,
        this.context.title, 
        this.context.video_url
      ).then((r) => {
        if(r.success == true) {
          UI.mensajeOk('', r.msg);
          UI.loading('hide');
          window.location = '#/videos/list';
        }
      }).catch((e) => {
        UI.loading('hide');
        UI.mensajeBad('', e.msg);
      });
    }
  }

  _infoVideo() {
    UI.loading('show');
    this.manage_service.infoVideo(this.context.video_id)
    .then((r) => {
      if(r.success == true) {
        this.context.info_video = r.data;

        this.context.video_id = r.data.mod_video_id;
        this.context.title = r.data.name;
        this.context.video_url = r.data.video;

        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }
}

export default ManageController;