import Vue from "vue";
import LoginLayout from "../layouts/Login.vue";
import Site from '../layouts/Principal.vue';
import Login from "../modules/login/views/Form.vue";
import Start from "../modules/start/views/Hello.vue";
import Jobs from "../modules/jobs/views/Jobs.vue";

//examples
import Paginator from "../modules/examples/views/Paginator.vue";
Vue.component("filter-select", require("../modules/examples/components/paginator/views/FilterSelect.vue").default);
Vue.component("filters", require("../modules/examples/components/paginator/views/Filters.vue").default);
Vue.component("table-paginator-simple", require("../modules/examples/components/paginator/views/TablePaginatorSimple.vue").default);
Vue.component("table-paginator-extended", require("../modules/examples/components/paginator/views/TablePaginatorExtended.vue").default);

Vue.component("table-paginator-view", require("../modules/examples/components/views/views/TablePaginatorView.vue").default);
Vue.component("dynamic-view", require("../modules/examples/components/views/views/DynamicView.vue").default);
Vue.component("filter-select-view", require("../modules/examples/components/views/views/FilterSelect.vue").default);
Vue.component("filters-view", require("../modules/examples/components/views/views/Filters.vue").default);

Vue.component("core-header", require("../modules/layouts/components/header.vue").default);
Vue.component("core-menu", require("../modules/menu/views/Menu.vue").default);

if(process.env.VUE_APP_CUSTOM_MENU_TOP){
  Vue.component("core-menu-superior", require('../../modules/_core/'+ process.env.VUE_APP_CUSTOM_MENU_TOP).default);
}else{
  Vue.component("core-menu-superior", require("../modules/menu-superior/views/Menu.vue").default);
}

try{
  let menu = require('../config/menu.json');
  let menu_core = require('../../modules/_core/menu.json');  
  for(const item of menu_core){
    menu.push(item);
  }
  window.menu = menu;
}catch(e){}

window.routes.push({ path: "/", redirect: "/login/ingresar" });

window.routes.push({
  path: '/login',
  name: 'login',
  component: LoginLayout,
  children: [
    { path: 'ingresar', 
      name: 'ingresar',
      component: Login 
    }
  ],
});

window.routes.push({
  path: '/start',
  name: 'start',
  component: Site,
  children: [
    { path: 'welcome', 
      name: 'welcome',
      component: Start 
    }
  ],
});

window.routes.push({
  path: '/developer',
  name: 'developer',
  component: Site,
  children: [
    { path: 'jobs', 
      name: 'jobs',
      component: Jobs 
    }
  ],
});

window.routes.push({
  path: '/files',
  name: 'files',
  component: Site,
  children: [
    { path: 'loadfile', 
      name: 'loadfile',
      component: Jobs 
    }
  ],
});

window.routes.push({
  path: '/examples',
  name: 'examples',
  component: Site,
  children: [
    { path: 'paginator', 
      name: 'paginator',
      component: Paginator 
    }
  ],
});