<template>
  <div>
    <section class="main">
      <div class="container-fluid main-box-content contentModulo py-2">
        <div class="breadcrumb px-3">
          <ul>
            <li>Inicio</li>
            <li>Lecciones</li>
            <li>Módulos</li>
            <li>Cuestionarios</li>
            <li>Listado de preguntas</li>
          </ul>
        </div>

        <div class="title">
          <h1 class="my-0">Listado de preguntas</h1>
        </div>

        <v-card class="mt-3 mt-md-4">
          <v-card-text>
            <div class="row pt-3">
              <div class="col-md-9 col-lg-10">
                <v-text-field
                  filled
                  v-model="search_list_questions"
                  append-icon="mdi-magnify"
                  label="Buscar"
                  hide-details
                  clearable
                  class="my-0"
                ></v-text-field>
              </div>
              <div class="col-md-3 col-lg-2">
                <v-btn color="primary" block height="56" width="150" href="#/quizes/manage-question">Crear pregunta</v-btn>
              </div>
            </div>

            <div class="table-responsive mt-3 mt-md-4">
              <v-data-table
                class="my-0"
                :headers="header_question"
                :items="list_questions"
                :items-per-page="10"
                :search="search_list_questions"
              >
                <template v-slot:item.status="{ item }">
                  <v-chip color="success" v-if="item.status == 1">Asignada</v-chip>
                  <v-chip color="warning" v-if="item.status == 2">Sin asignar</v-chip>
                </template>

                <template v-slot:item.actions="{ item }">
                  <!-- Editar -->
                  <v-tooltip bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn icon color="primary" v-bind="attrs" v-on="on" :href="'#/quizes/manage-question/'+item.question_id">
                        <v-icon>mdi-square-edit-outline</v-icon>
                      </v-btn>
                    </template>
                    <span>Editar</span>
                  </v-tooltip>
                  
                  <!-- Eliminar -->
                  <v-tooltip bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn icon color="danger" v-bind="attrs" v-on="on" @click="deleteQuestion(list_questions.map(function(x) {return x.question_id; }).indexOf(item.question_id), item.question_id)">
                        <v-icon>mdi-trash-can-outline</v-icon>
                      </v-btn>
                    </template>
                    <span>Eliminar</span>
                  </v-tooltip>
                </template>
              </v-data-table>
            </div>
          </v-card-text>
        </v-card>
      </div>
    </section>
  </div>
</template>

<script>
import Helpers from '../../../core/helpers/Helpers';
import ListQuestionsController from '../controllers/ListQuestionsController';

export default {
  data() {
    return {
      Helpers,

      header_question: [],
      list_questions: [],
      search_list_questions: '',

      list_questions_controller: null
    };
  },

  mounted() {
    this.list_questions_controller = new ListQuestionsController(this);
  },

  methods: {
    deleteQuestion(position, question_id) {
      this.list_questions_controller.deleteQuestion(position, question_id);
    }
  }
}
</script>