<template>
  <div>
    <section class="main">
      <div class="container-fluid main-box-content contentModulo py-2">
        <div class="breadcrumb px-3">
          <ul>
            <li>Inicio</li>
            <li>Cursos</li>
            <li>Listado de categorías</li>
          </ul>
        </div>

        <div class="title">
          <h1 class="my-0">Listado de categorías</h1>
        </div>

        <v-card class="mt-3 mt-md-4">
          <v-card-text>
            <v-form class="w-100" ref="form_category" method="post" v-model="form_valid" lazy-validation @submit.prevent="manageCategory()">
              <div class="row align-items-start py-3" v-if="show_form">
                <div class="col-md-8 col-lg-9">
                  <v-text-field label="Nombre categoría" :rules="[rules.requerido]" v-model="name_category" filled></v-text-field>
                </div>
                <div class="col-md-4 col-lg-3 mt-3 mt-md-0">
                  <div class="d-flex align-items-center justify-content-between">
                    <v-btn color="success" min-width="120" min-height="58" type="submit">{{is_edit ? 'Actualizar' : 'Guardar'}}</v-btn>
                    <v-btn color="danger" min-width="120" min-height="58" @click="cancelCategory()" type="reset">Cancelar</v-btn>
                  </div>
                </div>
              </div>
            </v-form>
            
            <v-data-table
              :headers="headers"
              :items="list_categories"
              :items-per-page="10"
              :search="search_list_categories"
            >
              <template v-slot:top>
                <div class="row">
                  <div class="col-sm-9 col-md-10">
                    <v-text-field filled v-model="search_list_categories" append-icon="mdi-magnify" label="Buscar" hide-details clearable class="mb-4"></v-text-field>
                  </div>
                  <div class="col-sm-3 col-md-2">
                    <v-btn block color="primary" min-height="58" @click="createCategory()">Crear</v-btn>
                  </div>
                </div>
              </template>

              <template v-slot:item.actions="{ item }">
                <!-- Editar -->
                <v-tooltip bottom>
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn icon color="primary" v-bind="attrs" v-on="on" @click="editCategory(item)">
                      <v-icon>mdi-square-edit-outline</v-icon>
                    </v-btn>
                  </template>
                  <span>Editar</span>
                </v-tooltip>
                
                <!-- Eliminar -->
                <v-tooltip bottom>
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn icon color="danger" v-bind="attrs" v-on="on" @click="deleteCategory(list_categories.map(function(x) {return x.course_cat_id; }).indexOf(item.course_cat_id), item.course_cat_id)">
                      <v-icon>mdi-trash-can-outline</v-icon>
                    </v-btn>
                  </template>
                  <span>Eliminar</span>
                </v-tooltip>
              </template>
            </v-data-table>
          </v-card-text>
        </v-card>
      </div>
    </section>
  </div>
</template>

<script>
import Helpers from '../../../core/helpers/Helpers';
import CategoriesController from '../controllers/CategoriesController';

export default {
  data() {
    return {
      Helpers,
      search_list_categories: '',
      headers: [],
      list_categories: [],
      category_id: null,
      name_category: '',
      is_edit: false,
      form_valid: true,
      show_form: false,

      rules: {
        requerido: value => !!value || 'Campo obligatorio',
        url: value => this.validatedUrl(value) || 'URL no válida',
        email: value => !value || /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(value) || 'Email incorrecto',
      },

      categories_controller: null
    };
  },

  mounted() {
    this.categories_controller = new CategoriesController(this);
  },

  methods: {
    scrollToTop() {
      window.scrollTo(0, 0);
    },

    editCategory(item) {
      this.categories_controller.editCategory(item);
      this.scrollToTop();
    },

    cancelCategory() {
      this.categories_controller.cancelCategory();
    },

    createCategory() {
      this.categories_controller.createCategory();
    },

    deleteCategory(position, course_cat_id) {
      this.categories_controller.deleteCategory(position, course_cat_id);
    },

    manageCategory() {
      this.categories_controller.manageCategory();
    },

    filterListCourses() {
      //this.categories_controller.filterListCourses();
    },

    listCourses() {
      //this.categories_controller.listCourses();
    }
  }
}
</script>