import UI from  '../../../core/helpers/UI';
import File from "../../../core/helpers/File";
import ManageService from '../services/ManageService';

class ManageController {
  context;
  manage_service;

  constructor(context) {
    this.context = context;
    this.manage_service = new ManageService();

    if(this.context.$route.params.id) {
      this.context.tuition_id = JSON.parse(this.context.$route.params.id);
      this.context.is_edit = true;
      this.infoTuition(this.tuition_id);
    } else {
      this.context.is_edit = false;
    }
    
    this.listTeachers();
    this.listCourses();
  }

  listCourses() {
    UI.loading('show');
    this.manage_service.listCourses()
    .then((r) => {
      if(r.success == true) {
        this.context.list_courses = [];
        this.context.list_courses = r.data;
        
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  listTeachers() {
    UI.loading('show');
    this.manage_service.listTeachers()
    .then((r) => {
      if(r.success == true) {
        this.context.list_teachers = [];
        this.context.list_teachers = r.data;
        
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  manageTuition() {
    if(this.context.is_edit) {
      this._editTuition();
    } else {
      this._createTuition();
    }
  }

  _createTuition() {
    if(this.context.$refs.form_tuition.validate()) {
      UI.loading('show');
      this.manage_service.createTuition(
        this.context.title,
        this.context.teacher_id,
        this.context.course_id,
        this.context.date_init,
        this.context.date_finish,
        this.context.description
      )
      .then((r) => {
        if(r.success == true) {
          this.context.$refs.form_tuition.reset();
          UI.mensajeOk('', r.msg);
          UI.loading('hide');
        }
      }).catch((e) => {
        UI.loading('hide');
        UI.mensajeBad('', e.msg);
      });
    }
  }

  _editTuition() {
    if(this.context.$refs.form_tuition.validate()) {
      UI.loading('show');
      this.manage_service.editTuition(
        this.context.tuition_id,
        this.context.title,
        this.context.teacher_id,
        this.context.course_id,
        this.context.date_init,
        this.context.date_finish,
        this.context.description
      )
      .then((r) => {
        if(r.success == true) {
          UI.mensajeOk('', r.msg);
          UI.loading('hide');
        }
      }).catch((e) => {
        UI.loading('hide');
        UI.mensajeBad('', e.msg);
      });
    }
  }

  infoTuition(tuition_id) {
    UI.loading('show');
    this.manage_service.infoTuition(tuition_id)
    .then((r) => {
      if(r.success == true) {
        this.context.tuition_id = r.data.tuition_id;
        this.context.title = r.data.title;
        this.context.description = r.data.description;
        this.context.date_init = r.data.date_init;
        this.context.date_finish = r.data.date_finish;

        if(r.data.course) {
          this.context.course_id = r.data.course.course_id;
        }
        
        if(r.data.teacher) {
          this.context.teacher_id = r.data.teacher.teacher_id;
        }
        
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }
}

export default ManageController;