import ListRepository from "../repositories/ListRepository";

class ListService {
  list_repository;

  constructor() {
    this.list_repository = new ListRepository();
  }

  async listStudents() {
    let r = await this.list_repository.listStudents();
    return r;
  }

  async listCourses() {
    let r = await this.list_repository.listCourses();
    return r;
  }

  async filterListStudents(course_id) {
    let r = await this.list_repository.filterListStudents(course_id);
    return r;
  }

  async deleteStudent(student_id) {
    let r = await this.list_repository.deleteStudent(student_id);
    return r;
  }

  async infoStudent(student_id) {
    let r = await this.list_repository.infoStudent(student_id);
    return r;
  }
}

export default ListService;