import Vue from 'vue'
import VueRouter from 'vue-router';
import App from '../src/core/layouts/App.vue';
import Vuetify from 'vuetify';
import VueSweetalert2 from 'vue-sweetalert2';
import 'sweetalert2/dist/sweetalert2.min.css';
import 'vuetify/dist/vuetify.min.css';

import './core/assets/css/bootstrap.min.css'
import './core/assets/css/all.min.css'
import './core/assets/css/materialdesignicons.min.css'
import './core/assets/css/CrCore.css'
import './modules/_core/main.css'

import System from './core/framework/System';
import {MODULES} from './modules/_core/declarations';

window.ENVIROMENT = {};
window.routes = [];
window.menu = [];

require('./core/route/module');
require('./modules/_core/main_top');

MODULES.forEach(module => {
  try{
    require('./modules/'+module+'/module')
  }catch(e){}
});

Vue.config.productionTip = false;
Vue.use(VueRouter);
Vue.use(Vuetify);
Vue.use(VueSweetalert2);

const router = new VueRouter({
  routes: window.routes
});

const SystemObj = new System();

require('./modules/_core/main_bottom');

new Vue({
  el: '#app',
  router,
  vuetify: new Vuetify({
    theme: {
      //dark: true,
      themes: {
        light: {
          primary: '#321fdb',
          secondary: '#ced2d8',
          success: '#2eb85c',
          warning: '#f9b115',
          danger: '#e55353',
          info: '#39f',
          light: '#ebedef',
          dark: '#636f83',
        },
        dark: {
          primary: '#4638c2',
          secondary: '#4c4f54',
          success: '#45a164',
          warning: '#e1a82d',
          danger: '#d16767',
          info: '#4799eb',
          light: '#20202a',
          dark: '#181924',
        },
      },
    },
    breakpoint: {
      thresholds: {
        xs: 320,
        sm: 576,
        md: 768,
        lg: 992,
        xl: 1200
      },
      scrollBarWidth: 24,
    },
  }),
  watch:{
    $route (to, from){
      dispatchEvent(new CustomEvent('changeRoute', {
        detail: {
          to: to,
          from: from
        }
      }));
    }
  },
  mounted(){
    document.title = process.env.VUE_APP_TITLE;
    SystemObj.setTokens();
    SystemObj.generateVisitId();
  },
  created: function () {
  },
  render: h=> h(App)
});