import Site from '../../core/layouts/Principal.vue';
import List from './views/List.vue';
import Manage from './views/Manage.vue';
import DetailStudents from './views/DetailStudents.vue';
import DetailTeachers from './views/DetailTeachers.vue';
import DetailAdmin from './views/DetailAdmin.vue';
import ListModules from './views/ListModules.vue';

window.routes.push({
  path: '/lessons',
  name: 'lessons',
  component: Site,
  children: [
    {
      path: 'list',
      name: 'list',
      component: List,
    },
    {
      path: 'manage/:id?',
      name: 'manage',
      component: Manage,
    },
    {
      path: 'detail-students/:id?',
      name: 'detail-students',
      component: DetailStudents,
    },
    {
      path: 'detail-teachers/:id?',
      name: 'detail-teachers',
      component: DetailTeachers,
    },
    {
      path: 'detail-admin/:id?',
      name: 'detail-admin',
      component: DetailAdmin,
    },
    {
      path: 'list-modules',
      name: 'list-modules',
      component: ListModules,
    },
  ],
});