import Rest from '../../../core/libs/Rest';
import { DETAIL_MOCKUP } from '../mockups/Detail';

class DetailRepository {
  rest;

  constructor() {
    this.rest = new Rest();
  }

  infoInscription(detail_inscription_id) {
    this.rest.setParams('/'+detail_inscription_id);
    this.rest.setEndPoint('api/inscription/show-inscription');
    return this.rest.peticionMockupV2('GET', null, DETAIL_MOCKUP.INFO_INSCRIPTION);
    //return this.rest.get();
  }
}

export default DetailRepository;