import ListRepository from "../repositories/ListRepository";

class ListService {
  list_repository;

  constructor() {
    this.list_repository = new ListRepository();
  }

  async listArticles() {
    let r = await this.list_repository.listArticles();
    return r;
  }

  async deleteArticle(article_id) {
    let r = await this.list_repository.deleteArticle(article_id);
    return r;
  }
}

export default ListService;