import UI from  '../../../core/helpers/UI';
import Helpers from '../../../core/helpers/Helpers';
import ManageService from '../services/ManageService';
import Enviroment from '../../../core/helpers/Enviroment';

class ManageController {
  context;
  manage_service;

  constructor(context) {
    this.context = context;
    this.manage_service = new ManageService();

    if(this.context.$route.params.id) {
      this.context.image_id = JSON.parse(this.context.$route.params.id);
      this.context.is_edit = true;
      this._infoImage();
    }
  }

  manageImage() {
    if(this.context.is_edit) {
      this._editImage();
    } else {
      this._createImage();
    }
  }

  _createImage() {
    if(this.context.$refs.form_image.validate() && this.context.image) {
      UI.loading('show');
      this.manage_service.createImage(this.context.title, this.context.image_link)
      .then((r) => {
        if(r.success == true) {
          this.context.$refs.form_image.reset();
          this.deleteImage();
          this.context.send_url = false;
          window.location = Helpers.urlappmodulo('learning', 'images', 'list');
          UI.mensajeOk('', r.msg);
          UI.loading('hide');
        }
      }).catch((e) => {
        UI.loading('hide');
        UI.mensajeBad('', e.msg);
      });
    } else {
      UI.mensajeBad('', 'Por favor, carga un image, playlist o podcast.');
    }
  }

  _editImage() {
    if(this.context.$refs.form_image.validate() && this.context.image) {
      UI.loading('show');
      this.manage_service.editImage(this.context.image_id, this.context.title, this.context.image_link)
      .then((r) => {
        if(r.success == true) {
          UI.mensajeOk('', r.msg);
          UI.loading('hide');
        }
      }).catch((e) => {
        UI.loading('hide');
        UI.mensajeBad('', e.msg);
      });
    } else {
      UI.mensajeBad('', 'Por favor, carga una imagen, playlist o podcast.');
    }
  }

  _infoImage() {
    UI.loading('show');
    this.manage_service.infoImage(this.context.image_id)
    .then((r) => {
      if(r.success == true) {
        this.context.info_image = r.data;

        this.context.image_id = r.data.mod_image_id;
        this.context.title = r.data.name;
        this.context.image = r.data.image;

        if(this.context.image) {
          this.context.send_url = false;
        } else {
          this.context.send_url = true;
        }

        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  uploadedImage() {
    //rutacuenta_encode_token

    //this.context.image_link = this.context.upload_image.name;
    this.context.image_link = 'a4670c7a42808c9-tk-_sl_Fimages_sl_F2022_sl_F04_sl_F22_sl_F1650641057_om_guia-elearning-1024x484-2.jpg';
    this.context.image = Enviroment.getUrlZfiles()+'a4670c7a42808c9-tk-_sl_Fimages_sl_F2022_sl_F04_sl_F22_sl_F1650641057_om_guia-elearning-1024x484-2.jpg';
  }

  deleteImage() {
    this.context.image = '';
    this.context.image_link = '';
    this.context.upload_image = null;
  }
}

export default ManageController;