import Rest from '../../../core/libs/Rest';
import { MANAGE_MOCKUP } from '../mockups/Manage';

class ManageRepository {
  rest;

  constructor() {
    this.rest = new Rest();
  }

  listTeachers() {
    this.rest.setParams('');
    this.rest.setEndPoint('api/tuition/list-all');
    return this.rest.peticionMockupV2('GET', null, MANAGE_MOCKUP.LIST_TEACHERS);
    //return this.rest.get();
  }

  listCourses() {
    this.rest.setParams('');
    this.rest.setEndPoint('api/categories/list-all');
    return this.rest.peticionMockupV2('GET', null, MANAGE_MOCKUP.LIST_COURSES);
    //return this.rest.get();
  }

  createTuition(title, teacher_id, course_id, date_init, date_finish, description) {
    this.rest.setParams({title:title, teacher_id:teacher_id, course_id:course_id, date_init:date_init, date_finish:date_finish, description:description});
    this.rest.setEndPoint('api/tuitions/store');
    return this.rest.peticionMockupV2('POST', null, MANAGE_MOCKUP.CREATE_TUITION);
    //return this.rest.get();
  }

  editTuition(tuition_id, title, teacher_id, course_id, date_init, date_finish, description) {
    this.rest.setParams({title:title, teacher_id:teacher_id, course_id:course_id, date_init:date_init, date_finish:date_finish, description:description});
    this.rest.setEndPoint('api/tuitions/store');
    return this.rest.peticionMockupV2('PUT', tuition_id, MANAGE_MOCKUP.EDIT_TUITION);
    //return this.rest.post();
  }

  infoTuition(tuition_id) {
    this.rest.setParams('/'+tuition_id);
    this.rest.setEndPoint('api/tuitions/detail');
    return this.rest.peticionMockupV2('GET', null, MANAGE_MOCKUP.INFO_TUITION);
    //return this.rest.get();
  }
}

export default ManageRepository;