import ManageRepository from "../repositories/ManageRepository";

class ManageService {
  manage_repository;

  constructor() {
    this.manage_repository = new ManageRepository();
  }

  async createVideo(title, video_url) {
    let r = await this.manage_repository.createVideo(title, video_url);
    return r;
  }

  async editVideo(video_id, title, video_url) {
    let r = await this.manage_repository.editVideo(video_id, title, video_url);
    return r;
  }

  async infoVideo(video_id) {
    let r = await this.manage_repository.infoVideo(video_id);
    return r;
  }
}

export default ManageService;