import Site from '../../core/layouts/Principal.vue';
import List from './views/List.vue';
import Detail from './views/Detail.vue';

window.routes.push({
  path: '/teachers',
  name: 'teachers',
  component: Site,
  children: [
    {
      path: 'list',
      name: 'list',
      component: List,
    },
    {
      path: 'detail/:id?',
      name: 'detail',
      component: Detail,
    },
  ],
});