import ManageRepository from "../repositories/ManageRepository";

class ManageService {
  manage_repository;

  constructor() {
    this.manage_repository = new ManageRepository();
  }

  async createTitle(title) {
    let r = await this.manage_repository.createTitle(title);
    return r;
  }

  async editTitle(title_id, title) {
    let r = await this.manage_repository.editTitle(title_id, title);
    return r;
  }

  async infoTitle(title_id) {
    let r = await this.manage_repository.infoTitle(title_id);
    return r;
  }
}

export default ManageService;