import UI from  '../../../core/helpers/UI';
import ManageService from '../services/ManageService';
import Enviroment from '../../../core/helpers/Enviroment';

class ManageController {
  context;
  manage_service;

  constructor(context) {
    this.context = context;
    this.manage_service = new ManageService();

    if(this.context.$route.params.id) {
      this.context.document_id = JSON.parse(this.context.$route.params.id);
      this.context.is_edit = true;
      this._infoDocument();
    }
  }

  manageDocument() {
    if(this.context.is_edit) {
      this._editDocument();
    } else {
      this._createDocument();
    }
  }

  _createDocument() {
    if(this.context.$refs.form_document.validate() && this.context.document) {
      UI.loading('show');
      this.manage_service.createDocument(
        this.context.title,
        this.context.document_link
      ).then((r) => {
        if(r.success == true) {
          this.context.list_documents = [];
          this.context.$refs.form_document.reset();
          this.deleteDocument();
          UI.mensajeOk('', r.msg);
          window.location = '#/documents/list';
          UI.loading('hide');
        }
      }).catch((e) => {
        UI.loading('hide');
        UI.mensajeBad('', e.msg);
      });
    }
  }

  _editDocument() {
    if(this.context.$refs.form_document.validate() && this.context.document) {
      UI.loading('show');
      this.manage_service.editDocument(
        this.context.document_id,
        this.context.title,
        this.context.document_link
      ).then((r) => {
        if(r.success == true) {
          UI.mensajeOk('', r.msg);
          UI.loading('hide');
          window.location = '#/documents/list';
        }
      }).catch((e) => {
        UI.loading('hide');
        UI.mensajeBad('', e.msg);
      });
    }
  }

  _infoDocument() {
    UI.loading('show');
    this.manage_service.infoDocument(this.context.document_id)
    .then((r) => {
      if(r.success == true) {
        this.context.info_document = r.data;

        this.context.document_id = r.data.mod_doc_id;
        this.context.title = r.data.name;
        this.context.document_link = r.data.document;
        this.context.document = Enviroment.getUrlZfiles()+'6e20a6e079a484-tk-_sl_Fdocuments_sl_F2022_sl_F04_sl_F22_sl_F1650643304_om_SEO%2BT%25C3%25A9cnico.pdf';
        this.context.name_document = this.context.document_link;

        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  uploadFile() {
    //rutacuenta_encode_token

    this.context.name_document = this.context.upload_document.name;
    this.context.document_link = '6e20a6e079a484-tk-_sl_Fdocuments_sl_F2022_sl_F04_sl_F22_sl_F1650643304_om_SEO%2BT%25C3%25A9cnico.pdf';
    this.context.document = Enviroment.getUrlZfiles()+'6e20a6e079a484-tk-_sl_Fdocuments_sl_F2022_sl_F04_sl_F22_sl_F1650643304_om_SEO%2BT%25C3%25A9cnico.pdf';

    // file.forEach((v, i) => {
    //   this.context.list_documents.push({
    //     name: v.name,
    //     type: v.type
    //   });
    // });
  }

  deleteUploaded(position) {
    this.context.list_documents.splice(position, 1);
  }

  deleteDocument() {
    this.context.document_link = '';
    this.context.document = '';
    this.context.name_document = '';
    this.context.upload_document = null;
  }
}

export default ManageController;