import Rest from '../../../core/libs/Rest';

class ListRepository {
  rest;

  constructor() {
    this.rest = new Rest();
  }

  listDocuments() {
    this.rest.setParams('');
    this.rest.setEndPoint('api/mod-doc/list-all');
    return this.rest.get();
  }

  deleteDocument(document_id) {
    this.rest.setParams('');
    this.rest.setEndPoint('api/mod-doc/delete-doc');
    return this.rest.delete(document_id);
  }
}

export default ListRepository;