import ManageRepository from "../repositories/ManageRepository";

class ManageService {
  manage_repository;

  constructor() {
    this.manage_repository = new ManageRepository();
  }

  async listCategories() {
    let r = await this.manage_repository.listCategories();
    return r;
  }

  async listLessons() {
    let r = await this.manage_repository.listLessons();
    return r;
  }

  async infoCourse(course_id) {
    let r = await this.manage_repository.infoCourse(course_id);
    return r;
  }

  async createCategory(name_category) {
    let r = await this.manage_repository.createCategory(name_category);
    return r;
  }

  async createCourse(title, category_id, image_str, description, lessons) {
    let r = await this.manage_repository.createCourse(title, category_id, image_str, description, lessons);
    return r;
  }

  async editCourse(course_id, title, category_id, image_str, description, lessons) {
    let r = await this.manage_repository.editCourse(course_id, title, category_id, image_str, description, lessons);
    return r;
  }
}

export default ManageService;