import DetailTeachersRepository from "../repositories/DetailTeachersRepository";

class DetailTeachersService {
  detail_teachers_repository;

  constructor() {
    this.detail_teachers_repository = new DetailTeachersRepository();
  }

  async infoLesson(lesson_id) {
    let r = await this.detail_teachers_repository.infoLesson(lesson_id);
    return r;
  }
}

export default DetailTeachersService;