import Rest from '../../../core/libs/Rest';

class ManageRepository {
  rest;

  constructor() {
    this.rest = new Rest();
  }

  createArticle(title, article) {
    this.rest.setParams({name:title, content:article});
    this.rest.setEndPoint('api/mod-article/store-article');
    return this.rest.post();
  }

  editArticle(mod_article_id, title, article) {
    this.rest.setParams({name:title, content:article});
    this.rest.setEndPoint('api/mod-article/update-article');
    return this.rest.put(mod_article_id);
  }

  infoArticle(article_id) {
    this.rest.setParams('/'+article_id);
    this.rest.setEndPoint('api/mod-article/show-article');
    return this.rest.get();
  }
}

export default ManageRepository;