import ListCoursesRepository from "../repositories/ListCoursesRepository";

class ListCoursesService {
  list_courses_repository;

  constructor() {
    this.list_courses_repository = new ListCoursesRepository();
  }

  async listCourses() {
    let r = await this.list_courses_repository.listCourses();
    return r;
  }
}

export default ListCoursesService;