<template>
  <div>
    <section class="main">
      <div class="container-fluid main-box-content contentModulo py-2">
        <div class="breadcrumb px-3">
          <ul>
            <li>Inicio</li>
            <li>Módulos</li>
            <li>{{is_edit ? 'Editar' : 'Crear'}} audio</li>
          </ul>
        </div>

        <div class="title">
          <h1 class="my-0">{{is_edit ? 'Editar' : 'Crear'}} audio</h1>
        </div>

        <v-card class="mt-3 mt-md-4">
          <v-card-text class="px-lg-4">
            <v-form class="w-100" ref="form_audio" method="post" v-model="form_valid" lazy-validation @submit.prevent="manageAudio()">
              <v-text-field label="Titulo" v-model="title" :rules="[rules.requerido]" filled></v-text-field>

              <div class="row">
                <div class="col-sm-12 mb-3 mb-md-0">
                  <div class="row align-items-center" v-if="audio && !send_url">
                    <div class="col-10 col-sm-11">
                      <audio controls class="w-100">
                        <source :src="audio" type="audio/mpeg">
                      </audio>
                    </div>
                    <div class="col-2 col-sm-1">
                      <v-btn icon class="d-table ml-auto" color="danger" @click="deleteAudio()">
                        <v-icon>mdi-close</v-icon>
                      </v-btn>
                    </div>
                  </div>

                  <v-file-input filled label="Archivo de audio" v-if="!send_url && !audio" :prepend-icon="null" v-model="upload_audio" @change="uploadedAudio"></v-file-input>
                </div>
              </div>

              <v-btn class="d-table mx-auto mt-2" rounded width="120" color="success" type="submit">{{is_edit ? 'Actualizar' : 'Guardar'}}</v-btn>
            </v-form>
          </v-card-text>
        </v-card>
      </div>
    </section>
  </div>
</template>

<script>
import Helpers from '../../../core/helpers/Helpers';
import ManageController from '../controllers/ManageController';

export default {
  data() {
    return {
      Helpers,
      audio_id: null,
      is_edit: false,
      title: '',
      audio: '',
      send_url: false,

      form_valid: true,

      rules: {
        requerido: value => !!value || 'Campo obligatorio',
        url: value => this.validatedUrl(value) || 'URL no válida',
        email: value => !value || /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(value) || 'Email incorrecto',
      },

      upload_audio: null,

      manage_controller: null
    };
  },

  mounted() {
    this.manage_controller = new ManageController(this);
  },

  methods: {
    manageAudio() {
      this.manage_controller.manageAudio();
    },

    uploadedAudio() {
      this.manage_controller.uploadedAudio();
    },

    deleteAudio() {
      this.manage_controller.deleteAudio();
    },

    changeSend() {
      this.audio = '';
      this.upload_audio = null;
      this.send_url = !this.send_url;
    },

    validatedUrl(str) {
      let url;

      try {
        url = new URL(str);
      } catch (_) {
        return false;
      }

      return url.protocol === "http:" || url.protocol === "https:";
    },
  }
}
</script>