<template>
  <div>
    <section class="main">
      <div class="container-fluid main-box-content contentModulo py-2">
        <div class="breadcrumb px-3">
          <ul>
            <li>Inicio</li>
            <li>Lecciones</li>
            <li>Módulos</li>
            <li>Listado de documentos</li>
          </ul>
        </div>

        <div class="title">
          <h1 class="my-0">Listado de documentos</h1>
        </div>

        <v-card class="mt-3 mt-md-4">
          <v-card-text>
            <div class="row pt-3">
              <div class="col-md-9 col-lg-10">
                <v-text-field
                  filled
                  v-model="search_list_documents"
                  append-icon="mdi-magnify"
                  label="Buscar"
                  hide-details
                  clearable
                  class="my-0"
                ></v-text-field>
              </div>
              <div class="col-md-3 col-lg-2">
                <v-btn color="primary" block height="56" width="150" href="#/documents/manage">Crear documento</v-btn>
              </div>
            </div>

            <div class="table-responsive mt-3 mt-md-4">
              <v-data-table
                class="my-0"
                :headers="header_documents"
                :items="list_documents"
                :items-per-page="10"
                :search="search_list_documents"
              >
                <template v-slot:item.state="{ item }">
                  <v-chip color="success" v-if="item.state == 1">Asignada</v-chip>
                  <v-chip color="warning" v-if="item.state == 2">Sin asignar</v-chip>
                </template>

                <template v-slot:item.document="{ item }">
                  <v-tooltip bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn icon color="success" v-bind="attrs" v-on="on" download target="_blank" :href="Enviroment.getUrlZfiles()+item.document">
                        <v-icon>mdi-download</v-icon>
                      </v-btn>
                    </template>
                    <span>Descargar documento</span>
                  </v-tooltip>
                </template>

                <template v-slot:item.actions="{ item }">
                  <!-- Editar -->
                  <v-tooltip bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn icon color="primary" v-bind="attrs" v-on="on" :href="'#/documents/manage/'+item.mod_doc_id">
                        <v-icon>mdi-square-edit-outline</v-icon>
                      </v-btn>
                    </template>
                    <span>Editar</span>
                  </v-tooltip>
                  
                  <!-- Eliminar -->
                  <v-tooltip bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn icon color="danger" v-bind="attrs" v-on="on" @click="deleteDocument(item.mod_doc_id)">
                        <v-icon>mdi-trash-can-outline</v-icon>
                      </v-btn>
                    </template>
                    <span>Eliminar</span>
                  </v-tooltip>
                </template>
              </v-data-table>
            </div>
          </v-card-text>
        </v-card>
      </div>
    </section>
  </div>
</template>

<script>
import Helpers from '../../../core/helpers/Helpers';
import Enviroment from '../../../core/helpers/Enviroment';
import ListController from '../controllers/ListController';

export default {
  data() {
    return {
      Helpers,
      Enviroment,
      header_documents: [],
      list_documents: [],
      search_list_documents: '',

      list_controller: null
    };
  },

  mounted() {
    this.list_controller = new ListController(this);
  },

  methods: {
    deleteDocument(document_id) {
      this.list_controller.deleteDocument(document_id);
    },
  }
}
</script>