import Site from '../../core/layouts/Principal.vue';
import ListAdmin from './views/ListAdmin.vue';
import ListStudents from './views/ListStudents.vue';
import ListTeachers from './views/ListTeachers.vue';
import Manage from './views/Manage.vue';
import DetailStudents from './views/DetailStudents.vue';
import DetailTeachers from './views/DetailTeachers.vue';
import DetailAdmin from './views/DetailAdmin.vue';
import Categories from './views/Categories.vue';

window.routes.push({
  path: '/courses',
  name: 'courses',
  component: Site,
  children: [
    {
      path: 'list-admin',
      name: 'list-admin',
      component: ListAdmin,
    },
    {
      path: 'list-students',
      name: 'list-students',
      component: ListStudents,
    },
    {
      path: 'list-teachers',
      name: 'list-teachers',
      component: ListTeachers,
    },
    {
      path: 'manage/:id?',
      name: 'manage',
      component: Manage,
    },
    {
      path: 'detail-students/:id?',
      name: 'detail-students',
      component: DetailStudents,
    },
    {
      path: 'detail-teachers/:id?',
      name: 'detail-teachers',
      component: DetailTeachers,
    },
    {
      path: 'detail-admin/:id?',
      name: 'detail-admin',
      component: DetailAdmin,
    },
    {
      path: 'categories',
      name: 'categories',
      component: Categories,
    },
  ],
});