<template>
  <div>
    <section class="main">
      <div class="container-fluid main-box-content contentModulo py-2">
        <div class="breadcrumb px-3">
          <ul>
            <li>Inicio</li>
            <li>Inscripciones</li>
            <li>Mis inscripciones</li>
          </ul>
        </div>

        <div class="title">
          <h1 class="my-0">Mis inscripciones</h1>
        </div>

        <div class="row align-items-end pt-4">
          <div class="col-md-8 col-lg-9 col-xl-10 box-set-date">
            <div class="row align-items-end mx-n2">
              <div class="col-lg-5 px-2">
                <p class="mt-0 mb-1 text-left text-standar fw-600 fz-16">Palabra clave</p>

                <v-text-field v-model="search" outlined dense hide-details></v-text-field>
              </div>
              <div class="col-lg-5 px-2">
                <p class="mt-0 mb-1 text-left text-standar fw-600 fz-16">Categorías</p>

                <v-select outlined v-model="category_id" :items="list_categories" item-value="course_cat_id" item-text="name" dense hide-details></v-select>
              </div>
              <div class="col-lg-2 px-2">
                <v-btn rounded @click="filterMyInscriptions()" color="success" class="d-table ml-auto px-md-4">Buscar</v-btn>
              </div>
            </div>
          </div>
          <div class="col-md-4 col-lg-3 col-xl-2 mt-3 mt-md-0">
            <div class="d-flex align-items-center justify-content-between">
              <v-btn color="info" @click="listMyInscriptions()" v-if="is_search" rounded class="d-table ml-auto">
                Ver todos
              </v-btn>
            </div>
          </div>
        </div>

        <div class="row py-3 py-md-4">
          <template v-for="(ins, i) in list_inscriptions">
            <div class="col-sm-6 col-md-4 mt-4" :key="i">
              <v-card elevation="2">
                <v-img :src="ins.image" v-if="ins.image" height="200px"></v-img>

                <div class="row mx-0 py-3 align-items-start">
                  <div class="col-3">
                    <v-progress-circular size="60" rotate="-90" width="5" :value="ins.progress" color="success" rounded>
                      {{ins.progress}}%
                    </v-progress-circular>
                  </div>
                  <div class="col-9 pl-0">
                    <v-card-title class="p-0">
                      {{ins.name}}
                    </v-card-title>

                    <v-card-subtitle class="p-0 mt-1">
                      {{ins.category}}
                    </v-card-subtitle>
                  </div>
                </div>

                <v-card-actions>
                  <v-btn text color="primary" :href="'#/courses/detail-students/'+ins.course_id">Ver curso</v-btn>
                  <v-spacer></v-spacer>
                  <v-btn color="danger" class="fw-500" text @click="deleteSubscription(ins.course_id, i)">
                    Cancelar inscripción
                  </v-btn>
                </v-card-actions>
              </v-card>
            </div>
          </template>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import Helpers from '../../../core/helpers/Helpers';
import MyInscriptionsController from '../controllers/MyInscriptionsController';

export default {
  data() {
    return {
      Helpers,
      list_categories: [],
      list_inscriptions: [],
      is_search: false,
      search: '',
      category_id: null,

      my_inscriptions_controller: null
    };
  },

  mounted() {
    this.my_inscriptions_controller = new MyInscriptionsController(this);
  },

  methods: {
    listMyInscriptions() {
      this.my_inscriptions_controller.listMyInscriptions();
    },

    deleteSubscription(course_id, position) {
      this.my_inscriptions_controller.deleteSubscription(course_id, position);
    },

    filterMyInscriptions() {
      this.my_inscriptions_controller.filterMyInscriptions();
    }
  }
}
</script>