<template>
  <div>
    <section class="main">
      <div class="container-fluid main-box-content contentModulo py-2">
        <div class="breadcrumb px-3">
          <ul>
            <li>Inicio</li>
            <li>Matrículas</li>
            <li>Listado de matrículas</li>
          </ul>
        </div>

        <div class="title">
          <h1 class="my-0">Listado de matrículas</h1>
        </div>

        <v-card class="mt-3 mt-md-4">
          <v-card-text>
            <div class="row align-items-end pt-3">
              <div class="col-md-8 col-lg-9 col-xl-10 box-set-date">
                <div class="row align-items-end mx-n2">
                  <div class="col-lg-3 px-2">
                    <p class="mt-0 mb-1 text-left text-standar fw-600 fz-16">Fecha inicial</p>

                    <v-menu v-model="menu_init" :close-on-content-click="false" :nudge-right="40" transition="scale-transition" offset-y min-width="auto">
                      <template v-slot:activator="{ on, attrs }">
                        <v-text-field v-model="date_init" readonly v-bind="attrs" outlined dense hide-details v-on="on" append-icon="mdi-calendar"></v-text-field>
                      </template>
                      <v-date-picker v-model="date_init" @input="menu_init = false"></v-date-picker>
                    </v-menu>
                  </div>
                  <div class="col-lg-3 px-2">
                    <p class="mt-0 mb-1 text-left text-standar fw-600 fz-16">Fecha final</p>

                    <v-menu v-model="menu_finish" :close-on-content-click="false" :nudge-right="40" transition="scale-transition" offset-y min-width="auto">
                      <template v-slot:activator="{ on, attrs }">
                        <v-text-field v-model="date_finish" readonly v-bind="attrs" outlined dense hide-details v-on="on" append-icon="mdi-calendar"></v-text-field>
                      </template>
                      <v-date-picker v-model="date_finish" @input="menu_finish = false"></v-date-picker>
                    </v-menu>
                  </div>
                  <div class="col-lg-3 px-2">
                    <p class="mt-0 mb-1 text-left text-standar fw-600 fz-16">Categoría</p>

                    <v-select v-model="category_id" :items="list_categories" outlined dense hide-details=""></v-select>
                  </div>
                  <div class="col-lg-2 px-2">
                    <v-btn rounded @click="filterListTuitions()" color="success" class="d-table ml-auto px-md-4">Buscar</v-btn>
                  </div>
                </div>
              </div>
              <div class="col-md-4 col-lg-3 col-xl-2 mt-3 mt-md-0">
                <div class="d-flex align-items-center justify-content-between">
                  <v-btn color="info" v-if="is_search" @click="listTuitions()" rounded class="d-table ml-auto">
                    Ver todos
                  </v-btn>
                </div>
              </div>
            </div>

            <div class="table-responsive mt-3">
              <v-data-table
                class="mt-3"
                :headers="headers"
                :items="list_tuitions"
                :items-per-page="10"
                :search="search_list_tuitions"
              >
                <template v-slot:top>
                  <v-text-field
                    filled
                    v-model="search_list_tuitions"
                    append-icon="mdi-magnify"
                    label="Buscar"
                    hide-details
                    clearable
                    class="mb-4"
                  ></v-text-field>
                </template>

                <template v-slot:item.state="{ item }">
                  <!-- Estado -->
                  <v-chip color="success" v-if="item.state == 1">Habilitada</v-chip>
                </template>

                <template v-slot:item.actions="{ item }">
                  <!-- Editar -->
                  <v-tooltip bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn icon color="primary" :href="'#/tuitions/manage/'+item.tuition_id" v-bind="attrs" v-on="on">
                        <v-icon>mdi-square-edit-outline</v-icon>
                      </v-btn>
                    </template>
                    <span>Editar</span>
                  </v-tooltip>

                  <!-- Detalle -->
                  <v-dialog v-model="dialog_detail[item.tuition_id]" width="500">
                    <template v-slot:activator="{ on, attrs }">
                      <v-tooltip bottom v-bind="attrs" v-on="on">
                        <template v-slot:activator="{ on, attrs }">
                          <v-btn icon color="success" @click="infoTuition(item.tuition_id)" v-bind="attrs" v-on="on">
                            <v-icon>mdi-eye-outline</v-icon>
                          </v-btn>
                        </template>
                        <span>Ver detalle</span>
                      </v-tooltip>
                    </template>

                    <v-card>
                      <div class="title d-flex align-items-center justify-content-between flex-wrap p-4">
                        <h1 class="my-0">Detalle de la matrícula</h1>
                        <v-btn icon color="danger" @click="closeDetail(item.tuition_id)">
                          <v-icon>mdi-close</v-icon>
                        </v-btn>
                      </div>

                      <v-card-text>
                        <div class="row">
                          <div class="col-sm-6 mb-4" v-if="info_tuition.title">
                            <h4 class="mt-0 mb-1 text-left text-standar fw-600 fz-16">Título:</h4>
                            <p class="my-0 fw-400 fz-16 text-black">{{info_tuition.title}}</p>
                          </div>

                          <div class="col-sm-6 mb-4" v-if="info_tuition.teacher">
                            <h4 class="mt-0 mb-1 text-left text-standar fw-600 fz-16">Profesor:</h4>
                            <p class="my-0 fw-400 fz-16 text-black">{{info_tuition.teacher}}</p>
                          </div>

                          <div class="col-sm-6 mb-4" v-if="info_tuition.course">
                            <h4 class="mt-0 mb-1 text-left text-standar fw-600 fz-16">Curso:</h4>
                            <p class="my-0 fw-400 fz-16 text-black">{{info_tuition.course.title}}</p>
                          </div>

                          <div class="col-sm-6 mb-4" v-if="info_tuition.date_init">
                            <h4 class="mt-0 mb-1 text-left text-standar fw-600 fz-16">Fecha inicial:</h4>
                            <p class="my-0 fw-400 fz-16 text-black">{{info_tuition.date_init}}</p>
                          </div>

                          <div class="col-sm-6 mb-4" v-if="info_tuition.date_finish">
                            <h4 class="mt-0 mb-1 text-left text-standar fw-600 fz-16">Fecha final:</h4>
                            <p class="my-0 fw-400 fz-16 text-black">{{info_tuition.date_finish}}</p>
                          </div>

                          <div class="col-sm-6 mb-4" v-if="info_tuition.description">
                            <h4 class="mt-0 mb-1 text-left text-standar fw-600 fz-16">Descripción:</h4>
                            <p class="my-0 fw-400 fz-16 text-black">{{info_tuition.description}}</p>
                          </div>
                        </div>
                      </v-card-text>
                    </v-card>
                  </v-dialog>
                  
                  <!-- Eliminar -->
                  <v-tooltip bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn icon color="danger" v-bind="attrs" v-on="on" @click="deleteTuition(list_tuitions.map(function(x) {return x.tuition_id; }).indexOf(item.tuition_id), item.tuition_id)">
                        <v-icon>mdi-trash-can-outline</v-icon>
                      </v-btn>
                    </template>
                    <span>Eliminar</span>
                  </v-tooltip>
                </template>
              </v-data-table>
            </div>
          </v-card-text>
        </v-card>
      </div>
    </section>
  </div>
</template>

<script>
import Helpers from '../../../core/helpers/Helpers';
import ListController from '../controllers/ListController';

export default {
  data() {
    return {
      Helpers,
      search_list_tuitions: '',
      headers: [],
      list_tuitions: [],
      dialog_detail: {},
      info_tuition: {},
      is_search: false,
      list_categories: [],

      date_init: null,
      date_finish: null,
      menu_init: false,
      menu_finish: false,
      category_id: null,

      list_controller: null
    };
  },

  mounted() {
    this.list_controller = new ListController(this);
  },

  methods: {
    deleteTuition(position, tuition_id) {
      this.list_controller.deleteTuition(position, tuition_id);
    },

    filterListTuitions() {
      this.list_controller.filterListTuitions();
    },

    listTuitions() {
      this.list_controller.listTuitions();
    },

    infoTuition(tuition_id) {
      this.list_controller.infoTuition(tuition_id);
    },

    closeDetail(tuition_id) {
      this.list_controller.closeDetail(tuition_id);
    }
  }
}
</script>