import Site from '../../core/layouts/Principal.vue';
import List from './views/List.vue';
import Detail from './views/Detail.vue';
import MyInscriptions from './views/MyInscriptions.vue';

window.routes.push({
  path: '/inscriptions',
  name: 'inscriptions',
  component: Site,
  children: [
    {
      path: 'list',
      name: 'list',
      component: List,
    },
    {
      path: 'detail/:id?',
      name: 'detail',
      component: Detail,
    },
    {
      path: 'my-inscriptions',
      name: 'my-inscriptions',
      component: MyInscriptions,
    },
  ],
});