import Site from '../../core/layouts/Principal.vue';
import Student from './views/Student.vue';
import Teacher from './views/Teacher.vue';

window.routes.push({
  path: '/profile',
  name: 'profile',
  component: Site,
  children: [
    {
      path: 'student',
      name: 'student',
      component: Student,
    },
    {
      path: 'teacher',
      name: 'teacher',
      component: Teacher,
    }
  ],
});