import UI from  '../../../core/helpers/UI';
import File from "../../../core/helpers/File";
import ListQuestionsService from '../services/ListQuestionsService';

class ListQuestionsController {
  context;
  list_questions_service;

  constructor(context) {
    this.context = context;
    this.list_questions_service = new ListQuestionsService();

    this.context.header_question = [
      { text: 'Título', value: 'title' },
      { text: 'Estado', value: 'status' },
      { text: 'Lecciones', value: 'lessons' },
      { text: '', value: 'actions', sortable: false },
    ];
    
    this.listQuestions();
  }

  listQuestions() {
    UI.loading('show');
    this.list_questions_service.listQuestions()
    .then((r) => {
      if(r.success == true) {
        this.context.list_questions = [];
        this.context.list_questions = r.data;
        
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  deleteQuestion(position, question_id) {
    UI.loading('show');
    this.list_questions_service.deleteQuestion(question_id)
    .then((r) => {
      if(r.success == true) {
        this.context.list_questions.splice(position, 1);
        UI.mensajeOk('', r.msg);
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }
}

export default ListQuestionsController;