import Rest from '../../../core/libs/Rest';
import { LISTADO_MOCKUP } from '../mockups/List';

class ListRepository {
  rest;

  constructor() {
    this.rest = new Rest();
  }

  listLessons() {
    this.rest.setParams('');
    this.rest.setEndPoint('api/lesson/list-all');
    return this.rest.peticionMockupV2('GET', null, LISTADO_MOCKUP.LIST_LESSONS);
    //return this.rest.get();
  }

  listModules() {
    this.rest.setParams('');
    this.rest.setEndPoint('api/module/list-all');
    return this.rest.peticionMockupV2('GET', null, LISTADO_MOCKUP.LIST_MODULES);
    //return this.rest.get();
  }

  deleteLesson(lesson_id) {
    this.rest.setParams('');
    this.rest.setEndPoint('api/lessons/delete');
    return this.rest.peticionMockupV2('DELETE', lesson_id, LISTADO_MOCKUP.DELETE_LESSON);
    //return this.rest.get();
  }

  filterListLessons(module_id) {
    this.rest.setParams({module_id:module_id});
    this.rest.setEndPoint('api/lesson/list-filtered');
    return this.rest.peticionMockupV2('POST', null, LISTADO_MOCKUP.LIST_LESSONS_BY_FILTER);
    //return this.rest.post();
  }
}

export default ListRepository;