import UI from  '../../../core/helpers/UI';
import File from "../../../core/helpers/File";
import MyInscriptionsService from '../services/MyInscriptionsService';

class MyInscriptionsController {
  context;
  my_inscriptions_service;

  constructor(context) {
    this.context = context;
    this.my_inscriptions_service = new MyInscriptionsService();
    
    this.listCategories();
    this.listMyInscriptions();
  }

  listCategories() {
    UI.loading('show');
    this.my_inscriptions_service.listCategories()
    .then((r) => {
      if(r.success == true) {
        this.context.list_categories = [];
        this.context.list_categories = r.data;
        
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  listMyInscriptions() {
    UI.loading('show');
    this.my_inscriptions_service.listMyInscriptions()
    .then((r) => {
      if(r.success == true) {
        this.context.is_search = false;
        this.context.search = '';
        this.context.category_id = null;
        this.context.list_inscriptions = [];
        this.context.list_inscriptions = r.data;
        
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  deleteSubscription(course_id, position) {
    UI.loading('show');
    this.my_inscriptions_service.deleteSubscription(course_id)
    .then((r) => {
      if(r.success == true) {
        this.context.list_inscriptions.splice(position, 1);
        UI.mensajeOk('', r.msg);

        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  filterMyInscriptions() {
    if(this.context.search || this.context.category_id) {
      UI.loading('show');
      this.my_inscriptions_service.filterMyInscriptions(this.context.search, this.context.category_id)
      .then((r) => {
        if(r.success == true) {
          this.context.is_search = true;
          this.context.list_inscriptions = [];
          this.context.list_inscriptions = r.data;
          
          UI.loading('hide');
        }
      }).catch((e) => {
        UI.loading('hide');
        UI.mensajeBad('', e.msg);
      });
    } else {
      UI.mensajeBad('', 'Completa los campos para realizar la búsqueda');
    }
  }
}

export default MyInscriptionsController;