import ListRepository from "../repositories/ListRepository";

class ListService {
  list_repository;

  constructor() {
    this.list_repository = new ListRepository();
  }

  async listTeachers() {
    let r = await this.list_repository.listTeachers();
    return r;
  }

  async listCourses() {
    let r = await this.list_repository.listCourses();
    return r;
  }

  async filterListTeachers(course_id) {
    let r = await this.list_repository.filterListTeachers(course_id);
    return r;
  }

  async deleteTeacher(teacher_id) {
    let r = await this.list_repository.deleteTeacher(teacher_id);
    return r;
  }

  async infoTeacher(teacher_id) {
    let r = await this.list_repository.infoTeacher(teacher_id);
    return r;
  }
}

export default ListService;