import Rest from '../../../core/libs/Rest';
import { MY_INSCRIPTIONS_MOCKUP } from '../mockups/MyInscriptions';

class MyInscriptionsRepository {
  rest;

  constructor() {
    this.rest = new Rest();
  }

  listCategories() {
    this.rest.setParams('');
    this.rest.setEndPoint('api/category/list-all');
    return this.rest.peticionMockupV2('GET', null, MY_INSCRIPTIONS_MOCKUP.LIST_CATEGORIES);
    //return this.rest.get();
  }

  listMyInscriptions() {
    this.rest.setParams('');
    this.rest.setEndPoint('api/inscriptions/my-inscriptions');
    return this.rest.peticionMockupV2('GET', null, MY_INSCRIPTIONS_MOCKUP.LIST_INSCRIPTIONS);
    //return this.rest.get();
  }

  deleteSubscription(course_id) {
    this.rest.setParams('');
    this.rest.setEndPoint('api/inscriptions/delete-inscription');
    return this.rest.peticionMockupV2('DELETE', course_id, MY_INSCRIPTIONS_MOCKUP.DELETE_INSCRIPTION);
    //return this.rest.get();
  }

  filterMyInscriptions(search, category_id) {
    this.rest.setParams({search:search, category_id:category_id});
    this.rest.setEndPoint('api/inscriptions/filter-my-inscriptions');
    return this.rest.peticionMockupV2('POST', null, MY_INSCRIPTIONS_MOCKUP.FILTER_LIST_INSCRIPTIONS);
    //return this.rest.get();
  }
}

export default MyInscriptionsRepository;