import ManageRepository from "../repositories/ManageRepository";

class ManageService {
  manage_repository;

  constructor() {
    this.manage_repository = new ManageRepository();
  }

  async createAudio(title, audio) {
    let r = await this.manage_repository.createAudio(title, audio);
    return r;
  }

  async editAudio(audio_id, title, audio) {
    let r = await this.manage_repository.editAudio(audio_id, title, audio);
    return r;
  }

  async infoAudio(audio_id) {
    let r = await this.manage_repository.infoAudio(audio_id);
    return r;
  }
}

export default ManageService;