import Rest from '../../../core/libs/Rest';
import { DETAIL_MOCKUP } from '../mockups/Detail';

class DetailTeachersRepository {
  rest;

  constructor() {
    this.rest = new Rest();
  }

  infoCourse(course_id) {
    this.rest.setParams('/'+course_id);
    this.rest.setEndPoint('api/course/show-course');
    return this.rest.peticionMockupV2('GET', null, DETAIL_MOCKUP.INFO_COURSE);
    //return this.rest.get();
  }
}

export default DetailTeachersRepository;