import ListStudentsNewsRepository from "../repositories/ListStudentsNewsRepository";

class ListStudentsNewsService {
  list_students_news_repository;

  constructor() {
    this.list_students_news_repository = new ListStudentsNewsRepository();
  }

  async listStudents() {
    let r = await this.list_students_news_repository.listStudents();
    return r;
  }
}

export default ListStudentsNewsService;