import UI from  '../../../core/helpers/UI';
import File from "../../../core/helpers/File";
import DetailService from '../services/DetailService';

class DetailController {
  context;
  detail_service;

  constructor(context) {
    this.context = context;
    this.detail_service = new DetailService();

    if(this.context.$route.params.id) {
      this.context.detail_inscription_id = JSON.parse(this.context.$route.params.id);

      if(this.context.detail_inscription_id) {
        this.infoInscription();
      }
    }
  }

  infoInscription() {
    UI.loading('show');
    this.detail_service.infoInscription(this.context.detail_inscription_id)
    .then((r) => {
      if(r.success == true) {
        this.context.detail_inscription = {};
        this.context.detail_inscription = r.data;
        this.context.detail_inscription_id = r.data.inscription_id;
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  backHistory() {
    this.context.$router.go(-1);
  }
}

export default DetailController;