import UI from  '../../../core/helpers/UI';
import File from "../../../core/helpers/File";
import ManageService from '../services/ManageService';

class ManageController {
  context;
  manage_service;
  current;
  after;
  before;

  constructor(context) {
    this.context = context;
    this.manage_service = new ManageService();

    if(this.context.$route.params.id) {
      this.context.quiz_id = JSON.parse(this.context.$route.params.id);

      if(this.context.quiz_id) {
        this.infoQuiz();
        this.context.is_edit = true;
      } else {
        this.context.is_edit = false;
      }
    }

    this.listQuestions();
  }

  listQuestions() {
    UI.loading('show');
    this.manage_service.listQuestions()
    .then((r) => {
      if(r.success == true) {
        this.context.list_questions = [];
        this.context.list_questions = r.data;
        
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  infoQuiz() {
    UI.loading('show');
    this.manage_service.infoQuiz(this.context.quiz_id)
    .then((r) => {
      if(r.success == true) {
        let info_lesson = r.data;

        this.context.quiz_id = info_lesson.quiz_id;
        this.context.title = info_lesson.title;
        this.context.description = info_lesson.description;
        this.context.answer_quiz = info_lesson.answer_quiz;
        this.context.start_date = info_lesson.start_date;
        this.context.final_date = info_lesson.final_date;
        
        if(info_lesson.quizes && info_lesson.quizes.length > 0) {
          info_lesson.quizes.forEach((v) => {
            this.context.selected_questions.push(v);
          });
        }

        UI.loading('hide');
      }
    }).catch((e) => {
      console.log(e);
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  manageQuiz() {
    if(this.context.is_edit) {
      this._editQuiz();
    } else {
      this._createQuiz();
    }
  }

  _createQuiz() {
    if(this.context.$refs.form_quiz.validate() && this.context.selected_questions.length > 0) {
      UI.loading('show');
      
      let components_arr = [];
      this.context.selected_questions.forEach((v) => {
        components_arr.push(v.question_id);
      });

      this.manage_service.createQuiz(
        this.context.title,
        this.context.description,
        this.context.answer_quiz,
        this.start_date,
        this.context.final_date,
        JSON.stringify(components_arr)
      ).then((r) => {
        if(r.success == true) {
          this.context.$refs.form_quiz.reset();
          this.context.selected_questions = [];
          components_arr = [];
          this.context.quiz_id = null;
          UI.loading('hide');
          UI.mensajeOk('', r.msg);
        }
      }).catch((e) => {
        UI.loading('hide');
        UI.mensajeBad('', e.msg);
      });
    } else {
      UI.mensajeBad('', 'Campos incompletos o preguntas no agregadas.');
    }
  }

  _editQuiz() {
    if(this.context.$refs.form_quiz.validate() && this.context.selected_questions.length > 0) {
      UI.loading('show');

      let components_arr = [];
      this.context.selected_questions.forEach((v) => {
        components_arr.push(v.question_id);
      });

      this.manage_service.editQuiz(
        this.context.quiz_id,
        this.context.title,
        this.context.description,
        this.context.answer_quiz,
        this.start_date,
        this.context.final_date,
        JSON.stringify(components_arr)
      ).then((r) => {
        if(r.success == true) {
          UI.loading('hide');
          components_arr = [];
          UI.mensajeOk('', r.msg);
        }
      }).catch((e) => {
        UI.loading('hide');
        UI.mensajeBad('', e.msg);
      });
    }
  }

  backHistory() {
    this.context.$router.go(-1);
  }

  addQuestion(quiz_id) {
    this.context.list_questions.forEach((v) => {
      if(v.question_id == quiz_id) {
        this.context.selected_questions.push(v);
      }
    });
    const dataArr = new Set(this.context.selected_questions);
    let result = [...dataArr];
    this.context.selected_questions = result;
    this.context.question_id = null;
  }

  removeQuestion(position) {
    this.context.selected_questions.splice(position, 1);
  }

  upQuestion(index) {
    let arr = this.context.selected_questions;
    let empty = [];

    arr.forEach((v, i) => {
      if(index == i) {
        this.current = v;
      }
      if(index-1 == i) {
        this.before = v;
      }
    });

    arr[index] = this.before;
    arr[index-1] = this.current;

    this.context.selected_questions = empty;
    this.context.selected_questions = arr;
    this.before = null;
    this.current = null;
  }

  downQuestion(index) {
    let arr = this.context.selected_questions;
    let empty = [];

    arr.forEach((v, i) => {
      if(index == i) {
        this.current = v;
      }
      if(index+1 == i) {
        this.after = v;
      }
    });

    arr[index] = this.after;
    arr[index+1] = this.current;

    this.context.selected_questions = empty;
    this.context.selected_questions = arr;
    this.after = null;
    this.current = null;
  }
}

export default ManageController;