import ManageRepository from "../repositories/ManageRepository";

class ManageService {
  manage_repository;

  constructor() {
    this.manage_repository = new ManageRepository();
  }

  async listComponents() {
    let r = await this.manage_repository.listComponents();
    return r;
  }

  async infoLesson(lesson_id) {
    let r = await this.manage_repository.infoLesson(lesson_id);
    return r;
  }

  async createLesson(title, description, components) {
    let r = await this.manage_repository.createLesson(title, description, components);
    return r;
  }

  async editLesson(lesson_id, title, description, components) {
    let r = await this.manage_repository.editLesson(lesson_id, title, description, components);
    return r;
  }
}

export default ManageService;