import ListQuestionsRepository from "../repositories/ListQuestionsRepository";

class ListQuestionsService {
  list_questions_repository;

  constructor() {
    this.list_questions_repository = new ListQuestionsRepository();
  }

  async listQuestions() {
    let r = await this.list_questions_repository.listQuestions();
    return r;
  }

  async deleteQuestion(question_id) {
    let r = await this.list_questions_repository.deleteQuestion(question_id);
    return r;
  }
}

export default ListQuestionsService;