import UI from  '../../../core/helpers/UI';
import File from "../../../core/helpers/File";
import ListStudentsEnrolledService from '../services/ListStudentsEnrolledService';

class ListStudentsEnrolledController {
  context;
  list_students_enrolled_service;

  constructor(context) {
    this.context = context;
    this.list_students_enrolled_service = new ListStudentsEnrolledService();

    this.context.headers = [
      { text: 'Código estudiante', value: 'student_id' },
      { text: 'Estudiante', value: 'name' },
      { text: 'Curso', value: 'course' },
    ];
    
    this.listStudents();
  }

  listStudents() {
    UI.loading('show');
    this.list_students_enrolled_service.listStudents()
    .then((r) => {
      if(r.success == true) {
        this.context.list_enrolled_students = [];
        this.context.list_enrolled_students = r.data;

        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }
}

export default ListStudentsEnrolledController;