import Rest from '../../../core/libs/Rest';

class ManageRepository {
  rest;

  constructor() {
    this.rest = new Rest();
  }

  createDocument(title, document) {
    this.rest.setParams({name:title, document:document});
    this.rest.setEndPoint('api/mod-doc/store-doc');
    return this.rest.post();
  }

  editDocument(document_id, title, article) {
    this.rest.setParams({name:title, document:article});
    this.rest.setEndPoint('api/mod-doc/update-doc');
    return this.rest.put(document_id);
  }

  infoDocument(document_id) {
    this.rest.setParams('/'+document_id);
    this.rest.setEndPoint('api/mod-doc/show-doc');
    return this.rest.get();
  }
}

export default ManageRepository;