import Rest from '../../../core/libs/Rest';
import { LISTADO_MOCKUP } from '../mockups/ListStudentsEnrolled';

class ListStudentsEnrolledRepository {
  rest;

  constructor() {
    this.rest = new Rest();
  }

  listStudents() {
    this.rest.setParams('');
    this.rest.setEndPoint('api/reports/list-all');
    return this.rest.peticionMockupV2('GET', null, LISTADO_MOCKUP.LIST_STUDENTS);
    //return this.rest.get();
  }
}

export default ListStudentsEnrolledRepository;