import UI from  '../../../core/helpers/UI';
import File from "../../../core/helpers/File";
import ListService from '../services/ListService';

class ListController {
  context;
  list_service;

  constructor(context) {
    this.context = context;
    this.list_service = new ListService();

    this.context.header_documents = [
      { text: 'Título', value: 'name' },
      { text: 'Estado', value: 'state' },
      { text: 'Documentos', value: 'document' },
      { text: '', value: 'actions', sortable: false },
    ];
    
    this._listDocuments();
  }

  _listDocuments() {
    this.context.list_documents = [];
    UI.loading('show');
    this.list_service.listDocuments()
    .then((r) => {
      if(r.success == true) {
        this.context.list_documents = r.data;
        
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  deleteDocument(document_id) {
    UI.loading('show');
    this.list_service.deleteDocument(document_id)
    .then((r) => {
      if(r.success == true) {
        this._listDocuments();
        UI.mensajeOk('', r.msg);
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }
}

export default ListController;