import ListTeachersRepository from "../repositories/ListTeachersRepository";

class ListTeachersService {
  list_teachers_repository;

  constructor() {
    this.list_teachers_repository = new ListTeachersRepository();
  }

  async listCourses() {
    let r = await this.list_teachers_repository.listCourses();
    return r;
  }
}

export default ListTeachersService;