import Site from '../../core/layouts/Principal.vue';
import List from './views/List.vue';
import Manage from './views/Manage.vue';
import ManageQuestion from './views/ManageQuestion.vue';
import ListQuestions from './views/ListQuestions.vue';

window.routes.push({
  path: '/quizes',
  name: 'quizes',
  component: Site,
  children: [
    {
      path: 'list',
      name: 'list',
      component: List,
    },
    {
      path: 'manage/:id?',
      name: 'manage',
      component: Manage,
    },
    {
      path: 'list-questions',
      name: 'list-questions',
      component: ListQuestions,
    },
    {
      path: 'manage-question/:id?',
      name: 'manage-question',
      component: ManageQuestion,
    },
  ],
});