import ManageRepository from "../repositories/ManageRepository";

class ManageService {
  manage_repository;

  constructor() {
    this.manage_repository = new ManageRepository();
  }

  async listTeachers() {
    let r = await this.manage_repository.listTeachers();
    return r;
  }

  async listCourses() {
    let r = await this.manage_repository.listCourses();
    return r;
  }

  async createTuition(title, teacher_id, course_id, date_init, date_finish, description) {
    let r = await this.manage_repository.createTuition(title, teacher_id, course_id, date_init, date_finish, description);
    return r;
  }

  async editTuition(tuition_id, title, teacher_id, course_id, date_init, date_finish, description) {
    let r = await this.manage_repository.editTuition(tuition_id, title, teacher_id, course_id, date_init, date_finish, description);
    return r;
  }

  async infoTuition(tuition_id) {
    let r = await this.manage_repository.infoTuition(tuition_id);
    return r;
  }
}

export default ManageService;