import ListRepository from "../repositories/ListRepository";

class ListService {
  list_repository;

  constructor() {
    this.list_repository = new ListRepository();
  }

  async listLessons() {
    let r = await this.list_repository.listLessons();
    return r;
  }

  async listModules() {
    let r = await this.list_repository.listModules();
    return r;
  }

  async deleteLesson(lesson_id) {
    let r = await this.list_repository.deleteLesson(lesson_id);
    return r;
  }

  async filterListLessons(module_id) {
    let r = await this.list_repository.filterListLessons(module_id);
    return r;
  }
}

export default ListService;