import UI from  '../../../core/helpers/UI';
import File from "../../../core/helpers/File";
import ListCoursesService from '../services/ListCoursesService';

class ListCoursesController {
  context;
  list_courses_service;

  constructor(context) {
    this.context = context;
    this.list_courses_service = new ListCoursesService();

    this.context.headers = [
      { text: 'Título', value: 'title', sortable: false },
      { text: 'Descripción', value: 'description', sortable: false },
      { text: 'Fecha', value: 'date', sortable: false },
      { text: 'Cant. Estudiantes', value: 'count_students', sortable: false },
      { text: 'Estado', value: 'state', sortable: false },
    ];
    
    this.listCourses();
  }

  listCourses() {
    UI.loading('show');
    this.list_courses_service.listCourses()
    .then((r) => {
      if(r.success == true) {
        this.context.list_courses = [];
        this.context.list_courses = r.data;

        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }
}

export default ListCoursesController;