import UI from  '../../../core/helpers/UI';
import File from "../../../core/helpers/File";
import ListService from '../services/ListService';

class ListController {
  context;
  list_service;

  constructor(context) {
    this.context = context;
    this.list_service = new ListService();

    this.context.header_students = [
      { text: 'ID', value: 'student_id' },
      { text: 'Nombres', value: 'names' },
      { text: 'Apellidos', value: 'last_names' },
      { text: 'Estado', value: 'state' },
      { text: 'Última conexión', value: 'last_conection' },
      { text: '', value: 'actions' },
    ];
    
    this.listCourses();
    this.listStudents();
  }

  listStudents() {
    UI.loading('show');
    this.list_service.listStudents()
    .then((r) => {
      if(r.success == true) {
        this.context.is_search = false;
        this.context.course_id = null;
        this.context.list_students = [];
        this.context.list_students = r.data;

        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  listCourses() {
    UI.loading('show');
    this.list_service.listCourses()
    .then((r) => {
      if(r.success == true) {
        this.context.list_courses = [];
        this.context.list_courses = r.data;
        
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  filterListStudents() {
    if(this.context.course_id) {
      UI.loading('show');
      this.list_service.filterListStudents(this.context.course_id)
      .then((r) => {
        if(r.success == true) {
          this.context.is_search = true;
          this.context.list_students = [];
          this.context.list_students = r.data;

          UI.loading('hide');
        }
      }).catch((e) => {
        UI.loading('hide');
        UI.mensajeBad('', e.msg);
      });
    } else {
      UI.mensajeBad('', 'Selecciona un curso para realizar la búsqueda.');
    }
  }

  deleteStudent(position, student_id) {
    UI.loading('show');
    this.list_service.deleteStudent(student_id)
    .then((r) => {
      if(r.success == true) {
        this.context.list_students.splice(position, 1);
        UI.mensajeOk('', r.msg);
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  infoStudent(student_id) {
    UI.loading('show');
    this.list_service.infoStudent(student_id)
    .then((r) => {
      if(r.success == true) {
        this.context.info_student = r.data;
        this.context.$set(this.context.dialog_detail, student_id, true);
        
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  closeDetail(student_id) {
    this.context.$set(this.context.dialog_detail, student_id, false);
    this.context.info_student = {};
  }
}

export default ListController;