import UI from  '../../../core/helpers/UI';
import File from "../../../core/helpers/File";
import ListService from '../services/ListService';

class ListController {
  context;
  list_service;

  constructor(context) {
    this.context = context;
    this.list_service = new ListService();

    this.context.headers = [
      { text: 'ID', value: 'tuition_id' },
      { text: 'Título', value: 'title' },
      { text: 'Curso asignado', value: 'course.title' },
      { text: 'Fecha inicial', value: 'date_init' },
      { text: 'Fecha final', value: 'date_finish' },
      { text: 'Profesor', value: 'teacher' },
      { text: '', value: 'actions' },
    ];
    
    this.listTuitions();
    this.listCategories();
  }

  listCategories() {
    UI.loading('show');
    this.list_service.listCategories()
    .then((r) => {
      if(r.success == true) {
        this.context.list_categories = [];
        this.context.list_categories = r.data;
        
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  listTuitions() {
    UI.loading('show');
    this.list_service.listTuitions()
    .then((r) => {
      if(r.success == true) {
        this.context.category_id = null;
        this.context.is_search = false;
        this.context.list_tuitions = [];
        this.context.list_tuitions = r.data;
        
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  filterListTuitions() {
    if(this.context.category_id || this.context.date_init && this.context.date_finish) {
      UI.loading('show');
      this.list_service.filterListTuitions(this.context.category_id, this.context.date_init, this.context.date_finish)
      .then((r) => {
        if(r.success == true) {
          this.context.is_search = true;
          this.context.list_tuitions = [];
          this.context.list_tuitions = r.data;
          
          UI.loading('hide');
        }
      }).catch((e) => {
        UI.loading('hide');
        UI.mensajeBad('', e.msg);
      });
    } else {
      UI.mensajeBad('', 'Debes seleccionar una categoría o fecha para filtrar.');
    }
  }

  deleteTuition(position, tuition_id) {
    UI.loading('show');
    this.list_service.deleteTuition(tuition_id)
    .then((r) => {
      if(r.success == true) {
        this.context.list_tuitions.splice(position, 1);
        UI.mensajeOk('', r.msg);
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  infoTuition(tuition_id) {
    UI.loading('show');
    this.list_service.infoTuition(tuition_id)
    .then((r) => {
      if(r.success == true) {
        this.context.$set(this.context.dialog_detail, tuition_id, true);
        this.context.info_tuition = {};
        this.context.info_tuition = r.data;
        
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  closeDetail(tuition_id) {
    this.context.$set(this.context.dialog_detail, tuition_id, false);
    this.context.info_tuition = {};
  }
}

export default ListController;