import ListAdminRepository from "../repositories/ListAdminRepository";

class ListAdminService {
  list_admin_repository;

  constructor() {
    this.list_admin_repository = new ListAdminRepository();
  }

  async listCourses() {
    let r = await this.list_admin_repository.listCourses();
    return r;
  }

  async listCategories() {
    let r = await this.list_admin_repository.listCategories();
    return r;
  }

  async deleteCourse(course_id) {
    let r = await this.list_admin_repository.deleteCourse(course_id);
    return r;
  }

  async filterListCourses(date_init, date_finish, category_id) {
    let r = await this.list_admin_repository.filterListCourses(date_init, date_finish, category_id);
    return r;
  }
}

export default ListAdminService;