import UI from  '../../../core/helpers/UI';
import File from "../../../core/helpers/File";
import ManageService from '../services/ManageService';

class ManageController {
  context;
  manage_service;
  current;
  after;
  before;

  constructor(context) {
    this.context = context;
    this.manage_service = new ManageService();

    if(this.context.$route.params.id) {
      this.context.course_id = JSON.parse(this.context.$route.params.id);

      if(this.context.course_id) {
        this.infoCourse();
        this.context.is_edit = true;
      } else {
        this.context.is_edit = false;
      }
    }

    this.listCategories();
    this.listLessons();
  }

  listCategories() {
    UI.loading('show');
    this.manage_service.listCategories()
    .then((r) => {
      if(r.success == true) {
        this.context.list_categories = [];
        this.context.list_categories = r.data;
        
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  listLessons() {
    UI.loading('show');
    this.manage_service.listLessons()
    .then((r) => {
      if(r.success == true) {
        this.context.list_lessons = [];
        this.context.list_lessons = r.data;
        
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  infoCourse() {
    UI.loading('show');
    this.manage_service.infoCourse(this.context.course_id)
    .then((r) => {
      if(r.success == true) {
        let info_course = r.data;

        this.context.course_id = info_course.course_id;
        this.context.title = info_course.title;
        this.context.category_id = info_course.course_categories.course_cat_id;
        this.context.image_str = info_course.image;
        this.context.description = info_course.description;

        if(info_course.lessons && info_course.lessons.length > 0) {
          info_course.lessons.forEach((v) => {
            this.context.selected_lessons.push(v);
          });
        }

        UI.loading('hide');
      }
    }).catch((e) => {
      console.log(e);
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  loadImage(file) {
    //this.context.image_send = file.name;
    this.context.image_str = 'https://cdn.vuetifyjs.com/images/cards/sunshine.jpg';
  }

  deleteImage() {
    this.context.image = null;
    this.context.image_str = '';
  }

  manageCourse() {
    if(this.context.is_edit) {
      this._editCourse();
    } else {
      this._createCourse();
    }
  }

  _createCourse() {
    if(this.context.$refs.form_manage_course.validate() && this.context.selected_lessons.length > 0) {
      UI.loading('show');

      let lessons_arr = [];
      this.context.selected_lessons.forEach((v) => {
        lessons_arr.push(v.lesson_id);
      });

      this.manage_service.createCourse(
        this.context.title,
        this.context.category_id,
        this.context.image_str,
        this.context.description,
        JSON.stringify(lessons_arr)
      ).then((r) => {
        if(r.success == true) {
          this.context.$refs.form_manage_course.reset();
          UI.mensajeOk('', r.msg);
          lessons_arr = [];
          this.deleteImage();
          this.context.selected_lessons = [];
          UI.loading('hide');
        }
      }).catch((e) => {
        UI.loading('hide');
        UI.mensajeBad('', e.msg);
      });
    }
  }

  _editCourse() {
    if(this.context.$refs.form_manage_course.validate() && this.context.selected_lessons.length > 0) {
      UI.loading('show');

      let lessons_arr = [];
      this.context.selected_lessons.forEach((v) => {
        lessons_arr.push(v.lesson_id);
      });

      this.manage_service.editCourse(
        this.context.course_id,
        this.context.title,
        this.context.category_id,
        this.context.image_str,
        this.context.description,
        JSON.stringify(lessons_arr)
      ).then((r) => {
        if(r.success == true) {
          this.context.$refs.form_manage_course.reset();
          UI.loading('show');
          UI.mensajeOk('', r.msg);
          lessons_arr = [];
          this.deleteImage();
          this.context.selected_lessons = [];
          UI.loading('hide');
          this.backHistory();
        }
      }).catch((e) => {
        UI.loading('hide');
        UI.mensajeBad('', e.msg);
      });
    }
  }

  backHistory() {
    this.context.$router.go(-1);
  }

  addLesson(lesson_id) {
    this.context.list_lessons.forEach((v) => {
      if(v.lesson_id == lesson_id) {
        this.context.selected_lessons.push(v);
      }
    });
    const dataArr = new Set(this.context.selected_lessons);
    let result = [...dataArr];
    this.context.selected_lessons = result;
    this.context.lesson_id = null;
  }

  removeLesson(position) {
    this.context.selected_lessons.splice(position, 1);
  }

  upLesson(index) {
    let arr = this.context.selected_lessons;
    let empty = [];

    arr.forEach((v, i) => {
      if(index == i) {
        this.current = v;
      }
      if(index-1 == i) {
        this.before = v;
      }
    });

    arr[index] = this.before;
    arr[index-1] = this.current;

    this.context.selected_lessons = empty;
    this.context.selected_lessons = arr;
    this.before = null;
    this.current = null;
  }

  downLesson(index) {
    let arr = this.context.selected_lessons;
    let empty = [];

    arr.forEach((v, i) => {
      if(index == i) {
        this.current = v;
      }
      if(index+1 == i) {
        this.after = v;
      }
    });

    arr[index] = this.after;
    arr[index+1] = this.current;

    this.context.selected_lessons = empty;
    this.context.selected_lessons = arr;
    this.after = null;
    this.current = null;
  }
}

export default ManageController;