<template>
  <div>
    <section class="main">
      <div class="container-fluid main-box-content contentModulo py-2">
        <div class="breadcrumb px-3">
          <ul>
            <li>Inicio</li>
            <li>Matrículas</li>
            <li>{{is_edit ? 'Editar' : 'Crear'}} matrícula</li>
          </ul>
        </div>

        <div class="title">
          <h1 class="my-0">{{is_edit ? 'Editar' : 'Crear'}} matrícula</h1>
        </div>

        <v-card class="my-3 my-md-4">
          <v-card-text class="px-lg-4">
            <v-form class="w-100" ref="form_tuition" method="post" v-model="form_valid" lazy-validation @submit.prevent="manageTuition()">
              <div class="row align-items-end pt-3">
                <div class="col-lg-12 px-2">
                  <v-text-field label="Titulo" v-model="title" :rules="[rules.requerido]" filled></v-text-field>
                </div>
                <div class="col-md-6 px-2">
                  <v-select label="Profesor" :items="list_teachers" :rules="[rules.requerido]" v-model="teacher_id" filled></v-select>
                </div>
                <div class="col-md-6 px-2">
                  <v-select label="Curso" :items="list_courses" :rules="[rules.requerido]" v-model="course_id" filled></v-select>
                </div>
                <div class="col-md-6 px-2">
                  <v-menu v-model="menu_init" :close-on-content-click="false" :nudge-right="40" transition="scale-transition" offset-y min-width="auto">
                    <template v-slot:activator="{ on, attrs }">
                      <v-text-field v-model="date_init" :rules="[rules.requerido]" label="Fecha inicial" readonly v-bind="attrs" filled v-on="on" append-icon="mdi-calendar"></v-text-field>
                    </template>
                    <v-date-picker v-model="date_init" @input="menu_init = false"></v-date-picker>
                  </v-menu>
                </div>
                <div class="col-md-6 px-2">
                  <v-menu v-model="menu_finish" :close-on-content-click="false" :nudge-right="40" transition="scale-transition" offset-y min-width="auto">
                    <template v-slot:activator="{ on, attrs }">
                      <v-text-field v-model="date_finish" :rules="[rules.requerido]" label="Fecha final" readonly v-bind="attrs" filled v-on="on" append-icon="mdi-calendar"></v-text-field>
                    </template>
                    <v-date-picker v-model="date_finish" @input="menu_finish = false"></v-date-picker>
                  </v-menu>
                </div>
                <div class="col-lg-12 px-2">
                  <v-textarea label="Descripción" v-model="description" :rules="[rules.requerido]" filled></v-textarea>
                </div>
              </div>

              <v-btn color="success" class="d-table mt-4 mx-auto" rounded min-width="120" type="submit">{{is_edit ? 'Actualizar' : 'Guardar'}}</v-btn>
            </v-form>
          </v-card-text>
        </v-card>
      </div>
    </section>
  </div>
</template>

<script>
import Helpers from '../../../core/helpers/Helpers';
import ManageController from '../controllers/ManageController';

export default {
  data() {
    return {
      Helpers,
      tuition_id: null,
      is_edit: false,
      title: '',
      description: '',
      course_id: null,
      teacher_id: null,
      
      list_teachers: [],
      list_courses: [],

      date_init: null,
      date_finish: null,
      menu_init: false,
      menu_finish: false,

      form_valid: true,

      rules: {
        requerido: value => !!value || 'Campo obligatorio',
        email: value => !value || /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(value) || 'Email incorrecto',
      },

      manage_controller: null
    };
  },

  mounted() {
    this.manage_controller = new ManageController(this);
  },

  methods: {
    manageTuition() {
      this.manage_controller.manageTuition();
    }
  }
}
</script>