import Rest from '../../../core/libs/Rest';

class ListRepository {
  rest;

  constructor() {
    this.rest = new Rest();
  }

  listTitles() {
    this.rest.setParams('');
    this.rest.setEndPoint('api/mod-title/list-all');
    return this.rest.get();
  }

  deleteTitle(title_id) {
    this.rest.setParams('');
    this.rest.setEndPoint('api/mod-title/delete-title');
    return this.rest.delete(title_id);
  }
}

export default ListRepository;