import DashboardTeacherRepository from "../repositories/DashboardTeacherRepository";

class DashboardTeacherService {
  dashboard_teacher_repository;

  constructor() {
    this.dashboard_teacher_repository = new DashboardTeacherRepository();
  }

  async counters() {
    let r = await this.dashboard_teacher_repository.counters();
    return r;
  }

  async myCourses() {
    let r = await this.dashboard_teacher_repository.myCourses();
    return r;
  }

  async lastLessons() {
    let r = await this.dashboard_teacher_repository.lastLessons();
    return r;
  }
}

export default DashboardTeacherService;