import ManageRepository from "../repositories/ManageRepository";

class ManageService {
  manage_repository;

  constructor() {
    this.manage_repository = new ManageRepository();
  }

  async createArticle(title, article) {
    let r = await this.manage_repository.createArticle(title, article);
    return r;
  }

  async editArticle(mod_article_id, title, article) {
    let r = await this.manage_repository.editArticle(mod_article_id, title, article);
    return r;
  }

  async infoArticle(article_id) {
    let r = await this.manage_repository.infoArticle(article_id);
    return r;
  }
}

export default ManageService;