import Rest from '../../../core/libs/Rest';
import { MANAGE_MOCKUP } from '../mockups/Manage';

class ManageRepository {
  rest;

  constructor() {
    this.rest = new Rest();
  }

  listQuestions() {
    this.rest.setParams('');
    this.rest.setEndPoint('api/questions/list-all');
    return this.rest.peticionMockupV2('GET', null, MANAGE_MOCKUP.LIST_QUESTIONS);
    //return this.rest.get();
  }

  infoQuiz(quiz_id) {
    this.rest.setParams('/'+quiz_id);
    this.rest.setEndPoint('api/quiz/info');
    return this.rest.peticionMockupV2('GET', null, MANAGE_MOCKUP.INFO_QUIZ);
    //return this.rest.get();
  }

  createQuiz(title, description, answer_quiz, start_date, final_date, questions) {
    this.rest.setParams({title:title, description:description, answer_quiz:answer_quiz, start_date:start_date, final_date:final_date, questions:questions});
    this.rest.setEndPoint('api/quiz/store');
    return this.rest.peticionMockupV2('POST', null, MANAGE_MOCKUP.CREATE_QUIZ);
    //return this.rest.get();
  }

  editQuiz(quiz_id, title, description, components) {
    this.rest.setParams({title:title, description:description, components:components});
    this.rest.setEndPoint('api/lesson/store');
    return this.rest.peticionMockupV2('PUT', quiz_id, MANAGE_MOCKUP.EDIT_QUIZ);
    //return this.rest.get();
  }
}

export default ManageRepository;