import DashboardAdminRepository from "../repositories/DashboardAdminRepository";

class DashboardAdminService {
  dashboard_admin_repository;

  constructor() {
    this.dashboard_admin_repository = new DashboardAdminRepository();
  }

  async counters() {
    let r = await this.dashboard_admin_repository.counters();
    return r;
  }

  async myCourses() {
    let r = await this.dashboard_admin_repository.myCourses();
    return r;
  }

  async lastLessons() {
    let r = await this.dashboard_admin_repository.lastLessons();
    return r;
  }
}

export default DashboardAdminService;