<template>
  <div>
    <section class="main">
      <div class="container-fluid main-box-content contentModulo py-2">
        <div class="breadcrumb px-3">
          <ul>
            <li>Inicio</li>
            <li>Módulos</li>
            <li>{{is_edit ? 'Editar' : 'Crear'}} cuestionario</li>
          </ul>
        </div>

        <div class="title">
          <h1 class="my-0">{{is_edit ? 'Editar' : 'Crear'}} cuestionario</h1>
        </div>

        <v-card class="mt-3 mt-md-4">
          <v-card-text class="px-lg-4">
            <v-form class="w-100" ref="form_quiz" method="post" v-model="form_valid" lazy-validation @submit.prevent="manageQuiz()">
              <v-text-field label="Titulo" v-model="title" :rules="[rules.requerido]" filled></v-text-field>

              <div class="row">
                <div class="col-md-6">
                  <v-textarea filled label="Descripción del cuestionario" :rules="[rules.requerido]" v-model="description"></v-textarea>
                </div>
                <div class="col-md-6">
                  <v-textarea filled label="Mensaje de respuesta del cuestionario" v-model="answer_quiz" :rules="[rules.requerido]"></v-textarea>
                </div>
              </div>

              <div class="row">
                <div class="col-md-6">
                  <v-menu v-model="menu_init" :close-on-content-click="false" :nudge-right="40" transition="scale-transition" offset-y min-width="auto">
                    <template v-slot:activator="{ on, attrs }">
                      <v-text-field v-model="start_date" readonly v-bind="attrs" filled v-on="on" label="Fecha inicial" :rules="[rules.requerido]" append-icon="mdi-calendar"></v-text-field>
                    </template>
                    <v-date-picker v-model="start_date" @input="menu_init = false"></v-date-picker>
                  </v-menu>
                </div>
                <div class="col-md-6">
                  <v-menu v-model="menu_finish" :close-on-content-click="false" :nudge-right="40" transition="scale-transition" offset-y min-width="auto">
                    <template v-slot:activator="{ on, attrs }">
                      <v-text-field v-model="final_date" readonly v-bind="attrs" filled v-on="on" label="Fecha final" :rules="[rules.requerido]" append-icon="mdi-calendar"></v-text-field>
                    </template>
                    <v-date-picker v-model="final_date" @input="menu_finish = false"></v-date-picker>
                  </v-menu>
                </div>
                <div class="col-sm-9 col-md-8">
                  <v-select item-text="title" v-model="question_id" item-value="question_id" hide-details :disabled="selected_questions.length == list_questions.length" :items="list_questions" label="Preguntas" filled clearable></v-select>
                </div>
                <div class="col-sm-3 col-md-2">
                  <v-btn block color="primary" min-width="120" min-height="56" :disabled="!question_id" @click="addQuestion(question_id)">Agregar componente</v-btn>
                </div>
                <div class="col-sm-3 col-md-2">
                  <v-btn height="56" outlined block color="primary" href="#/quizes/manage-question">Crear pregunta</v-btn>
                </div>

                <div class="col-12 mt-3">
                  <template v-for="(item, index) in selected_questions">
                    <div :class="index > 0 ? 'block-lesson py-2 border-top' : 'block-lesson py-2'" :key="index">
                      <div class="row align-items-center">
                        <div class="col-sm-8 col-md-9">
                          <h4 class="my-0 fz-16 fw-500 text-standar">{{item.title}}</h4>
                        </div>
                        <div class="col-sm-4 col-md-3 d-flex align-items-center justify-content-end flex-wrap">
                          <v-tooltip bottom v-if="selected_questions.length > 0 && index+1 != selected_questions.length">
                            <template v-slot:activator="{ on, attrs }">
                              <v-btn color="warning" v-bind="attrs" class="ml-3" v-on="on" icon @click="downQuestion(index)">
                                <v-icon>mdi-arrow-down</v-icon>
                              </v-btn>
                            </template>
                            <span>Bajar {{item.title}}</span>
                          </v-tooltip>

                          <v-tooltip bottom v-if="selected_questions.length > 0 && index > 0">
                            <template v-slot:activator="{ on, attrs }">
                              <v-btn color="warning" v-bind="attrs" class="ml-3" v-on="on" icon @click="upQuestion(index)">
                                <v-icon>mdi-arrow-up</v-icon>
                              </v-btn>
                            </template>
                            <span>Subir {{item.title}}</span>
                          </v-tooltip>

                          <v-tooltip bottom>
                            <template v-slot:activator="{ on, attrs }">
                              <v-btn color="primary" v-bind="attrs" class="ml-3" v-on="on" icon>
                                <v-icon>mdi-eye-outline</v-icon>
                              </v-btn>
                            </template>
                            <span>Habilitar {{item.title}}</span>
                          </v-tooltip>

                          <v-tooltip bottom>
                            <template v-slot:activator="{ on, attrs }">
                              <v-btn color="danger" v-bind="attrs" v-on="on" @click="removeQuestion(index)" class="ml-3" icon>
                                <v-icon>mdi-close</v-icon>
                              </v-btn>
                            </template>
                            <span>Eliminar {{item.title}}</span>
                          </v-tooltip>
                        </div>
                      </div>
                    </div>
                  </template>
                </div>
              </div>

              <v-btn class="d-table mx-auto mt-2" type="submit" rounded width="120" color="success">{{is_edit ? 'Actualizar' : 'Guardar'}}</v-btn>
            </v-form>
          </v-card-text>
        </v-card>
      </div>
    </section>
  </div>
</template>

<script>
import Helpers from '../../../core/helpers/Helpers';
import ManageController from '../controllers/ManageController';

export default {
  data() {
    return {
      Helpers,
      quiz_id: null,
      is_edit: false,
      title: '',
      description: '',
      answer_quiz: '',
      menu_init: false,
      menu_finish: false,
      start_date: null,
      final_date: null,

      form_valid: true,

      rules: {
        requerido: value => !!value || 'Campo obligatorio',
        url: value => this.validatedUrl(value) || 'URL no válida',
        email: value => !value || /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(value) || 'Email incorrecto',
      },

      selected_questions: [],
      question_id: null,
      list_questions: [],

      manage_controller: null
    };
  },

  mounted() {
    this.manage_controller = new ManageController(this);
  },

  methods: {
    addQuestion(quiz_id) {
      this.manage_controller.addQuestion(quiz_id);
    },

    removeQuestion(index) {
      this.manage_controller.removeQuestion(index);
    },

    upQuestion(index) {
      this.manage_controller.upQuestion(index);
    },

    downQuestion(index) {
      this.manage_controller.downQuestion(index);
    },

    manageQuiz() {
      this.manage_controller.manageQuiz();
    }
  }
}
</script>