import UI from  '../../../core/helpers/UI';
import File from "../../../core/helpers/File";
import ListService from '../services/ListService';

class ListController {
  context;
  list_service;

  constructor(context) {
    this.context = context;
    this.list_service = new ListService();

    this.context.header_images = [
      { text: 'Título', value: 'name' },
      { text: 'Estado', value: 'state' },
      { text: 'Image', value: 'image' },
      { text: '', value: 'actions', sortable: false },
    ];
    
    this._listImages();
  }

  _listImages() {
    this.context.list_images = [];
    UI.loading('show');
    this.list_service.listImages()
    .then((r) => {
      if(r.success == true) {
        this.context.list_images = r.data;
        
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  deleteImage(image_id) {
    UI.loading('show');
    this.list_service.deleteImage(image_id)
    .then((r) => {
      if(r.success == true) {
        this._listImages();
        UI.mensajeOk('', r.msg);
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }
}

export default ListController;