import Rest from '../../../core/libs/Rest';
import { DETAIL_STUDENTS_MOCKUP } from '../mockups/DetailStudents';

class ListRepository {
  rest;

  constructor() {
    this.rest = new Rest();
  }

  infoLesson(lesson_id) {
    this.rest.setParams('/'+lesson_id);
    this.rest.setEndPoint('api/lesson/detail');
    return this.rest.peticionMockupV2('GET', null, DETAIL_STUDENTS_MOCKUP.INFO_LESSONS);
    //return this.rest.get();
  }
}

export default ListRepository;