import Rest from '../../../core/libs/Rest';
import { LISTADO_MOCKUP } from '../mockups/ListQuestions';

class ListQuestionsRepository {
  rest;

  constructor() {
    this.rest = new Rest();
  }

  listQuestions() {
    this.rest.setParams('');
    this.rest.setEndPoint('api/question/list-all');
    return this.rest.peticionMockupV2('GET', null, LISTADO_MOCKUP.LIST_QUESTIONS);
    //return this.rest.get();
  }

  deleteQuestion(question_id) {
    this.rest.setParams('');
    this.rest.setEndPoint('api/question/delete');
    return this.rest.peticionMockupV2('DELETE', question_id, LISTADO_MOCKUP.DELETE_QUESTION);
    //return this.rest.get();
  }
}

export default ListQuestionsRepository;