import UI from  '../../../core/helpers/UI';
import ListService from '../services/ListService';

class ListController {
  context;
  list_service;

  constructor(context) {
    this.context = context;
    this.list_service = new ListService();

    this.context.header_articles = [
      { text: 'Título', value: 'name' },
      { text: 'Contenido', value: 'content' },
      { text: 'Estado', value: 'status' },
      { text: 'Fecha', value: 'created_at' },
      { text: '', value: 'actions', sortable: false },
    ];
    
    this._listArticles();
  }

  _listArticles() {
    this.context.list_articles = [];
    UI.loading('show');
    this.list_service.listArticles()
    .then((r) => {
      if(r.success == true) {
        this.context.list_articles = r.data;
        
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  deleteArticle(article_id) {
    UI.loading('show');
    this.list_service.deleteArticle(article_id)
    .then((r) => {
      if(r.success == true) {
        this._listArticles();
        UI.mensajeOk('', r.msg);
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }
}

export default ListController;