import UI from  '../../../core/helpers/UI';
import File from "../../../core/helpers/File";
import ListService from '../services/ListService';

class ListController {
  context;
  list_service;

  constructor(context) {
    this.context = context;
    this.list_service = new ListService();

    this.context.header_videos = [
      { text: 'Título', value: 'name' },
      { text: 'Estado', value: 'state' },
      { text: 'Video', value: 'video' },
      { text: '', value: 'actions', sortable: false },
    ];
    
    this._listVideos();
  }

  _listVideos() {
    this.context.list_videos = [];
    UI.loading('show');
    this.list_service.listVideos()
    .then((r) => {
      if(r.success == true) {
        this.context.list_videos = r.data;
        
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  deleteVideo(video_id) {
    UI.loading('show');
    this.list_service.deleteVideo(video_id)
    .then((r) => {
      if(r.success == true) {
        this._listVideos();
        UI.mensajeOk('', r.msg);
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }
}

export default ListController;