<template>
  <div>
    <section class="main">
      <div class="container-fluid main-box-content contentModulo py-2">
        <div class="breadcrumb px-3">
          <ul>
            <li>Inicio</li>
            <li>Cursos</li>
            <li>Listado de cursos</li>
          </ul>
        </div>

        <div class="title">
          <h1 class="my-0">Listado de cursos</h1>
        </div>

        <v-card class="mt-3 mt-md-4">
          <v-card-text>
            <div class="row align-items-end pt-3">
              <div class="col-md-8 col-lg-9 col-xl-10 box-set-date">
                <div class="row align-items-end mx-n2">
                  <div class="col-lg-6 px-2">
                    <p class="mt-0 mb-1 text-left text-standar fw-600 fz-16">Palabra clave</p>

                    <v-text-field v-model="search_list_courses" append-icon="mdi-magnify" outlined dense hide-details clearable></v-text-field>
                  </div>
                  <div class="col-lg-3 px-2">
                    <p class="mt-0 mb-1 text-left text-standar fw-600 fz-16">Categoría</p>

                    <v-select v-model="category_id" :items="list_categories" outlined dense hide-details item-value="course_cat_id" item-text="name"></v-select>
                  </div>
                  <div class="col-lg-2 px-2">
                    <v-btn rounded @click="filterListCourses()" color="success" class="d-table ml-auto px-md-4">Buscar</v-btn>
                  </div>
                </div>
              </div>
              <div class="col-md-4 col-lg-3 col-xl-2 mt-3 mt-md-0">
                <div class="d-flex align-items-center justify-content-between">
                  <v-btn color="info" v-if="is_search" @click="listCourses()" rounded class="d-table ml-auto">
                    Ver todos
                  </v-btn>
                </div>
              </div>
            </div>

            <div class="table-responsive mt-3">
              <v-data-table
                class="mt-3"
                :headers="headers"
                :items="list_courses"
                :items-per-page="10"
                :search="search_list_courses"
              >
                <template v-slot:item.state="{ item }">
                  <!-- Estado -->
                  <v-chip color="success" v-if="item.state == 1">Habilitada</v-chip>
                </template>

                <template v-slot:item.actions="{ item }">
                  <div class="d-flex align-items-center justify-content-center">
                    <!-- Editar -->
                    <v-tooltip bottom>
                      <template v-slot:activator="{ on, attrs }">
                        <v-btn icon color="primary" :href="'#/courses/manage/'+item.course_id" v-bind="attrs" v-on="on">
                          <v-icon>mdi-square-edit-outline</v-icon>
                        </v-btn>
                      </template>
                      <span>Editar</span>
                    </v-tooltip>

                    <!-- Detalle -->
                    <v-tooltip bottom>
                      <template v-slot:activator="{ on, attrs }">
                        <v-btn icon color="success" :href="'#/courses/detail-admin/'+item.course_id" v-bind="attrs" v-on="on">
                          <v-icon>mdi-eye-outline</v-icon>
                        </v-btn>
                      </template>
                      <span>Ver detalle</span>
                    </v-tooltip>
                    
                    <!-- Eliminar -->
                    <v-tooltip bottom>
                      <template v-slot:activator="{ on, attrs }">
                        <v-btn icon color="danger" v-bind="attrs" v-on="on" @click="deleteCourse(list_courses.map(function(x) {return x.course_id; }).indexOf(item.course_id), item.course_id)">
                          <v-icon>mdi-trash-can-outline</v-icon>
                        </v-btn>
                      </template>
                      <span>Eliminar</span>
                    </v-tooltip>
                  </div>
                </template>
              </v-data-table>
            </div>
          </v-card-text>
        </v-card>
      </div>
    </section>
  </div>
</template>

<script>
import Helpers from '../../../core/helpers/Helpers';
import ListAdminController from '../controllers/ListAdminController';

export default {
  data() {
    return {
      Helpers,
      search_list_courses: '',
      headers: [],
      list_courses: [],
      dialog_detail: {},
      is_search: false,
      list_categories: [],

      date_init: null,
      date_finish: null,
      menu_init: false,
      menu_finish: false,
      category_id: null,

      list_admin_controller: null
    };
  },

  mounted() {
    this.list_admin_controller = new ListAdminController(this);
  },

  methods: {
    deleteCourse(position, course_id) {
      this.list_admin_controller.deleteCourse(position, course_id);
    },

    filterListCourses() {
      this.list_admin_controller.filterListCourses();
    },

    listCourses() {
      this.list_admin_controller.listCourses();
    }
  }
}
</script>