import UI from  '../../../core/helpers/UI';
import File from "../../../core/helpers/File";
import ListStudentsService from '../services/ListStudentsService';

class ListStudentsController {
  context;
  list_students_service;

  constructor(context) {
    this.context = context;
    this.list_students_service = new ListStudentsService();
    
    this.listCoursesForStudent();
  }

  listCoursesForStudent() {
    UI.loading('show');
    this.list_students_service.listCoursesForStudent()
    .then((r) => {
      if(r.success == true) {
        this.context.list_courses = [];
        this.context.list_courses = r.data;
        
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  subscribeCourse(course_id, position) {
    UI.loading('show');
    this.list_students_service.subscribeCourse(course_id, position)
    .then((r) => {
      if(r.success == true) {
        this.context.list_courses[position].is_subscribed = true;
        UI.mensajeOk('', r.msg);
        window.location.href = '#/courses/detail-students/'+course_id;
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  cancelSubscribeCourse(course_id, position) {
    UI.loading('show');
    this.list_students_service.cancelSubscribeCourse(course_id, position)
    .then((r) => {
      if(r.success == true) {
        this.context.list_courses[position].is_subscribed = false;
        UI.mensajeOk('', r.msg);
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }
}

export default ListStudentsController;