<template>
  <div>
    <section class="main">
      <div class="container-fluid main-box-content contentModulo py-2">
        <div class="breadcrumb px-3">
          <ul>
            <li>Inicio</li>
            <li>Cursos</li>
            <li>{{is_edit ? 'Editar' : 'Crear'}} curso</li>
          </ul>
        </div>

        <div class="title">
          <h1 class="my-0">{{is_edit ? 'Editar' : 'Crear'}} curso</h1>
        </div>

        <v-card class="mt-3 mt-md-4">
          <v-form class="w-100" ref="form_manage_course" method="post" v-model="form_valid" lazy-validation @submit.prevent="manageCourse()">
            <v-card-text class="px-lg-4">
              <div class="row align-items-start pt-3">
                <div class="col-lg-6">
                  <v-text-field v-model="title" :rules="[rules.requerido]" label="Titulo" filled></v-text-field>
                </div>
                <div class="col-lg-6">
                  <v-select v-model="category_id" :rules="[rules.requerido]" label="Categoría" item-text="name" item-value="course_cat_id" filled :items="list_categories"></v-select>
                </div>
                <div class="col-12">
                  <v-file-input :prepend-icon="null" v-model="image" label="Imagen" @change="loadImage" v-if="!image_str" :rules="[rules.requerido]" filled></v-file-input>
                </div>
                <div :class="image_str ? 'col-lg-8' : 'col-lg-12'">
                  <v-textarea v-model="description" :rules="[rules.requerido]" label="Descripción" filled></v-textarea>
                </div>
                <div class="col-lg-4 mb-4" v-if="image_str">
                  <div class="box-img">
                    <v-btn fab x-small color="danger" @click="deleteImage()">
                      <v-icon>mdi-close</v-icon>
                    </v-btn>
                    <v-img :src="image_str" height="200px"></v-img>
                  </div>
                </div>
                <div class="col-sm-6 col-md-8">
                  <v-select item-text="title" v-model="lesson_id" item-value="lesson_id" :items="list_lessons" label="Lecciones" :disabled="selected_lessons.length == list_lessons.length" filled clearable></v-select>
                </div>
                <div class="col-sm-3 col-md-2">
                  <v-btn block color="primary" min-width="120" min-height="56" rounded :disabled="!lesson_id" @click="addLesson(lesson_id)">Agregar lección</v-btn>
                </div>
                <div class="col-sm-3 col-md-2">
                  <v-btn block color="primary" min-width="120" min-height="56" rounded outlined href="#/lessons/manage">Crear lección</v-btn>
                </div>
                <div class="col-12">
                  <template v-for="(item, index) in selected_lessons">
                    <div :class="index > 0 ? 'block-lesson py-2 border-top' : 'block-lesson py-2'" :key="index">
                      <div class="row align-items-center">
                        <div class="col-sm-8 col-md-9">
                          <h4 class="my-0 fz-16 fw-500 text-standar">{{item.title}}</h4>
                        </div>
                        <div class="col-sm-4 col-md-3 d-flex align-items-center justify-content-end flex-wrap">
                          <v-tooltip bottom v-if="selected_lessons.length > 0 && index+1 != selected_lessons.length">
                            <template v-slot:activator="{ on, attrs }">
                              <v-btn color="warning" v-bind="attrs" class="ml-3" v-on="on" icon @click="downLesson(index)">
                                <v-icon>mdi-arrow-down</v-icon>
                              </v-btn>
                            </template>
                            <span>Bajar {{item.title}}</span>
                          </v-tooltip>

                          <v-tooltip bottom v-if="selected_lessons.length > 0 && index > 0">
                            <template v-slot:activator="{ on, attrs }">
                              <v-btn color="warning" v-bind="attrs" class="ml-3" v-on="on" icon @click="upLesson(index)">
                                <v-icon>mdi-arrow-up</v-icon>
                              </v-btn>
                            </template>
                            <span>Subir {{item.title}}</span>
                          </v-tooltip>

                          <v-tooltip bottom>
                            <template v-slot:activator="{ on, attrs }">
                              <v-btn color="primary" v-bind="attrs" class="ml-3" v-on="on" icon>
                                <v-icon>mdi-eye-outline</v-icon>
                              </v-btn>
                            </template>
                            <span>Habilitar {{item.title}}</span>
                          </v-tooltip>

                          <v-tooltip bottom>
                            <template v-slot:activator="{ on, attrs }">
                              <v-btn color="danger" v-bind="attrs" v-on="on" @click="removeLesson(index)" class="ml-3" icon>
                                <v-icon>mdi-close</v-icon>
                              </v-btn>
                            </template>
                            <span>Eliminar {{item.title}}</span>
                          </v-tooltip>
                        </div>
                      </div>
                    </div>
                  </template>
                </div>
              </div>

              <v-btn color="success" class="d-table mt-4 mx-auto" rounded min-width="120"  :disabled="selected_lessons.length == 0" type="submit">{{is_edit ? 'Actualizar' : 'Guardar'}}</v-btn>
            </v-card-text>
          </v-form>
        </v-card>
      </div>
    </section>
  </div>
</template>

<script>
import Helpers from '../../../core/helpers/Helpers';
import ManageController from '../controllers/ManageController';

export default {
  data() {
    return {
      Helpers,
      dialog_lessons: {},
      title: '',
      category_id: null,
      image: null,
      image_str: '',
      description: '',
      selected_lessons: [],
      lesson_id: null,
      
      form_valid: true,
      name_category: '',
      rules: {
        requerido: value => !!value || 'Campo obligatorio',
        url: value => this.validatedUrl(value) || 'URL no válida',
        email: value => !value || /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(value) || 'Email incorrecto',
      },

      list_categories: [],
      list_lessons: [],
      is_edit: false,

      dialog_category: {},

      manage_controller: null
    };
  },

  mounted() {
    this.manage_controller = new ManageController(this);
  },

  methods: {
    loadImage(file) {
      this.manage_controller.loadImage(file);
    },

    deleteImage() {
      this.manage_controller.deleteImage();
    },

    manageCourse() {
      this.manage_controller.manageCourse();
    },
    
    addLesson(lesson_id) {
      this.manage_controller.addLesson(lesson_id);
    },

    removeLesson(index) {
      this.manage_controller.removeLesson(index);
    },

    upLesson(index) {
      this.manage_controller.upLesson(index);
    },

    downLesson(index) {
      this.manage_controller.downLesson(index);
    }
  }
}
</script>