import Rest from '../../../core/libs/Rest';
import { LISTADO_MOCKUP } from '../mockups/List';

class ListRepository {
  rest;

  constructor() {
    this.rest = new Rest();
  }

  listImages() {
    this.rest.setParams('');
    this.rest.setEndPoint('api/mod-image/list-all');
    return this.rest.get();
  }

  deleteImage(image_id) {
    this.rest.setParams('');
    this.rest.setEndPoint('api/mod-image/delete-image');
    return this.rest.delete(image_id);
  }
}

export default ListRepository;