<template>
  <div>
    <section class="main">
      <div class="container-fluid main-box-content contentModulo py-2">
        <div class="breadcrumb px-3">
          <ul>
            <li>Inicio</li>
            <li>Inscripciones</li>
            <li>Listado de inscripciones</li>
          </ul>
        </div>

        <div class="title">
          <h1 class="my-0">Listado de inscripciones</h1>
        </div>

        <v-card class="mt-3 mt-md-4">
          <v-card-text>
            <div class="row align-items-end pt-3">
              <div class="col-md-8 col-lg-9 col-xl-10 box-set-date">
                <div class="row align-items-end mx-n2">
                  <div class="col-lg-5 px-2">
                    <p class="mt-0 mb-1 text-left text-standar fw-600 fz-16">Fecha inicial</p>

                    <v-menu v-model="menu_init" :close-on-content-click="false" :nudge-right="40" transition="scale-transition" offset-y min-width="auto">
                      <template v-slot:activator="{ on, attrs }">
                        <v-text-field v-model="date_init" readonly v-bind="attrs" outlined dense hide-details v-on="on" append-icon="mdi-calendar"></v-text-field>
                      </template>
                      <v-date-picker v-model="date_init" @input="menu_init = false"></v-date-picker>
                    </v-menu>
                  </div>
                  <div class="col-lg-5 px-2">
                    <p class="mt-0 mb-1 text-left text-standar fw-600 fz-16">Fecha final</p>

                    <v-menu v-model="menu_finish" :close-on-content-click="false" :nudge-right="40" transition="scale-transition" offset-y min-width="auto">
                      <template v-slot:activator="{ on, attrs }">
                        <v-text-field v-model="date_finish" readonly v-bind="attrs" outlined dense hide-details v-on="on" append-icon="mdi-calendar"></v-text-field>
                      </template>
                      <v-date-picker v-model="date_finish" @input="menu_finish = false"></v-date-picker>
                    </v-menu>
                  </div>
                  <div class="col-lg-2 px-2">
                    <v-btn rounded @click="filterInscriptions()" color="success" class="d-table ml-auto px-md-4">Buscar</v-btn>
                  </div>
                </div>
              </div>
              <div class="col-md-4 col-lg-3 col-xl-2 mt-3 mt-md-0">
                <div class="d-flex align-items-center justify-content-between">
                  <v-btn color="info" @click="listInscriptions()" v-if="is_search" rounded class="d-table ml-auto">
                    Ver todos
                  </v-btn>
                </div>
              </div>
            </div>

            <div class="table-responsive mt-3">
              <v-data-table
                class="mt-3"
                :headers="headers"
                :items="list_inscriptions"
                :items-per-page="10"
                :search="search_list_inscriptions"
              >
                <template v-slot:top>
                  <v-text-field
                    filled
                    v-model="search_list_inscriptions"
                    append-icon="mdi-magnify"
                    label="Buscar"
                    hide-details
                    clearable
                    class="mb-4"
                  ></v-text-field>
                </template>

                <template v-slot:item.state="{ item }">
                  <!-- Estado -->
                  <v-chip color="success" v-if="item.state == 1">Inscrito</v-chip>
                  <v-chip color="danger" v-if="item.state == 3">No inscrito</v-chip>
                </template>

                <template v-slot:item.student="{ item }">
                  <span class="mx-1" v-if="item.user.usuario.pri_nombre">{{item.user.usuario.pri_nombre}}</span>
                  <span class="mx-1" v-if="item.user.usuario.seg_nombre">{{item.user.usuario.seg_nombre}}</span>
                  <span class="mx-1" v-if="item.user.usuario.pri_apellido">{{item.user.usuario.pri_apellido}}</span>
                  <span class="mx-1" v-if="item.user.usuario.seg_apellido">{{item.user.usuario.seg_apellido}}</span>
                </template>

                <template v-slot:item.actions="{ item }">
                  <!-- Detalle -->
                  <v-tooltip bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn icon color="success" :href="'#/inscriptions/detail/'+item.inscription_id" v-bind="attrs" v-on="on">
                        <v-icon>mdi-eye-outline</v-icon>
                      </v-btn>
                    </template>
                    <span>Ver detalle</span>
                  </v-tooltip>
                  
                  <!-- Eliminar -->
                  <v-tooltip bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn icon color="danger" v-bind="attrs" v-on="on" @click="deleteInscription(list_inscriptions.map(function(x) {return x.inscription_id; }).indexOf(item.inscription_id), item.inscription_id)">
                        <v-icon>mdi-trash-can-outline</v-icon>
                      </v-btn>
                    </template>
                    <span>Eliminar</span>
                  </v-tooltip>
                </template>
              </v-data-table>
            </div>
          </v-card-text>
        </v-card>
      </div>
    </section>
  </div>
</template>

<script>
import Helpers from '../../../core/helpers/Helpers';
import ListController from '../controllers/ListController';

export default {
  data() {
    return {
      Helpers,
      search_list_inscriptions: '',
      headers: [],
      list_inscriptions: [],
      dialog_detail: {},
      is_search: false,

      date_init: null,
      date_finish: null,
      menu_init: false,
      menu_finish: false,

      list_controller: null
    };
  },

  mounted() {
    this.list_controller = new ListController(this);
  },

  methods: {
    deleteInscription(position, inscription_id) {
      this.list_controller.deleteInscription(position, inscription_id);
    },

    filterInscriptions() {
      this.list_controller.filterInscriptions();
    },

    listInscriptions() {
      this.list_controller.listInscriptions();
    }
  }
}
</script>