import Rest from '../../../core/libs/Rest';
import { MAIN_MOCKUP } from '../mockups/Main';

class MainRepository {
  rest;

  constructor() {
    this.rest = new Rest();
  }

  createSetting(setting, description) {
    this.rest.setParams({setting:setting, description:description});
    this.rest.setEndPoint('api/settings/store');
    return this.rest.peticionMockupV2('GET', null, MAIN_MOCKUP.CREATE_SETTING);
    //return this.rest.get();
  }
}

export default MainRepository;