import UI from  '../../../core/helpers/UI';
import File from "../../../core/helpers/File";
import ListService from '../services/ListService';

class ListController {
  context;
  list_service;

  constructor(context) {
    this.context = context;
    this.list_service = new ListService();

    this.context.header_quizes = [
      { text: 'Título', value: 'title' },
      { text: 'Estado', value: 'status' },
      { text: 'Lecciones', value: 'lessons' },
      { text: 'Veces que ha sido respondida', value: 'answers' },
      { text: '', value: 'actions', sortable: false },
    ];
    
    this.listQuizes();
  }

  listQuizes() {
    UI.loading('show');
    this.list_service.listQuizes()
    .then((r) => {
      if(r.success == true) {
        this.context.list_quizes = [];
        this.context.list_quizes = r.data;
        
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }

  deleteQuiz(position, quiz_id) {
    UI.loading('show');
    this.list_service.deleteQuiz(quiz_id)
    .then((r) => {
      if(r.success == true) {
        this.context.list_quizes.splice(position, 1);
        UI.mensajeOk('', r.msg);
        UI.loading('hide');
      }
    }).catch((e) => {
      UI.loading('hide');
      UI.mensajeBad('', e.msg);
    });
  }
}

export default ListController;