import Rest from '../../../core/libs/Rest';
import { MANAGE_MOCKUP } from '../mockups/Manage';

class ManageRepository {
  rest;

  constructor() {
    this.rest = new Rest();
  }

  createAudio(title, audio) {
    this.rest.setParams({name:title, audio:audio});
    this.rest.setEndPoint('api/mod-audio/store-audio');
    return this.rest.post();
  }

  editAudio(mod_audio_id, title, audio) {
    this.rest.setParams({name:title, audio:audio});
    this.rest.setEndPoint('api/mod-audio/update-audio');
    return this.rest.put(mod_audio_id);
  }

  infoAudio(audio_id) {
    this.rest.setParams('/'+audio_id);
    this.rest.setEndPoint('api/mod-audio/show-audio');
    return this.rest.get();
  }
}

export default ManageRepository;