<template>
  <div>
    <section class="main">
      <div class="container-fluid main-box-content contentModulo py-2">
        <div class="breadcrumb px-3">
          <ul>
            <li>Inicio</li>
            <li>Configuraciones</li>
            <li>Crear configuración</li>
          </ul>
        </div>

        <div class="title">
          <h1 class="my-0">Crear configuración</h1>
        </div>

        <v-card class="mt-3 mt-md-4">
          <v-card-text class="px-lg-4">
            <v-form class="w-100" ref="form_settings" method="post" v-model="form_valid" lazy-validation @submit.prevent="manageSetting()">
              <v-text-field label="Configuración" :rules="[rules.requerido]" v-model="setting" filled></v-text-field>

              <v-textarea label="Descripción" :rules="[rules.requerido]" v-model="description" filled></v-textarea>

              <v-btn class="d-table mx-auto mt-2" rounded width="120" color="success" type="submit">Guardar</v-btn>
            </v-form>
          </v-card-text>
        </v-card>
      </div>
    </section>
  </div>
</template>

<script>
import Helpers from '../../../core/helpers/Helpers';
import MainController from '../controllers/MainController';

export default {
  data() {
    return {
      Helpers,
      setting: '',
      description: '',
      form_valid: true,

      rules: {
        requerido: value => !!value || 'Campo obligatorio',
        url: value => this.validatedUrl(value) || 'URL no válida',
        email: value => !value || /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(value) || 'Email incorrecto',
      },

      main_controller: null
    };
  },

  mounted() {
    this.main_controller = new MainController(this);
  },

  methods: {
    manageSetting() {
      this.main_controller.createSetting();
    }
  }
}
</script>